<template>
  <div>
    <el-form label-position="left"  ref="force_offline" :model="force_offline" label-width="100px" @submit.prevent="onSubmit" style="margin:30px;width:80%;min-width:600px;" :label-position="position" :rules="rules" border>

        <el-form-item label="时间" prop="date">
            <el-date-picker v-model="force_offline.date" type="date" format="yyyy-MM-dd" placeholder="选择时间" style="width: 300px;" @change="formatDate">
            </el-date-picker>
        </el-form-item>

        <el-form-item label="图片" prop="images">
             <PictureWall></PictureWall>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" :disabled="submitVisible" @click="onSubmit('force_offline')">发布</el-button>
        </el-form-item>
    </el-form>
    <div>
    </div>
  </div>
</template>

<script>
    import { mapState, mapActions } from 'vuex'
    import NProgress from 'nprogress'
    import PictureWall from '../../components/album/PictureWall'
    var Resource = require('../../util/Resource');
    export default {
        components : {PictureWall},
        computed: mapState ({
            sels: state => state.sels
        }),
        data() {
            return {
                id: "",
                imageDialogVisible: false,
                dialogImageUrl: "",
                data: {
                },
                force_offline: {
                    date: '',
                },
                albums: {},
                loading: false,
                tags: [],
                submitVisible: false,
                position: "right",
                rules: {
                    date: [
                      {
                          required: true,
                          message: '请输入时间',
                          trigger: 'change'
                      }
                    ],
                    banner_id: [
                      {
                          required: true,
                          message: '请输入关联 ID',
                          trigger: 'change'
                      }
                    ],
                },
            }
        },
        methods: {
            ...mapActions({
                setSels: 'setSels',
                setType: 'setType',
            }),
            formatDate(date) {
              this.force_offline.date = date;
            },
            getSelIds() {
                var result = [];
                this.sels.forEach(function(v){
                    result.push(v.id);
                })
                return result.join(",")
            },
            onSubmit() {
              this.$refs['force_offline'].validate((valid) => {
                if (this.sels.length == 0) {
                    this.$notify({
                        title: '失败',
                        message: '请选择图片图片',
                        type: 'error'
                    });
                    return false;
                }
                if (valid){
                  var _this = this;

                  var data = {
                      date: this.force_offline.date,
                      id: this.id,
                      album_ids: this.getSelIds()
                  }

                  var method = "put";
                  if (this.id){
                    method = "post";
                  }
                  Resource.options({
                      method: method,
                      resource: "force_offline.force_offline",
                      data: data,
                      success: function(data) {
                          _this.$notify({
                              title: '发布成功',
                              message: '发布成功',
                              type: 'success'
                          });
                          window.location.href = '/#/apps/force_offline';
                      },
                      error: function(data) {
                          var message = '保存失败，请联系相关研发～';
                          var id = 0;
                          if (data.data && data.data.errmsg) {
                              message = data.data.errmsg;
                              id = data.data.id;
                          }
                          _this.$notify({
                              title: '失败',
                              message: message,
                              type: 'error'
                          });
                          if (id > 0) {
                               window.location.href = "/#/apps/edit_force_offline?id="+ id;
                          }
                      }
                  });
                } else {
                    this.$notify({
                      title: '保存失败',
                      message: '请按照红色提示修改区域内容',
                      type: 'error'
                    });
                    return false;
                }
              });
            },

          formatAlbum(albums) {
              var _this = this;
              if(albums) {
                  this.setSels(albums);
              }
          }
        },

        mounted() {
            // 设置查询类型为全部，图片墙展示图片及专辑
            this.setType(-1);
            var force_offline_id = this.$route.query.id||"";
            this.id = force_offline_id
            this.listLoading = true;
            NProgress.start();
            var _this = this;
            Resource.get({
                resource: "force_offline.force_offline",
                data: {
                  id: force_offline_id
                },
                success: function(data) {
                    if (data['force_offline'] && data['force_offline'].id > 0) {
                        _this.force_offline.date = data['force_offline'].date;
                        _this.force_offline.id = data['force_offline'].id;
                    }
                    if (force_offline_id) {
                        _this.formatAlbum(data['force_offline']['albums']);
                    }
                    _this.listLoading = false;
                    NProgress.done();
                },
                error: function() {
                    _this.listLoading = false;
                    NProgress.done();
                    _this.$notify({
                        title: '失败',
                        message: '请求失败',
                        type: 'error'
                    });
                }
            });
        }
    }

</script>



