<template>
  <div>
    <el-form label-position="left"  ref="picture" :model="picture" label-width="100px" @submit.prevent="onSubmit" style="margin:30px;width:80%;min-width:600px;" :rules="rules" border>

        <el-form-item label="标题" prop="title">
            <el-input v-model="picture.title"></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="status">
            <el-select v-model="picture.status" placeholder="请选择状态">
                <el-option
                  v-for="item in data.status"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="权重" prop="weight" style="display: none">
            <el-input v-model.number="picture.weight"></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="desc">
            <el-input type="textarea" v-model="picture.desc"></el-input>
        </el-form-item>
        <el-form-item label="分类" prop="categories">
            <el-checkbox-group v-model="picture.categories">
                <el-checkbox v-for="filter in data.categories" :label="filter.id" :key="filter.id">{{filter.name}}</el-checkbox>
            </el-checkbox-group>
        </el-form-item>
        <el-form-item label="CP" prop="cp">
            <el-select v-model="picture.cp" placeholder="请选择CP">
               <el-option
                  v-for="item in data.cps"
                  :label="item.name"
                  :value="item.id"
                  :key="item.id">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="品牌" prop="brand">
            <el-select v-model="picture.brand" placeholder="请选择品牌">
                <el-option
                  v-for="item in data.brands"
                  :label="item.name"
                  :value="item.id"
                  :key="item.id">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="Vendor" prop="vendor">
            <el-select v-model="picture.vendor" placeholder="请选择Vendor">
                <el-option
                  v-for="item in data.vendors"
                  :label="item.name"
                  :value="item.id"
                  :key="item.id">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="banner" prop="banner" >
            <el-select v-model="picture.banner" placeholder="请选择Banner">
                <el-option
                  v-for="item in data.banners"
                  :label="item.name"
                  :value="item.id"
                  :key="item.id">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="标签" prop="tags">
          <el-select
              v-model="picture.tags"
              multiple
              filterable
              remote
              placeholder="请输入关键词"
              :remote-method="getTags"
              :loading="loading">
              <el-option
                v-for="item in tags"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="同步图片时间" prop="isUpdatePicDate">
           <el-checkbox v-model="isUpdatePicDate">是</el-checkbox>
        </el-form-item>

        <el-form-item label="时间" prop="date">
        <div class="block">
            <el-date-picker v-model="picture.date" format="yyyy-MM-dd HH:mm:ss" type="datetimerange" placeholder="选择时间范围" @change="formatDate">
            </el-date-picker>
        </div>
        </el-form-item>
        <el-form-item label="图片" prop="images">
             <PictureWall></PictureWall>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" :disabled="submitVisible" @click="onSubmit('picture')">发布</el-button>
        </el-form-item>
    </el-form>
    <div>
    </div>
  </div>
</template>

<script>
    import { mapState, mapActions } from 'vuex'
    import NProgress from 'nprogress'
    import PictureWall from '../../components/album/PictureWall'
    var Resource = require('../../util/Resource');
    var moment = require('moment');
    export default {
        components : {
          PictureWall,
        },
        computed: mapState ({
            sels: state => state.sels,
            type: state => state.type
        }),
        data() {
            var start_time = moment().format("YYYY-MM-DD HH:mm:ss");
            var end_time = moment().add(180, 'days').format("YYYY-MM-DD HH:mm:ss");
            return {
                isUpdatePicDate: false,
                id:"",
                imageDialogVisible: false,
                dialogImageUrl: "",
                data: {
                },
                picture: {
                    title: '',
                    status: '',
                    weight: 0,
                    desc: '',
                    desc: '',
                    categories: '',
                    cp: '',
                    brand: '',
                    tags: [],
                    date: [start_time, end_time],
                    pictures: [],
                    categories: [],
                    vendor: 1,
                    banner: ''
                },
                loading: false,
                tags: [],
                submitVisible: false,
                rules: {
                    title: [
                      {
                          required: true,
                          message: '请输入标题',
                          trigger: 'blur'
                      }
                    ],
                    status: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请选择状态',
                          trigger: 'change'
                      }
                    ],
                    weight: [
                      {
                          required: true,
                          type: 'number',
                          message: '请输入权重',
                          trigger: 'blur'
                      },
                      {
                          type: 'number',
                          message: '请输入数字',
                          trigger: 'blur'
                      }
                    ],
                    desc: [
                      {
                          required: true,
                          message: '请输入描述',
                          trigger: 'blur'
                      }
                    ],
                    categories: [
                      {
                          type: 'array',
                          required: true,
                          message: '请至少选择一个分类',
                          trigger: 'change'
                      }
                    ],
                    cp: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请选择cp',
                          trigger: 'change'
                      }
                    ],
                    brand: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请选择品牌',
                          trigger: 'change'
                      }
                    ],
                    vendor: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请选择vendor',
                          trigger: 'blur'
                      }
                    ],
                },
            }
        },
        methods: {
            ...mapActions({
                setSels: 'setSels',
                setType: 'setType',
            }),
            getTags(value) {
                //search tags
                var _this = this;
                this.loading = true;
                Resource.get({
                    resource: "tag.search_tag",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.loading = false;
                        _this.tags = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '标签请求失败',
                            type: 'error'
                        });
                    }
                })

            },
            onSubmit(formName) {
              this.$refs[formName].validate((valid) => {
                this.submitVisible = true;
                if (this.sels.length == 0) {
                    this.$notify({
                        title: '失败',
                        message: '请选择图片图片',
                        type: 'error'
                    });
                    this.submitVisible = false;
                    return false;
                }
                if (valid) {
                  var _this = this;
                  var data = {
                        picture: this.picture,
                        id: this.id,
                        is_update_pic_date: this.isUpdatePicDate
                      };
                  data.picture.pictures = this.sels;
                  if (this.id) {
                      Resource.post({
                          resource: "album.album",
                          data: data,
                          success: function(data) {
                            _this.$notify({
                                title: '发布成功',
                                message: '发布成功',
                                type: 'success'
                            });
                            window.location.href = '/#/apps/album';
                            _this.submitVisible = false;

                          },
                          error: function() {
                            _this.$notify({
                                title: '失败',
                                message: '保存失败，请联系相关研发～',
                                type: 'error'
                            });
                            _this.submitVisible = false;
                          }
                      });
                  } else {
                      Resource.put({
                          resource: "album.album",
                          data: data,
                          success: function(data) {
                              _this.$notify({
                                  title: '发布成功',
                                  message: '发布成功',
                                  type: 'success'
                              });
                              window.location.href = '/#/apps/album';
                          },
                          error: function() {
                              _this.$notify({
                                  title: '失败',
                                  message: '保存失败，请联系相关研发～',
                                  type: 'error'
                              });
                              _this.submitVisible = false;
                          }
                      });
                  }
                } else {
                  this.$notify({
                        title: '保存失败',
                        message: '请按照红色提示修改区域内容',
                        type: 'error'
                    });
                  this.submitVisible = false;
                  return false;
                }
              });
            },
            formatAlbum(picture) {
              this.tags = picture.tags;
              var _this = this;
              var pictures = []
              if (picture) {
                  picture.pictures.forEach(function(i){
                      pictures.push(_this.formatePicture(i));
                  });
              }
              this.picture = {
                    title: picture.title,
                    link_type: picture.link_type,
                    status: picture.status,
                    weight: picture.weight,
                    desc: picture.desc,
                    title_color: picture.title_color,
                    desc_color: picture.desc_color,
                    categories: picture.category_ids,
                    cp: picture.cp_id,
                    brand: picture.brand_id,
                    tags: picture.tag_ids,
                    date: [picture.start_time, picture.end_time],
                    pictures: pictures,
                    acc_url: [],
                    acc_type: picture.acc_type,
                    vendor: picture.vendor_id,
                    id: picture.id,
                    txt_size: picture.txt_size,
                    banner: picture.banner
            };
            if(pictures) {
                this.setSels(pictures);
                this.setType(0);
            }
          },
          formatePicture(picture) {
              var pictures = []
              if (picture) {
                  picture.pictures.forEach(function(i){
                      var arr = i.split("/");
                      pictures.push({
                          name: arr[arr.length-1],
                          url: i
                      });
                  });
              }
              return {
                  title: picture.title,
                  type: picture.type,
                  link_type: picture.link_type,
                  status: picture.status,
                  weight: picture.weight,
                  desc: picture.desc,
                  title_color: picture.title_color,
                  desc_color: picture.desc_color,
                  categories: picture.category_ids,
                  cp: picture.cp_id,
                  brand: picture.brand_id,
                  tags: picture.tag_ids,
                  date: [picture.start_time, picture.end_time],
                  pictures: pictures,
                  acc_url: [],
                  acc_type: picture.acc_type,
                  vendor: picture.vendor_id,
                  id: picture.id,
                  txt_size: picture.txt_size,
                  branner: picture.branne,
                  icon: picture.images[0]
              }
          },
          formatDate(date) {
              this.picture.date = date.split(' - ');
          }
        },
        mounted() {
            var picture_id = this.$route.query.id||"";
            this.id = picture_id
            this.listLoading = true;
            NProgress.start();
            var _this = this;
            Resource.get({
                resource: "album.picture",
                data: {
                  id: picture_id
                },
                success: function(data) {
                    _this.data = data;
                    if (_this.data.banners.length > 0) {
                        _this.data.banners.splice(0,0, {id:"",name:"无"})
                    }
                    if (picture_id) {
                        _this.formatAlbum(data.picture);
                    }
                    _this.listLoading = false;
                    NProgress.done();
                },
                error: function() {
                    _this.listLoading = false;
                    NProgress.done();
                    _this.$notify({
                        title: '失败',
                        message: '请求失败',
                        type: 'error'
                    });
                }
            });
        }
    }

</script>



