<template>
    <section style="left: 185px;">
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item label="全局ID">
                    <el-input v-model="filters.global_id" style="width: 200px;"></el-input>
                </el-form-item>
                <el-form-item label="Legacy ID">
                    <el-input v-model="filters.legacy_id" style="width: 180px;"></el-input>
                </el-form-item>
                <el-form-item label="状态" style="padding-left: 28px;">
                    <el-select v-model="filters.status" placeholder="请选择状态" style="width: 175px;">
                        <el-option
                          v-for="item in filter_result.status"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-tooltip content="查询" placement="top">
                    <el-button class="el-icon-search"  type="primary" v-on:click="getCategoryList"></el-button>
                    </el-tooltip>
                    <el-tooltip content="新增" placement="top">
                    <el-button type="primary" @click="handleAdd" class="el-icon-plus"></el-button>
                    </el-tooltip>
                </el-form-item>
                <!-- <el-form-item >
                    <el-button type="primary" @click="handlePublish">发布</el-button>
                </el-form-item> -->
            </el-form>
        </el-col>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>

        <!--列表-->
        <el-table :data="categorys" highlight-current-row v-loading="listLoading" element-loading-text="拼命加载中"  border style="font-size:12px; ">
            <el-table-column prop="name"  label="名称" width="118" align="center"> </el-table-column>

            <el-table-column prop="global_id"  label="全局ID" width="200" align="center"></el-table-column>

            <el-table-column prop="legacy_id" label="Legacy ID" width="200" align="center"></el-table-column>

            <el-table-column prop="index" label="顺序"  width="180" align="center">
                <template scope="scope">
                    <el-input-number  v-model="scope.row.index" v-bind:value="scope.row.index" v-on:change="handleChange('index',$event, scope.row.id)"  :min="0"></el-input-number>
              </template>

            </el-table-column>

            <el-table-column prop="is_default" label="是否默认订阅"  width="100" align="center">
                <template scope="scope">
                    <el-radio-group  v-model="scope.row.is_default" v-on:change="handleChange('is_default', scope.row.is_default, scope.row.id)" >
                        <el-radio
                            v-for="item in filter_result.subscribe_set"
                            :label="item.key"
                            :value="item.value"
                            :key="item.key"
                            >{{item.value}}
                        </el-radio>
                    </el-radio-group>
                </template>
            </el-table-column>

            <el-table-column prop="status" label="状态" width="150" align="center">
                <template scope="scope">
                    <el-radio-group  v-model="scope.row.status"  v-on:change="handleChange('status', scope.row.status, scope.row.id)" >
                        <el-radio
                            v-for="item in filter_result.status"
                            :label="item.key"
                            :value="item.value"
                            :key="item.key"
                            >{{item.value}}
                        </el-radio>
                    </el-radio-group>
                </template>


            </el-table-column>

            <el-table-column label="封面" width="138" align="left">
              <template scope="scope">
                <a :href="scope.row.cover" target="_blank">
                    <img :src="scope.row.cover" style="height: 70px">
                </a>
              </template>
            </el-table-column>

             <el-table-column label="方形图" width="200" align="left">
              <template scope="scope">
                <a :href="scope.row.s_icon" target="_blank">
                    <img :src="scope.row.s_icon" style="height: 70px">
                </a>
              </template>
            </el-table-column>

             <el-table-column label="圆形图" width="200" align="left">
              <template scope="scope">
                <a :href="scope.row.p_icon" target="_blank">
                    <img :src="scope.row.p_icon" style="height: 70px">
                </a>
              </template>
            </el-table-column>

            <el-table-column label="操作" width="80" align="center">
                <template scope="scope">
                <el-tooltip content="编辑" placement="top">
                    <el-button class="el-icon-edit" type="primary" size="small" v-on:click="handleUpdate(scope.row.id)"></el-button>
                </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>
        <!-- 弹框 新增、编辑分类-->
        <el-dialog :visible.sync="dialogFormVisible" :title="title" :before-close="handleCancle" top='5%'>
            <el-form label-position="left"  ref="category" :model="category" label-width="100px" @submit.prevent="onSubmit" style="margin:30px;width:80%;min-width:600px;" :rules="rules" border>

                <el-form-item label="分类名称" prop="name" style="width: 300px;">
                    <el-input v-model="category.name"></el-input>
                </el-form-item>

                <el-form-item label="全局ID" prop="global_id" style="width: 500px;"v-show="globalIdShow" >
                    <el-input v-model="category.global_id" readonly></el-input>
                </el-form-item>

                <el-form-item label="Legacy ID" prop="legacy_id" style="width: 500px;"  v-show="legacyIdShow">
                    <el-input v-model="category.legacy_id" readonly></el-input>
                </el-form-item>

                <el-form-item label="显示顺序" prop="index" style="width: 600px;">
                    <el-input-number  v-model="category.index" :min="0"></el-input-number><font color="red">（***数字越小，在客户端显示的位置越靠前***）</font>
                </el-form-item>

                <el-form-item label="状态">
                    <el-select v-model="category.status" placeholder="请选择" style="width: 175px;">
                        <el-option
                          v-for="item in filter_result.status"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="封面图" width="100">
                    <el-upload
                      class="upload-demo"
                      :action="upload_url"
                      :on-preview="handlePreview"
                      :on-remove="handleCoverRemove"
                      :on-success="handleCoverSuccess"
                      :file-list="category.cover"
                      :data="cover_data"
                      list-type="picture">
                      <el-button size="small" type="primary">点击上传</el-button>
                    </el-upload>
                </el-form-item>
                <el-form-item label="圆形图" width="100">
                    <el-upload
                      class="upload-demo"
                      :action="upload_url"
                      :on-preview="handlePreview"
                      :on-remove="handlePiconRemove"
                      :on-success="handlePiconSuccess"
                      :file-list="category.p_icon"
                      :data="p_icon_data"
                      list-type="picture">
                      <el-button size="small" type="primary">点击上传</el-button>
                    </el-upload>
                </el-form-item>
                <el-form-item label="方形图" width="100">
                    <el-upload
                      class="upload-demo"
                      :action="upload_url"
                      :on-preview="handlePreview"
                      :on-remove="handleSiconRemove"
                      :on-success="handleSiconSuccess"
                      :file-list="category.s_icon"
                      :data="s_icon_data"
                      list-type="picture">
                      <el-button size="small" type="primary">点击上传</el-button>
                    </el-upload>
                </el-form-item>

                <!-- 图片预览弹窗 -->
                <el-dialog v-model="imageDialogVisible" size="small" :modal="false" :modal-append-to-body="false">
                    <img width="100%" :src="dialogImageUrl" alt="">
                </el-dialog>

                <el-form-item>
                    <el-button type="primary" v-on:click="onSubmit('category')">保存</el-button>
                    <el-button type="primary" v-on:click="handleCancle">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </section>
</template>

<script>
    import util from '../../common/js/util'
    import NProgress from 'nprogress'
    import GlobalConfig from '../../GlobalConfig'
    var Resource = require('../../util/Resource');

    export default {
        data() {
            return {
                filters: {
                    global_id: '',
                    status: '',
                    legacy_id:''
                },
                categorys: [],
                upload_url:GlobalConfig.uploadImageUri,
                total: 0,
                title:"",
                pageSize: 30,
                page: 1,
                listLoading: false,
                imageDialogVisible: false,
                globalIdShow: true,
                legacyIdShow: true,
                cover_data:{
                    type: "new"
                },
                p_icon_data:{
                    type: "new"
                },
                s_icon_data:{
                    type: "new"
                },
                filter_result:{},
                sort: '-id',
                //dialog
                dialogFormVisible: false,
                category:{
                    id:'',
                    name:'',
                    global_id:'',
                    legacy_id:'',
                    desc:'',
                    index:'',
                    is_default:'',
                    status:'',
                    cover: [],
                    p_icon: [],
                    s_icon: []
                },
                dialogImageUrl: "",
                rules: {
                    name: [
                      {
                          required: true,
                          message: '请输入名称',
                          trigger: 'blur'
                      },
                    ],
                    index: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请输入顺序，只能为整数！',
                          trigger: 'blur'
                      }
                    ],
                }
            }
        },
        methods: {
            // 页面加载前获取集合值
            getSearch() {
                var _this = this;
                Resource.get({
                    resource: "category.category_search",
                    success: function(data) {
                        _this.filter_result = data.filter_result;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                });
            },
            handleCurrentChange(val) {
                this.page = val;
                this.categorys = [];
                this.getCategoryList();
            },
            handlePublish(){
                var _this = this;
                Resource.post({
                    resource: "category.category_publish",
                    success: function(data) {
                        _this.$notify({
                            title: '发布成功',
                            message: '发布成功',
                            type: 'success'
                        });
                    },
                    error: function() {
                        _this.$notify({
                            title: '发布失败',
                            message: '发布失败',
                            type: 'error'
                        });
                    }
                });
            },
            getFilters() {
                var page = this.page || 1;
                var status = this.filters.status;
                var global_id = this.filters.global_id || '';
                var legacy_id = this.filters.legacy_id || '';
                return {
                    page: page,
                    status: status,
                    global_id: global_id,
                    legacy_id: legacy_id
                }
            },
            handleAdd(){
                this.globalIdShow = false;
                this.legacyIdShow = false;
                this.dialogFormVisible = true;
                this.title = "添加分类";
            },
            handleCancle(){
                this.category_clean(this.category)
            },
            handlePreview(file) {
                this.dialogImageUrl = file.url;
                this.imageDialogVisible = true;
            },
            handleCoverRemove(file, fileList) {
                this.category.cover = [];
            },
            handleSiconRemove(file, fileList) {
                this.category.s_icon = [];
            },
            handlePiconRemove(file, fileList) {
                this.category.p_icon = [];
            },
            handleCoverSuccess(response, file, fileList) {
                if (response.code == 200 && response.data) {
                   this.category.cover = [response.data];
                } else {
                    this.$notify({
                        title: '失败',
                        message: '图片上传失败',
                        type: 'error'
                    });
                    this.category.cover = [];
                }
            },
            handleSiconSuccess(response, file, fileList) {
                if (response.code == 200 && response.data) {
                   this.category.s_icon = [response.data];
                } else {
                    this.$notify({
                        title: '失败',
                        message: '图片上传失败',
                        type: 'error'
                    });
                    this.category.s_icon = [];
                }
            },
            handlePiconSuccess(response, file, fileList) {
                if (response.code == 200 && response.data) {
                   this.category.p_icon = [response.data];
                } else {
                    this.$notify({
                        title: '失败',
                        message: '图片上传失败',
                        type: 'error'
                    });
                    this.category.p_icon = [];
                }
            },
            formatCategory(category) {
                var cover_array = [];
                var p_icon_array = [];
                var s_icon_array = [];
                if (category) {
                    // 封面图
                    var cover_link = category.cover
                    var arr = cover_link.split("/");
                    cover_array.push({
                        name: arr[arr.length-1],
                        url: cover_link
                    });
                    // 方形图
                    var s_icon_link = category.s_icon
                    var arr = s_icon_link.split("/");
                    s_icon_array.push({
                        name: arr[arr.length-1],
                        url: s_icon_link
                    });
                    // 圆形图
                    var p_icon_link = category.p_icon
                    var arr = p_icon_link.split("/");
                    p_icon_array.push({
                        name: arr[arr.length-1],
                        url: p_icon_link
                    });
                }
                this.category = {
                    name: category.name,
                    is_default: category.is_default,
                    global_id: category.global_id,
                    legacy_id: category.legacy_id,
                    desc: category.desc,
                    index: category.index,
                    status: category.status,
                    cover: cover_array,
                    p_icon: p_icon_array,
                    s_icon: s_icon_array,
                    id: category.id
                };
            },
            handleUpdate(id){
                var data={
                    id: id
                };
                var _this = this;
                Resource.get({
                    resource: "category.category",
                    data:data,
                    success: function(data) {
                        _this.dialogFormVisible = true;
                        _this.globalIdShow = true;
                        _this.legacyIdShow = true;
                        _this.title = "修改分类";
                        _this.formatCategory(data.category);
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                });

            },
            handleChange(type,value,id) {
                this.listLoading = true;
                var _this = this;
                var data = {}
                var resource = ''
                if(type=='index'){
                    data= {
                        type: type,
                        index: value,
                        id: id
                    };
                    resource = "category.category_index"
                }else if(type=='is_default'){
                    data= {
                        type: type,
                        is_default: value,
                        id: id
                    };
                    resource = "category.category_is_subscribe"
                }else{
                    data= {
                        type: type,
                        status: value,
                        id: id
                    };
                    resource = "category.category_status"
                };
                this.listLoading = true;
                NProgress.start();
                Resource.post({
                    resource: resource,
                    data: data,
                    success: function(data) {
                      _this.listLoading = false;
                      NProgress.done();
                      _this.categorys = [];
                      _this.getCategoryList();
                      _this.category_clean(_this.category);
                      _this.$notify({
                        title: '成功',
                        message: '保存成功',
                        type: 'success'
                      });
                    },
                    error: function() {
                      _this.listLoading = false;
                      NProgress.done();
                      _this.$notify({
                          title: '失败',
                          message: '保存失败，请联系相关研发～',
                          type: 'error'
                      });
                    }
                });
            },
            category_clean(category){
                category.id = '';
                category.name = '';
                category.global_id = '';
                category.legacy_id = '';
                category.is_default = '';
                category.desc = '';
                category.index = '';
                category.cover = [];
                category.p_icon = [];
                category.s_icon = [];
                this.dialogFormVisible = false;
            },
            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                  if (valid) {
                    var _this = this;
                    var data = {
                        category: this.category,
                    };
                    this.listLoading = true;
                    NProgress.start();
                    var method = "put";
                    if (this.category.id && this.category.id != "" ) {
                        method = "post"
                    }
                    Resource.options({
                        method: method,
                        resource: "category.category",
                        data: data,
                        success: function(data) {
                          _this.listLoading = false;
                          NProgress.done();
                          _this.categorys = [];
                          _this.getCategoryList();
                          _this.category_clean(_this.category);
                          _this.$notify({
                            title: '成功',
                            message: '保存成功',
                            type: 'success'
                          });
                        },
                        error: function(data) {
                          _this.listLoading = false;
                          var message = '保存失败，请联系相关研发～';
                          if (data.data && data.data.errmsg) {
                              message = data.data.errmsg
                          }
                          NProgress.done();
                          _this.$notify({
                              title: '失败',
                              message: message,
                              type: 'error'
                          });
                        }
                    });
                  } else {
                    this.$notify({
                          title: '保存失败',
                          message: '请按照红色提示修改区域内容',
                          type: 'error'
                      });
                    return false;
                  }
                });
            },
            //获取用分类列表
            getCategoryList() {
                let para = this.getFilters();
                this.listLoading = true;
                NProgress.start();
                this.categorys = [];
                var _this = this;
                Resource.get({
                    resource: "category.category_list",
                    data: para,
                    success: function(data) {
                        _this.total = data.total_count;
                        _this.categorys = data.items;
                        _this.pageSize = data.page_size;
                        _this.listLoading = false;
                        NProgress.done();
                    },
                    error: function() {
                        _this.listLoading = false;
                        NProgress.done();
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
        },
        mounted() {
            this.getSearch();
            this.getCategoryList();
        }
    }

</script>

<style lang="scss" scoped>
.el-table td>div {
    height:100px!important;
}
</style>



