import axios from 'axios';
import GlobalConfig from '../GlobalConfig'
var querystring = require('querystring');
let base = GlobalConfig.base;
// var isProduction = process.env.NODE_ENV === 'production';
// let base = 'http://localhost:8008';
// if (isProduction) {
//     base = ""
// }


export const getPictureList = params => {
    return axios.get(`${base}/rest/album/api/picture_list?` + querystring.stringify(params)).then(res => res.data);
};

export const downloadPic = params => {
    window.open(`${base}/rest/album/api/picture_download/?`+querystring.stringify(params) , '_blank');
}

export const getCategories = params => {
    return axios.get(`${base}/rest/category/api/category_list`,  {params: params}).then(res => res.data);
};

export const setAlbum = params => {
    var id = params.id
    if (id) {
        JSON.stringify(params)
        return axios.post(`${base}/rest/cp_check/api/cp_album_status`, JSON.stringify(params)).then(res => res.data);
    } else {
        return axios.put(`${base}/rest/cp_check/api/cp_album`, JSON.stringify(params)).then(res => res.data);
    }
};

export const getAlbumList = params => {
    return axios.get(`${base}/rest/cp_check/api/cp_album_list`,  {params: params}).then(res => res.data);
};

export const getAlbum = params => {
    return axios.get(`${base}/rest/cp_check/api/cp_album`,  {params: params}).then(res => res.data);
};

export const updateStatus = params => {
    return axios.post(`${base}/rest/cp_check/api/cp_picture_status`,  JSON.stringify(params)).then(res => res.data);
};

export const updateAlbumStatus = params => {
    return axios.post(`${base}/rest/cp_check/api/cp_album_status`,  JSON.stringify(params)).then(res => res.data);
};

export const getFilterResult = params => {
    return axios.get(`${base}/rest/cp_check/api/cp_album_status`).then(res => res.data);
};

export const getCpPicture = params => {
    return axios.get(`${base}/rest/cp_check/api/picture`).then(res => res.data);
};

export const getTags = params => {
    return axios.get(`${base}/rest/tag/api/search_tag`).then(res => res.data);
};




