<template>
  <div>
    <el-form label-position="left" :model="albums" label-width="100px" @submit.prevent="onSubmit" style="margin:30px;width:80%;min-width:600px;"  border>
        <el-form-item label="专辑" prop="images">
             <PictureWall></PictureWall>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="onSubmit">发布</el-button>
        </el-form-item>
    </el-form>
  </div>
</template>

<script>
    import { mapState, mapActions } from 'vuex'
    import NProgress from 'nprogress'
    import PictureWall from '../../components/album/PictureWall'
    var Resource = require('../../util/Resource');
    export default {
        components : {PictureWall},
        computed: mapState ({
            sels: state => state.sels,
            type: state => state.type
        }),
        data() {
            return {
                albums: {},
            }
        },
        methods: {
            ...mapActions({
                setSels: 'setSels',
                setType: 'setType',
            }),
            onSubmit() {
                if (this.sels.length == 0) {
                    this.$notify({
                        title: '失败',
                        message: '请选择图片图片',
                        type: 'error'
                    });
                    return false;
                }
                  var _this = this;
                  var data = {
                        albums: this.sels,
                        id: this.id
                      };
                  console.log('=====data=====');
                  console.log(data);
                  Resource.post({
                      resource: "latest_topic.latest_topic",
                      data: data,
                      success: function(data) {
                          _this.$notify({
                              title: '发布成功',
                              message: '发布成功',
                              type: 'success'
                          });
                          window.location.href = '/#/apps/latest_topic';
                      },
                      error: function() {
                          _this.$notify({
                              title: '失败',
                              message: '保存失败，请联系相关研发～',
                              type: 'error'
                          });
                      }
                  });
            },
            formatAlbum(albums) {
                var _this = this;
                if(albums) {
                    console.log(albums);
                    this.setSels(albums);
                    // 把专辑的类型传过去，使图片墙展示专辑
                    this.setType(1);
                }
            }
        },
        mounted() {
            this.listLoading = true;
            NProgress.start();
            var _this = this;
            Resource.get({
                resource: "latest_topic.latest_topic",
                success: function(data) {
                    _this.data = data;
                    console.log(data.items)
                    _this.formatAlbum(data.items);
                    _this.listLoading = false;
                    NProgress.done();
                },
                error: function() {
                    _this.listLoading = false;
                    NProgress.done();
                    _this.$notify({
                        title: '失败',
                        message: '请求失败',
                        type: 'error'
                    });
                }
            });
        }
    }

</script>



