<template>
  <div>
    <el-form label-position="left"  ref="force_recommend" :model="force_recommend" label-width="100px" @submit.prevent="onSubmit" style="margin:30px;width:80%;min-width:600px;" :rules="rules" border :label-position="position">

        <el-form-item label="推荐时间" prop="date">
            <el-date-picker v-model="force_recommend.date" type="date" format="yyyy-MM-dd" placeholder="选择时间" style="width: 300px;" @change="formatDate">
            </el-date-picker>
        </el-form-item>

        <el-form-item label="图片" prop="images">
             <PictureWall></PictureWall>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" :disabled="submitVisible" @click="onSubmit('force_recommend')">发布</el-button>
        </el-form-item>
    </el-form>
    <div>
    </div>
  </div>
</template>

<script>
    import { mapState, mapActions } from 'vuex'
    import NProgress from 'nprogress'
    import PictureWall from '../../components/album/PictureWall'
    var Resource = require('../../util/Resource');
    export default {
        components : {PictureWall},
        computed: mapState ({
            sels: state => state.sels
        }),
        data() {
            return {
                id: "",
                imageDialogVisible: false,
                dialogImageUrl: "",
                data: {
                },
                force_recommend: {
                    date: '',
                },
                position: "right",
                albums: {},
                loading: false,
                tags: [],
                submitVisible: false,
                rules: {
                    date: [
                      {
                          required: true,
                          message: '请输入时间',
                          trigger: 'change'
                      }
                    ],
                    banner_id: [
                      {
                          required: true,
                          message: '请输入关联 ID',
                          trigger: 'change'
                      }
                    ],
                },
            }
        },
        methods: {
            ...mapActions({
                setSels: 'setSels',
                setType: 'setType',
            }),
            formatDate(date) {
              this.force_recommend.date = date;
            },
            getSelIds() {
                var result = [];
                this.sels.forEach(function(v){
                    result.push(v.id);
                })
                return result.join(",")
            },
            onSubmit() {
                if (this.sels.length == 0) {
                    this.$notify({
                        title: '失败',
                        message: '请选择图片图片',
                        type: 'error'
                    });
                    return false;
                }
                var _this = this;
                var data = {
                    date: this.force_recommend.date,
                    album_ids: this.getSelIds(),
                    id: this.id
                }

                var method = "put";
                if (this.id){
                    method = "post";
                }
                Resource.options({
                    method: method,
                    resource: "force_recommend.force_recommend",
                    data: data,
                    success: function(data) {
                        _this.$notify({
                            title: '发布成功',
                            message: '发布成功',
                            type: 'success'
                          });
                        window.location.href = '/#/apps/force_recommend';
                      },
                    error: function(data) {
                          var message = '保存失败，请联系相关研发～';
                          var id = 0;
                          if (data.data && data.data.errmsg) {
                            message = data.data.errmsg;
                            id = data.data.id;
                          }
                          _this.$notify({
                            title: '失败',
                            message: message,
                            type: 'error'
                          });
                          if (id > 0) {
                              window.location.href = "/#/apps/force_recommend?id="+ id;
                          }
                      }
                  });
            },
          formatAlbum(albums) {
              var _this = this;
              if(albums) {
                  this.setSels(albums);
              }
          }
        },
        mounted() {
            // 设置查询类型为全部，图片墙展示图片及专辑
            this.setType(-1);
            var force_recommend_id = this.$route.query.id||"";
            this.id = force_recommend_id
            this.listLoading = true;
            NProgress.start();
            var _this = this;
            Resource.get({
                resource: "force_recommend.force_recommend",
                data: {
                  id: force_recommend_id
                },
                success: function(data) {
                    if (data['force_recommend'] && data['force_recommend'].id > 0) {
                        _this.force_recommend = data['force_recommend'];
                    }
                    if (force_recommend_id) {
                        _this.formatAlbum(data['force_recommend']['albums']);
                    }
                    _this.listLoading = false;
                    NProgress.done();
                },
                error: function() {
                    _this.listLoading = false;
                    NProgress.done();
                    _this.$notify({
                        title: '失败',
                        message: '请求失败',
                        type: 'error'
                    });
                }
            });
        }
    }

</script>



