<template>
    <section style="left: 185px;">
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item label="名称">
                    <el-input v-model="filters.name" style="width: 200px;"></el-input>
                </el-form-item>
                <el-form-item label="全局ID">
                    <el-input v-model="filters.legacy_id" style="width: 180px;"></el-input>
                </el-form-item>
                <el-form-item label="状态" style="padding-left: 28px;">
                    <el-select v-model="filters.status" placeholder="请选择状态" style="width: 175px;">
                        <el-option
                          v-for="item in filter_result.status"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-tooltip content="查询" placement="top">
                    <el-button class="el-icon-search"  type="primary" v-on:click="getCategoryList"></el-button>
                    </el-tooltip>
                    <el-tooltip content="新增" placement="top">
                    <el-button type="primary" @click="handleAdd" class="el-icon-plus"></el-button>
                    </el-tooltip>
                </el-form-item>
            </el-form>
        </el-col>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>

        <!--列表-->
        <el-table :data="categorys" highlight-current-row v-loading="listLoading" element-loading-text="拼命加载中"  border style="font-size:12px; ">
            <el-table-column prop="name"  label="名称"  align="center"> </el-table-column>

            <el-table-column prop="legacy_id" label="全局ID"  align="center"></el-table-column>

            <el-table-column prop="index" label="顺序"   align="center">
                <template scope="scope">
                    <el-input-number  v-model="scope.row.index" v-bind:value="scope.row.index" v-on:change="handleChange('index',$event, scope.row.id)"  :min="0"></el-input-number>
              </template>

            </el-table-column>

            <el-table-column prop="status" label="状态"  align="center">
                <template scope="scope">
                    <el-radio-group  v-model="scope.row.status"  v-on:change="handleChange('status', scope.row.status, scope.row.id)" >
                        <el-radio
                            v-for="item in filter_result.status"
                            :label="item.key"
                            :value="item.value"
                            :key="item.key"
                            >{{item.value}}
                        </el-radio>
                    </el-radio-group>
                </template>
            </el-table-column>

            <el-table-column label="操作"  align="center">
                <template scope="scope">
                <el-tooltip content="编辑" placement="top">
                    <el-button class="el-icon-edit" type="primary" size="small" v-on:click="getCategory(scope.row.id)"></el-button>
                </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>
        <!-- 弹框 新增、编辑分类-->
        <el-dialog :visible.sync="dialogFormVisible" :title="title" :before-close="handleCancle" top='5%'>
            <el-form label-position="left"  ref="category" :model="category" label-width="100px" @submit.prevent="onSubmit" style="margin:30px;width:80%;min-width:600px;" :rules="rules" border>

                <el-form-item label="分类名称" prop="name" style="width: 300px;">
                    <el-input v-model="category.name"></el-input>
                </el-form-item>

                <el-form-item label="全局ID" prop="legacy_id" style="width: 500px;"  v-show="legacyIdShow">
                    <el-input v-model="category.legacy_id" readonly></el-input>
                </el-form-item>

                <el-form-item label="显示顺序" prop="index" style="width: 600px;">
                    <el-input-number  v-model="category.index" :min="0"></el-input-number><font color="red">（***数字越小，在客户端显示的位置越靠前***）</font>
                </el-form-item>

                <el-form-item label="状态">
                    <el-select v-model="category.status" placeholder="请选择" style="width: 175px;">
                        <el-option
                          v-for="item in filter_result.status"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="onSubmit('category')">保存</el-button>
                    <el-button type="primary" v-on:click="handleCancle">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </section>
</template>

<script>
    import NProgress from 'nprogress'
    import GlobalConfig from '../../../GlobalConfig'
    export default {
        data() {
            return {
                filters: {
                    status: '',
                    legacy_id: '',
                    name: '',
                },
                categorys: [],
                total: 0,
                title:"",
                pageSize: 30,
                page: 1,
                listLoading: false,
                legacyIdShow: true,
                filter_result:{},
                sort: '-id',
                dialogFormVisible: false,
                category:{
                    id:'',
                    name:'',
                    legacy_id:'',
                    index:'',
                    status:1,
                },
                rules: {
                    name: [
                      {
                          required: true,
                          message: '请输入名称',
                          trigger: 'blur'
                      },
                    ],
                    index: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请输入顺序，只能为整数！',
                          trigger: 'blur'
                      }
                    ],
                    status: [
                      {
                          required: true,
                          message: '请选择状态！',
                          trigger: 'blur'
                      }
                    ],
                }
            }
        },
        methods: {
            // 页面加载前获取集合值
            getSearch() {
                var _this = this;
                this.API_UTIL.API_NOTEBOOK.getCategorySearch().then(data=>{
                    if (data.code == 200) {
                        _this.filter_result = data.data.filter_result;
                    }else {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                 });
            },
            handleCurrentChange(val) {
                this.page = val;
                this.categorys = [];
                this.getCategoryList();
            },
            getFilters() {
                var page = this.page || 1;
                var status = this.filters.status;
                var name = this.filters.name || '';
                var legacy_id = this.filters.legacy_id || '';
                return {
                    page: page,
                    name:name,
                    status: status,
                    legacy_id: legacy_id
                }
            },
            handleAdd(){
                this.legacyIdShow = false;
                this.dialogFormVisible = true;
                this.title = "添加分类";
            },
            handleCancle(){
                this.category_clean(this.category)
            },

            formatCategory(category) {
                this.category = {
                    name: category.name,
                    legacy_id: category.legacy_id,
                    index: category.index,
                    status: category.status,
                    id: category.id
                };
            },
            getCategory(id){
                var data={
                    id: id
                };
                var _this = this;
                this.API_UTIL.API_NOTEBOOK.getCategory(data).then(data=>{
                    if (data.code == 200) {
                        _this.dialogFormVisible = true;
                        _this.legacyIdShow = true;
                        _this.title = "修改分类";
                        _this.formatCategory(data.data.category);
                    }else {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                 });
            },
            handleChange(type,value,id) {
                this.listLoading = true;
                var _this = this;
                var data = {}
                var resource = ''
                if(type=='index'){
                    resource = "notebook.category_index"
                    data= {
                        type: type,
                        index: value,
                        id: id,
                        resource: resource,
                    };
                }else{
                    resource = "notebook.category_status"
                    data= {
                        type: type,
                        status: value,
                        id: id,
                        resource: resource,
                    };
                };
                this.listLoading = true;
                NProgress.start();

                this.API_UTIL.API_NOTEBOOK.updateCategoryIndexStatus(data).then(data=>{
                    if (data.code == 200) {
                        _this.listLoading = false;
                        NProgress.done();
                        _this.categorys = [];
                        _this.getCategoryList();
                        _this.category_clean(_this.category);
                        _this.$notify({
                          title: '成功',
                          message: '保存成功',
                          type: 'success'
                        });
                    }else {
                        _this.listLoading = false;
                        NProgress.done();
                        _this.$notify({
                            title: '失败',
                            message: '保存失败，请联系相关研发～',
                            type: 'error'
                        });
                    }
                 });
            },
            category_clean(category){
                category.id = '';
                category.name = '';
                category.legacy_id = '';
                category.index = '';
                this.dialogFormVisible = false;
            },
            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                  if (valid) {
                    var _this = this;
                    var method = "put";
                    if (this.category.id && this.category.id != "" ) {
                        method = "post"
                    }
                    var data = {
                        category: this.category,
                        method: method,
                    };
                    this.listLoading = true;
                    NProgress.start();
                    this.API_UTIL.API_NOTEBOOK.setNotebookCategory(data).then(data=>{
                        if (data.code == 200) {
                            _this.listLoading = false;
                            NProgress.done();
                            _this.categorys = [];
                            _this.getCategoryList();
                            _this.category_clean(_this.category);
                            _this.$notify({
                              title: '成功',
                              message: '保存成功',
                              type: 'success'
                            });
                        }else {
                            _this.listLoading = false;
                            var message = '保存失败，请联系相关研发～';
                            if (data.data && data.data.errmsg) {
                              message = data.data.errmsg
                            }
                            NProgress.done();
                            _this.$notify({
                                title: '失败',
                                message: message,
                                type: 'error'
                            });
                        }
                     });
                  } else {
                    this.$notify({
                          title: '保存失败',
                          message: '请按照红色提示修改区域内容',
                          type: 'error'
                      });
                    return false;
                  }
                });
            },
            //获取用分类列表
            getCategoryList() {
                let para = this.getFilters();
                this.listLoading = true;
                NProgress.start();
                this.categorys = [];
                var _this = this;
                this.API_UTIL.API_NOTEBOOK.getNotebookCategorys(para).then(data=>{
                    console.log('---------',data.code)
                    if (data.code == 200) {
                        _this.total = data.data.total_count;
                        _this.categorys = data.data.items;
                        _this.pageSize = data.data.page_size;
                        _this.listLoading = false;
                        NProgress.done();
                    }else {
                        _this.listLoading = false;
                        NProgress.done();
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                });
            },
        },
        mounted() {
            this.getSearch();
            this.getCategoryList();
        }
    }

</script>

<style lang="scss" scoped>
.el-table td>div {
    height:100px!important;
}
</style>



