<template>
  <div>
    <el-form label-position="left"  ref="daily_recommend" :model="daily_recommend" label-width="100px" @submit.prevent="onSubmit" style="margin:30px;width:80%;min-width:600px;" :rules="rules">

        <el-form-item label="推荐日期" prop="date">
            <div class="block">
                <el-date-picker v-model="daily_recommend.date" type="date"  format="yyyy-MM-dd" placeholder="选择日期" style="width: 300px;" @change="formatDate">
                </el-date-picker>
            </div>
        </el-form-item>

        <!-- <el-form-item label="关联 id" prop="banner_id">
            <el-input v-model="daily_recommend.banner_id"></el-input>
        </el-form-item>

        <el-form-item label="button 描述" prop="button_desc">
            <el-input v-model="daily_recommend.button_desc"></el-input>
        </el-form-item>

        <el-form-item label="banner 标题" prop="banner_title">
            <el-input v-model="daily_recommend.banner_title"></el-input>
        </el-form-item>

        <el-form-item label="banner 描述" prop="banner_desc">
            <el-input v-model="daily_recommend.banner_desc"></el-input>
        </el-form-item> -->

        <el-form-item label="图片" prop="images">
             <AlbumWall></AlbumWall>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" :disabled="submitVisible" @click="onSubmit('daily_recommend')">发布</el-button>
        </el-form-item>
    </el-form>
    <div>
    </div>
  </div>
</template>

<script>
    import { mapState, mapActions } from 'vuex'
    import NProgress from 'nprogress'
    import AlbumWall from '../../../components/notebook/AlbumWall'
    import util from '../../../common/js/util'
    var Resource = require('../../../util/Resource');
    export default {
        components : {AlbumWall},
        computed: mapState ({
            sels: state => state.sels,
            type: state => state.type
        }),
        data() {
            var myDate = new Date()
            myDate.setDate (myDate.getDate() + 1);
            var date_str = util.formatDate.format(myDate,"yyyy-MM-dd");
            return {
                id: "",
                imageDialogVisible: false,
                dialogImageUrl: "",
                data: {
                },
                daily_recommend: {
                    date: date_str,
                    button_desc: '',
                    banner_id: '',
                    banner_title: '',
                    banner_desc: '',
                },
                listLoading: false,
                tags: [],
                submitVisible: false,
                rules: {
                    date: [
                        {
                          required: true,
                          message: '请输入时间'
                        }
                    ],
                    banner_id: [
                        {
                          required: true,
                          message: '请输入关联 ID',
                          trigger: 'blur'
                        }
                    ],
                },
            }
        },
        methods: {
            ...mapActions({
                setSels: 'setSels',
                setType: 'setType',
            }),
            formatDate(date) {
              this.daily_recommend.date = date;
            },
            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                    this.listLoading = true;
                    if (this.sels.length == 0) {
                        this.$notify({
                            title: '失败',
                            message: '请选择专辑或图片',
                            type: 'error'
                        });
                        return false;
                    }
                    var _this = this;
                    var album_ids = []
                    if (this.sels) {
                        this.sels.forEach(function(i){
                            album_ids.push(i.id);
                        });
                    }

                    var data = {
                        daily_recommend: this.daily_recommend,
                        album_ids: album_ids,
                    }
                    if (valid) {
                        var method = "put";
                        if (this.id){
                          method = "post";
                          data['id'] = this.id
                        }
                        Resource.options({
                            method: method,
                            resource: "notebook.daily_recommend",
                            data: data,
                            success: function(data) {
                                _this.listLoading = false;
                                NProgress.done();
                                _this.$notify({
                                    title: '发布成功',
                                    message: '发布成功',
                                    type: 'success'
                                });
                                window.location.href = '/#/minotebook/daily_recommend';
                            },
                            error: function(data) {
                                _this.listLoading = false;
                                NProgress.done();
                                var message = '保存失败，请联系相关研发～';
                                if (data.data && data.data.errmsg) {
                                  message = data.data.errmsg;
                                }
                                _this.$notify({
                                  title: '失败',
                                  message: message,
                                  type: 'error'
                                });
                            }
                        });
                    }else {
                        this.$notify({
                              title: '保存失败',
                              message: '请按照红色提示修改区域内容',
                              type: 'error'
                          });
                        this.submitVisible = false;
                        return false;
                    }
                });
            },

          formatAlbum(albums) {
              var _this = this;
              if(albums) {
                  this.setSels(albums);
              }
          },
          formatDailyRecommend(recommend){
            this.daily_recommend = {
                date: recommend.date,
                button_desc: recommend.button_desc,
                banner_id: recommend.banner_id,
                banner_title: recommend.banner_title,
                banner_desc: recommend.banner_desc
            }
          }

        },

        mounted() {
            // 设置查询类型为全部，图片墙展示图片及专辑
            this.setType(1);
            var daily_recommend_id = this.$route.query.id||"";
            this.id = daily_recommend_id
            this.listLoading = true;
            NProgress.start();
            var _this = this;
            Resource.get({
                resource: "notebook.daily_recommend",
                data: {
                  id: daily_recommend_id
                },
                success: function(data) {
                    if (daily_recommend_id) {
                        _this.formatDailyRecommend(data['daily_recommend']);
                        _this.formatAlbum(data['daily_recommend']['albums']);
                    }
                    _this.listLoading = false;
                    NProgress.done();
                },
                error: function() {
                    _this.listLoading = false;
                    NProgress.done();
                    _this.$notify({
                        title: '失败',
                        message: '请求失败',
                        type: 'error'
                    });
                }
            });
        }
    }

</script>



