<template>
  <div >
    <el-form label-position="left"  ref="picture" :model="picture" label-width="100px" @submit.prevent="onSubmit" style="margin:30px;" :rules="rules" border >
        <el-form-item label="ID" prop="Id" v-if="picture.legacy_id">
            {{ picture.legacy_id }}
        </el-form-item>
        <el-form-item label="标题" prop="title" style="margin-top:10px;">
            <el-input v-model="picture.title"></el-input>
        </el-form-item>
        <el-form-item label="链接图标" prop="link_type" style="margin-top:10px;">
            <el-select v-model="picture.link_type" placeholder="请选择链接图标">
                <el-option
                  v-for="item in data.link_types"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="状态" prop="status" style="margin-top:10px;">
            <el-select v-model="picture.status" placeholder="请选择状态">
                <el-option
                  v-for="item in data.status"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
        </el-form-item>

        <el-form-item label="描述" prop="desc" style="margin-top:10px;">
            <el-input type="textarea" v-model="picture.desc"></el-input>
        </el-form-item>
        <el-form-item label="Link链接" prop="link_url" style="margin-top:10px;">
            <el-input v-model="picture.link_url"></el-input>
        </el-form-item>

        <!-- <el-form-item label="文字分发">
            <el-button type="primary" size="small" @click="showDialogForm(picture)">添加文字分发</el-button>

            <div class="block">
            <el-table
              :data="wordDistData"
              style="width: 100%" border>
              <el-table-column
                prop="app_type"
                label="类型" align="center" width="100" :formatter="formatType">
              </el-table-column>
              <el-table-column
                prop="deeplink"
                label="deeplink"
                width="200">
              </el-table-column>
              <el-table-column
                prop="strintent"
                label="strintent"
                width="160">
              </el-table-column>

              <el-table-column
                prop="params"
                label="参数" width="100"
                >
              </el-table-column>
              <el-table-column label="操作">
                <template scope="scope">
                    <el-button type="danger" size="small" @click="delNotebookWordDistribution(scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
            </div>
        </el-form-item> -->

        <el-form-item label="图片"  style="margin-top:10px;">
            <el-upload
              class="upload-demo"
              :action="upload_url"
              :on-preview="handlePreview"
              :on-remove="handlePictureRemove"
              :on-success="handleSuccess"
              :file-list="picture.pictures"
              :data="picture_upload_type"
              list-type="picture">
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
            <!-- 图片预览弹窗 -->
          <el-dialog v-model="imageDialogVisible"  size="large" :modal="false" :modal-append-to-body="false">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </el-form-item>

        <el-form-item  style="margin-top:10px;">
            <el-button type="primary"  @click="onSubmit('picture')">保存</el-button>
        </el-form-item>
    </el-form>
    <div>
    <!-- <el-dialog  title="文字分发" :visible.sync="dialogFormVisible" :modal="false" :modal-append-to-body="false" :key="this.album_id" >
      <WordDistribution></WordDistribution>
    </el-dialog> -->
    </div>
  </div>
</template>

<script>
    import { mapState, mapActions } from 'vuex'
    import NProgress from 'nprogress'
    import WordDistribution from '../../components/notebook/WordDistribution'
    import GlobalConfig from '../../GlobalConfig'
    var Resource = require('../../util/Resource');
    export default {
        // components : {WordDistribution},
        computed: mapState ({
            notebook_sels: state => state.notebook_sels,
            // wordDistData: state => state.notebookWordDistributions,
            index: state => state.index,
            album_id: state => state.album_id,
        }),
        data() {

            return {
                id:"",
                imageDialogVisible: false,
                dialogImageUrl: "",
                data: {
                    link_types:[],
                    status:[],
                },
                upload_url:GlobalConfig.uploadImageUri,
                picture: {
                    legacy_id: '',
                    title: '',
                    link_type: 0,
                    status: 10,
                    desc: '',
                    pictures: [],
                    // word_dists:[],
                    link_url:'',
                },
                // dialogFormVisible: false,
                loading: false,
                picture_upload_type: {
                    type: "new"
                },
                rules: {
                    title: [
                      {
                          required: true,
                          message: '请输入标题',
                          trigger: 'blur'
                      }
                    ],
                    link_type: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请选择链接图标',
                          trigger: 'change'
                      }
                    ],
                    status: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请选择状态',
                          trigger: 'change'
                      }
                    ],
                    desc: [
                      {
                          required: true,
                          message: '请输入描述',
                          trigger: 'blur'
                      }
                    ],
                },
            }
        },
        methods: {
            ...mapActions({
                setNotebookSels: 'setNotebookSels',
                addNotebookSel:'addNotebookSel',
                setIndex:'setIndex',
            }),
            formatType: function(row, column) {
                return row.app_type == 0 ? 'EXT' : row.app_type == 1 ? 'INT' : '未知';
            },
            handlePictureRemove(file, fileList) {
                this.picture.pictures = [];
            },
            handlePreview(file) {
                this.dialogImageUrl = file.url;
                this.imageDialogVisible = true;
            },

            handleSuccess(response, file, fileList) {
                if (response.code == 200 && response.data) {
                   this.picture.pictures = [response.data];
                } else {
                    this.$notify({
                        title: '失败',
                        message: '图片上传失败',
                        type: 'error'
                    });
                    this.picture.pictures = [];
                }
            },

            // showDialogForm(picture) {
            //     this.form1 = {
            //         app_type: "",
            //     }
            //     this.dialogFormVisible = true;
            // },

            onSubmit(formName) {
              this.$refs[formName].validate((valid) => {
                if (this.picture.pictures.length== 0) {
                    this.$notify({
                        title: '失败',
                        message: '请上传图片',
                        type: 'error'
                    });
                    return false;
                }
                // if (this.picture.link_type > 0 && this.wordDistData.length == 0) {
                //     this.$notify({
                //         title: '提示',
                //         message: '请添加文字分发',
                //         type: 'error'
                //     });
                //     return false;
                // }
                // if (this.picture.link_type == 0 && this.wordDistData.length > 0) {
                //     this.$notify({
                //         title: '提示',
                //         message: '请选择链接图标类型',
                //         type: 'error'
                //     });
                //     return false;
                // }
                if (valid) {
                    var _this = this
                  if (this.picture.id) {
                    console.log('=====this.picture======',this.picture)
                      var data = {
                        picture: this.picture,
                        // word_dists: this.wordDistData,
                        id: this.picture.id
                      };
                      Resource.post({
                          resource: "notebook.picture",
                          data: data,
                          success: function(data) {
                            _this.$notify({
                                title: '保存成功',
                                message: '保存成功',
                                type: 'success'
                            });
                            window.location.reload();
                            _this.$parent.$parent.dialogPictureVisible=false;
                          },
                          error: function() {
                            _this.$notify({
                                title: '失败',
                                message: '保存失败，请联系相关研发～',
                                type: 'error'
                            });
                          }
                      });
                  } else {
                      var data = {
                        picture: this.picture,
                        // word_dists: this.wordDistData,
                      };
                      console.log('==666===this.picture======',this.picture)
                      Resource.put({
                          resource: "notebook.picture",
                          data: data,
                          success: function(data) {
                              _this.$notify({
                                  title: '保存成功',
                                  message: '保存成功',
                                  type: 'success'
                              });
                              let pic = _this.picture
                              // pic.word_dists= _this.wordDistData
                              // if(pic.index){
                              //   pic.index = _this.notebook_sels.length
                              // }
                              pic.id = data.id
                              pic.legacy_id = data.legacy_id
                              pic.icon = _this.picture.pictures[0].url
                              pic.images = [_this.picture.pictures[0].url]
                              console.log('-----pic.index----',pic.index)
                              console.log('-----_this.notebook_sels----',_this.notebook_sels)
                              _this.notebook_sels.splice(pic.index,1,pic)
                              // _this.setIndex(pic.index)
                              // _this.notebook_sels.push(pic)
                              // window.location.reload();
                              _this.$parent.$parent.dialogPictureVisible=false;
                          },
                          error: function() {
                              _this.$notify({
                                  title: '失败',
                                  message: '保存失败，请联系相关研发～',
                                  type: 'error'
                              });
                          }
                      });
                  }
                } else {
                  this.$notify({
                        title: '保存失败',
                        message: '请按照红色提示修改区域内容',
                        type: 'error'
                    });
                  return false;
                }
              });
            },
            formatPicture(picture) {
              // if (picture.word_dists) {
              //     this.setNotebookWordDistributions(picture.word_dists);
              // }
              var pictures = []
              if (picture) {
                  picture.images.forEach(function(i){
                      var arr = i.split("/");
                      pictures.push({
                          name: arr[arr.length-1],
                          url: i
                      });
                  });
              }
              if(this.album_id==0){
                this.picture = {
                    album_id: this.album_id,
                    title: '',
                    link_type: 0,
                    status: 10,
                    desc: '',
                    pictures: pictures,
                    id: '',
                    legacy_id: '',
                    link_url: '',
                    index:picture.index,
                };
              }else{
                this.picture = {
                    album_id: picture.album_id,
                    title: picture.title,
                    link_type: picture.link_type || 0,
                    status: picture.status || 10,
                    desc: picture.desc,
                    pictures: pictures,
                    id: picture.id,
                    legacy_id: picture.legacy_id,
                    link_url: picture.link_url || '',
                    index:this.index,
                };
            }
          }
        },
        mounted() {
            // this.setNotebookWordDistributions([]);
            this.API_UTIL.API_NOTEBOOK.getPictureSet().then(data => {
                this.data.link_types = data.data.link_types
                this.data.status = data.data.status
            });
            console.log(this.index,'=======this.index=======',this.album_id)
            if(this.index!=-1){
                var item = this.notebook_sels[this.index]
                this.formatPicture(item);
            }else{
                this.picture = {
                    id: '',
                    link_type:0,
                    status:10,
                    album_id:this.album_id,
                    // word_dists:[],
                    legacy_id: '',
                    title: '',
                    desc: '',
                    pictures: [],
                    link_url:'',
                    index:this.index,
                }
            }
        }
    }

</script>



