<template>
    <section style="left: 185px;">
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item label="专辑标题" >
                    <el-input v-model="filters.title" style="width: 200px;" ></el-input>
                </el-form-item>
                 <!-- <el-form-item label="专辑描述" >
                    <el-input v-model="filters.desc" style="width: 180px;"></el-input>
                </el-form-item> -->
                <el-form-item label="专辑ID" style="padding-left: 11px;">
                    <el-input v-model="filters.album_id" ></el-input>
                </el-form-item>
                <el-form-item label="所属CP" prop="cp" style="padding-left: 8px;">
                     <el-select
                       v-model="filters.cp"
                       filterable
                       remote
                       placeholder="请选择CP"
                       :remote-method="getCPs"
                       style="width:173px;">
                        <el-option
                            v-for="item in cps"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                        </el-option>
                     </el-select>
                </el-form-item>
                <br>
                <el-form-item label="专辑状态" >
                    <el-select v-model="filters.status" placeholder="请选择" style="width: 200px;">
                        <el-option
                          v-for="item in filter_result.status"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="创建人" prop="creator" >
                     <el-select
                       v-model="filters.creator"
                       filterable
                       remote
                       placeholder="请选择创建人"
                       :remote-method="getCreators" style="padding-left: 13px;width: 180px;">
                        <el-option
                            v-for="item in creators"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                        </el-option>
                     </el-select>
                </el-form-item>

                <el-form-item style="padding-left: 60px;">
                    <el-tooltip content="查询" placement="top">
                    <el-button type="primary" v-on:click="getAlbumList" class="el-icon-search"></el-button>
                    </el-tooltip>
                    <el-tooltip content="重置" placement="top">
                    <el-button type="primary" v-on:click="handleReset" class="el-icon-circle-cross" ></el-button>
                    </el-tooltip>
                    <el-tooltip content="新增" placement="top" >
                     <el-button type="primary" v-on:click="toAddAlbum" class="el-icon-plus"></el-button>
                     </el-tooltip>
                </el-form-item>
            </el-form>
        </el-col>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <font class="toolbar">更多操作:</font>
            <el-cascader
            :options="actions"
            @change="handleChange"
            placeholder="请选择">
            </el-cascader>
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>

        <!--列表-->
        <el-table :data="albums" highlight-current-row element-loading-text="拼命加载中"
        @selection-change="handleSelectionChange"
        @sort-change="handlerSortChange" v-loading="listLoading"  border style="font-size:12px; ">
            <el-table-column type="selection" width="50">
            </el-table-column>
            <el-table-column label="图片" width="300" align="left">
              <template scope="scope">
                <a :href="scope.row.icon" target="_blank">
                    <img :src="scope.row.icon" style="height: 200px">
                </a>
              </template>
            </el-table-column>
            <el-table-column prop="title"  label="标题"  >
            </el-table-column>

            <!-- <el-table-column prop="desc"  label="描述" width="300">
            </el-table-column> -->

            <el-table-column prop="legacy_id" label="ID" sortable="custom">
                <template scope="scope">
                    <a :href="'#/minotebook/edit_album?id='+scope.row.id" target="_blank">
                       {{scope.row.legacy_id}}
                    </a>
                </template>
            </el-table-column>


            <!-- <el-table-column prop="isready" label="发布" width="30" :formatter="formatBoolean" ></el-table-column> -->

             <el-table-column prop="status" label="状态" width="30" ></el-table-column>

            <el-table-column prop="cp" label="CP"> </el-table-column>

            <el-table-column prop="creator_name" label="创建者"> </el-table-column>

            <el-table-column prop="operator_name" label="最近操作者">
            </el-table-column>

            <el-table-column prop="create_time" label="入库时间" width="120" sortable
            ></el-table-column>

            <el-table-column prop="update_time" label="更新时间" width="120" sortable
            ></el-table-column>

            <!-- <el-table-column prop="last_readytime" label="最后发布时间" width="120" sortable
            ></el-table-column> -->
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>
    </section>
</template>

<script>
    import util from '../../../common/js/util'
    import NProgress from 'nprogress'

    export default {
        data() {
            return {
                filters: {
                    title: '',
                    status: '',
                    album_id:'',
                    cp:'',
                    sort: '-create_time',
                    creator:'',
                    // desc:''
                },
                cps: [],
                creators: [],
                filter_result:{},
                albums: [],
                total: 0,
                page: 1,
                pageSize: 30,
                listLoading: true,
                sels: [],//列表选中列
                actions:
                    [{
                      value: 'tooffline',
                      label: '停用专辑',
                    },{
                      value: 'toavailable',
                      label: '启用专辑',
                    },{
                      value: 'todailyrecommend',
                      label: '加入每日推荐',
                    }],
            }
        },
        methods: {
            getCreators(value) {
                var _this=this;
                var data={
                      name: value
                };
                this.API_UTIL.API_NOTEBOOK.getCreators(data).then(data=>{
                    if (data.code == 200) {
                        _this.creators = data.data.items;
                    }else {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                 });
            },

            getCPs(value) {
                var _this = this;
                var data={
                      name: value
                };
                this.API_UTIL.API_NOTEBOOK.getCPs(data).then(data=>{
                    if (data.code == 200) {
                        _this.cps = data.data.items;
                    }else {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                 });
            },
            toAddAlbum: function() {
                window.location.href = '/#/minotebook/add_album';
            },
            handleChange: function(value) {
                if (this.sels.length < 1) {
                    this.$notify({
                        title: '失败',
                        message: '请选择一条或一条以上专辑',
                        type: 'error'
                    });
                    return;
                }
                switch(value[0]) {
                    case "tooffline":
                        this.changeStatus("off");
                        break;
                    case "toavailable":
                        this.changeStatus("on");
                        break;
                    case "todailyrecommend":
                        this.toDailyRecommend();
                        break;
                }
            },
            changeStatus: function(status) {
                var _this = this;
                var data = {
                        ids: this.sels,
                        status: status
                    }
                this.API_UTIL.API_NOTEBOOK.changeStatus(data).then(data=>{
                    if (data.code == 200) {
                        _this.getAlbumList();
                        _this.$notify({
                            title: '成功',
                            message: '操作成功',
                            type: 'success'
                        });
                    }else {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                 });
            },
            toDailyRecommend: function() {
              var _this = this;
              var data = {
                  album_ids: this.sels,
              }
              this.API_UTIL.API_NOTEBOOK.toDailyRecommend(data).then(data=>{
                    if (data.code == 200) {
                        _this.$notify({
                              title: '发布成功',
                              message: '发布成功',
                              type: 'success'
                        });
                    }else {
                        var message = '操作失败，请联系相关研发～';
                          var id = 0;
                          if (data.data && data.data.errmsg) {
                              message = data.data.errmsg;
                              id = data.data.id;
                          }
                          _this.$notify({
                              title: '失败',
                              message: message,
                              type: 'error'
                          });
                          if (id > 0) {
                              window.location.href = "/#/minotebook/edit_daily_recommend?id="+ id;
                          }
                      }
                 });
            },
            handlerSortChange: function(obj) {
                if (obj.column) {
                    if (obj.order == "descending") {
                        this.filters.sort = "-" + obj.prop;
                    } else {
                        this.filters.sort = obj.prop;
                    }
                }
                this.getAlbumList();
            },
            // formatBoolean: function(row, column) {
            //     return row.isready == true ? '是' : row.isready == false ? '否' : '未知';
            // },
            handleCurrentChange(val) {
                this.page = val;
                this.getAlbumList();
            },
            handleSelectionChange(vals) {
                this.sels = [];
                var _this = this;
                vals.forEach(function(v){
                    _this.sels.push(v.id);
                })
            },
            handleReset() {
                this.filters={
                    title: '',
                    status: '',
                    album_id:'',
                    cp:'',
                    sort: '-create_time',
                    creator: '',
                    // desc:''
                },
                this.getAlbumList();
            },
            // 页面加载前获取集合值
            getSearch() {
                var _this = this;
                this.API_UTIL.API_NOTEBOOK.getAlbumSearch().then(data=>{
                    if (data.code == 200) {
                        _this.filter_result = data.data.filter_result;
                    }else {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                 });
            },
            getFilters() {
                var page = this.page || 1;
                var title = encodeURIComponent(this.filters.title || '');
                var status = this.filters.status;
                var album_id = this.filters.album_id || '';
                var cp_id = this.filters.cp || '';
                var creator = this.filters.creator || '';
                var sort = this.filters.sort || '-create_time';
                // var desc = this.filters.desc || '';
                return {
                    page: page,
                    title: title,
                    status: status,
                    album_id: album_id,
                    cp_id: cp_id,
                    creator: creator,
                    sort: sort,
                    // desc:desc
                }
            },
            //获取用户列表
            getAlbumList() {
                this.listLoading = true;
                let para = this.getFilters();
                NProgress.start();
                var _this = this;
                this.API_UTIL.API_NOTEBOOK.getAlbumList(para).then(data=>{
                    if (data.code == 200) {
                        _this.total = data.data.total_count;
                        _this.albums = data.data.items;
                        _this.listLoading = false;
                        NProgress.done();
                    }else {
                        _this.listLoading = false;
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                 });
            }
        },
        mounted() {
            this.getSearch();
            this.getAlbumList();
        }
    }

</script>

<style lang="scss" scoped>
.el-table td>div {
    height:201px!important;
}
</style>



