<template>
<div>
  <div class="el-upload-list el-upload-list--picture-card list-group" v-sortable="{ onUpdate: onUpdate }">

    <div class="el-upload-list__item is-success" v-for="pic in notebook_sels" style="height:250px;width:1300px" :key=pic.index>
    <el-form label-position="left"  ref="album" :model="pic" @submit.prevent="onSubmit" style="min-width:600px;" border>
      <div  style="float:left;">
        <el-form-item prop="icon">
          <img :src="pic.icon" style="height:250px;width:400px" >
          </img>
          <span class="el-upload-list__item-actions" style="height:250px;width:400px">
            <span class="el-upload-list__item-preview" style="height:250px;width:400px">
              <i class="el-icon-view" @click="handlePictureCardPreview(pic.icon)"></i>
            </span>
          </span>
        </el-form-item>
      </div>
      <div  style="margin-left: 20px;float:left;width:200px;">
        <el-form-item label="ID" prop="title" >
            <el-input v-model="pic.legacy_id"  :readonly="true"></el-input>
        </el-form-item>
        <el-form-item label="标题" prop="title" >
            <el-input v-model="pic.title" :readonly="true"></el-input>
        </el-form-item>

        <el-form-item style="margin-top: 10px;">
            <el-button type="primary"  @click="handleEditPicture(pic.index)">编辑</el-button>
        </el-form-item>
      </div>
      <div  style="margin-left: 50px;float:left;width:200px;">
        <el-form-item label="链接图标" prop="link_type">
            <el-select v-model="pic.link_type" placeholder="请选择链接图标" disabled="disabled">
                <el-option
                  v-for="item in data.link_types"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
        </el-form-item>
        <!-- <el-form-item label="Link链接" prop="link_url" style="width: 450px;">
            <el-input  v-model="pic.link_url" :readonly="true"></el-input>
        </el-form-item> -->
        <el-form-item label="描述" prop="desc" style="width: 450px;">
            <el-input  v-model="pic.desc" :readonly="true"></el-input>
        </el-form-item>
      </div>
      <div  style="margin-left: 50px;float:left;width:200px;">
        <el-form-item label="状态" prop="status">
            <el-select v-model="pic.status" placeholder="请选择状态" disabled="disabled">
                <el-option
                  v-for="item in data.status"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
        </el-form-item>
      </div>
      <!-- <img src="../../assets/delete.png" style="width: 30px;height: 30px;float:right;display: inline;margin-left:10px;" @click="handlePictureDel(pic)"> -->
          </img>

    </el-form>

    </div>

  </div>
  </br>
<!--   <div class="el-upload el-upload--picture-card" style="height:228px;width:137px;line-height: 220px" multiple="true" @click="handleGetPicture">
    <i class="el-icon-plus"></i>
  </div> -->


  <el-upload list-type="picture-card" multiple :limit="10"  :action="upload_url" :data="picture_upload_type"  :on-remove="handlePictureRemove" :on-success="handleSuccess">
  <i class="el-icon-plus"></i>
  </el-upload>

  <div>
  <!-- 图片列表弹窗 -->
  <el-dialog v-model="dialogPictureVisible"  :modal="true" :modal-append-to-body="false" width="100%" :key="this.index">
    <NotebookPicture ></NotebookPicture>
  </el-dialog>

  <!-- 图片预览弹窗 -->
  <el-dialog v-model="dialogVisible" size="middle">
    <img width="100%" :src="dialogImageUrl" alt="">
  </el-dialog>
</div>
</div>
</template>
<script>
    import { mapState, mapActions } from 'vuex';
    import NProgress from 'nprogress'
    import NotebookPicture from './CommonPicture'
    import GlobalConfig from '../../GlobalConfig'
    export default {
        components : {NotebookPicture},
        computed: mapState ({
            notebook_sels: state => state.notebook_sels,
            index: state => state.index,
            album_id: state => state.album_id,
            // wordDistData: state => state.notebookWordDistributions,
        }),
        data() {
          this.API_UTIL.API_NOTEBOOK.getPictureSet().then(data => {
                this.data.link_types = data.data.link_types
                this.data.status = data.data.status
            });
            return {
                fileList:[],
                data:{},
                dialogPictureVisible:false,
                dialogVisible: false,
                dialogImageUrl: "",
                upload_url:GlobalConfig.uploadImageUri,
                picture_upload_type: {
                    type: "new"
                },
            }
        },
        methods: {
            ...mapActions({
                setNotebookSels: 'setNotebookSels',
                setIndex:'setIndex',
            }),
            handlePictureRemove(file, fileList) {
                for(var i=0; i<this.notebook_sels.length; i++) {
                      if(this.notebook_sels[i].icon == file.response.data.url){
                        console.log('====iiiii====',i)
                        this.notebook_sels.splice(i,1)
                      }
                  }
                for(var i=0; i<this.notebook_sels.length; i++) {
                      this.notebook_sels[i].index = i
                  }
            },

            handleSuccess(response, file, fileList) {
                if (response.code == 200 && response.data) {
                    let pic = {}
                    if(this.album_id){
                      pic["album_id"] = this.album_id
                    }
                    pic["index"] = this.notebook_sels.length
                    pic["icon"] = response.data["url"]
                    pic["images"] = [response.data["url"]]
                    this.notebook_sels.push(pic)
                    // this.setIndex(this.notebook_sels.length)
                } else {
                    this.$notify({
                        title: '失败',
                        message: '图片上传失败',
                        type: 'error'
                    });
                }
            },

            handlePictureCardPreview(url) {
                this.dialogImageUrl = url;
                this.dialogVisible = true;
            },
            onUpdate: function(event) {
              this.notebook_sels.splice(event.newIndex, 0, this.notebook_sels.splice(event.oldIndex, 1)[0]);
            },
            handleEditPicture(index){
              this.setIndex(index);
              console.log('======index new======',this.index);
              this.dialogPictureVisible = true;
            },
            handleGetPicture(){
              this.dialogPictureVisible = true;
              this.setIndex(-1);
            }
        },
        mounted() {

        }
    }
</script>

<style>
.im_delete {
    width: 30px;
    height: 30px;
    float:right;
    display: inline;
}
</style>



