<template>
  <div >
    <el-form label-position="left"  ref="picture" :model="picture" label-width="100px" @submit.prevent="onSubmit" style="margin:30px;width:80%;min-width:600px;" :rules="rules" border >
         <el-form-item label="ID" prop="Id">
            {{ picture.legacy_id }}
        </el-form-item>
        <el-form-item label="标题" prop="title">
            <el-input v-model="picture.title"></el-input>
        </el-form-item>
        <el-form-item label="链接图标" prop="link_type">
            <el-select v-model="picture.link_type" placeholder="请选择链接图标">
                <el-option
                  v-for="item in data.link_types"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="状态" prop="status">
            <el-select v-model="picture.status" placeholder="请选择状态">
                <el-option
                  v-for="item in data.status"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="权重" prop="weight" style="display: none">
            <el-input v-model.number="picture.weight"></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="desc">
            <el-input type="textarea" v-model="picture.desc"></el-input>
        </el-form-item>
        <el-form-item label="标题颜色" prop="title_color">
            <el-select v-model="picture.title_color" placeholder="请选择标题颜色">
                <el-option
                  v-for="item in data.color_types"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="描述颜色" prop="desc_color">
            <el-select v-model="picture.desc_color" placeholder="请选择描述颜色">
                <el-option
                  v-for="item in data.color_types"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
            </el-select>
        </el-form-item>
        <el-form-item label="标题字体大小" prop="txt_size">
            <el-select v-model="picture.txt_size" placeholder="请选择标题字体大小">
                <el-option
                  v-for="item in data.font_size_types"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
            </el-select>
        </el-form-item>
        <el-form-item label="数据等级" prop="level">
           <el-checkbox v-model="picture.level">锁屏库</el-checkbox>
        </el-form-item>
        <el-form-item label="分类" prop="categories">
            <el-checkbox-group v-model="picture.categories">
                <el-checkbox v-for="filter in data.categories" :label="filter.id" :key="filter.id">{{filter.name}}</el-checkbox>
            </el-checkbox-group>
        </el-form-item>
        <!-- <el-form-item label="CP" prop="cp">
            <el-select v-model="picture.cp" placeholder="请选择CP">
               <el-option
                  v-for="item in data.cps"
                  :label="item.name"
                  :value="item.id"
                  :key="item.id">
                </el-option>
            </el-select>
        </el-form-item> -->
        <el-form-item label="CP" prop="cp">
          <el-select
              v-model="picture.cp"
              filterable
              remote
              placeholder="请选择CP"
              :remote-method="getCPs"
              :loading="loading">
              <el-option
                v-for="item in cps"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
        </el-form-item>
        <!-- <el-form-item label="品牌" prop="brand">
            <el-select v-model="picture.brand" placeholder="请选择品牌">
                <el-option
                  v-for="item in data.brands"
                  :label="item.name"
                  :value="item.id"
                  :key="item.id">
                </el-option>
            </el-select>
        </el-form-item> -->

        <el-form-item label="品牌" prop="brand">
          <el-select
              v-model="picture.brand"
              filterable
              remote
              placeholder="请选择品牌"
              :remote-method="getBrands"
              :loading="loading">
              <el-option
                v-for="item in brands"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="Vendor" prop="vendor">
            <el-select v-model="picture.vendor" placeholder="请选择Vendor">
                <el-option
                  v-for="item in data.vendors"
                  :label="item.name"
                  :value="item.id"
                  :key="item.id">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="标签" prop="tag">
          <el-select
              v-model="picture.tag"
              multiple
              filterable
              remote
              placeholder="请输入关键词"
              :remote-method="getTags"
              :loading="loading">
              <el-option
                v-for="item in tags"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
            <el-tooltip content="新增" placement="top">
                <el-button type="primary" @click="handleAdd" class="el-icon-plus"></el-button>
            </el-tooltip>
        </el-form-item>


        <el-form-item label="时间" prop="date">
        <div class="block">
            <el-date-picker v-model="picture.date" format="yyyy-MM-dd HH:mm:ss" type="datetimerange" placeholder="选择时间范围" @change="formatDate">
            </el-date-picker>
        </div>
        </el-form-item>



        <el-form-item label="文字分发">
            <el-button type="primary" size="small" @click="showDialogForm('word')">添加文字分发</el-button>

            <div class="block">
            <el-table
              :data="wordDistData"
              style="width: 100%" border>
              <!--  <el-table-column
                prop="id"
                label="ID"
                width="40">
              </el-table-column> -->
              <el-table-column
                prop="deeplink"
                label="deeplink"
                width="150">
              </el-table-column>
              <el-table-column
                prop="packagename"
                label="packagename"
                width="150">
              </el-table-column>
              <el-table-column
                prop="strintent"
                label="strintent"
                width="160">
              </el-table-column>
              <el-table-column
                prop="app_name"
                label="跳转应用">
              </el-table-column>
              <el-table-column
                prop="params"
                label="参数"
                >
              </el-table-column>
              <el-table-column label="操作">
                <template scope="scope">
                    <el-button type="danger" size="small" @click="delWordDistribution(scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
            </div>
        </el-form-item>

        <el-form-item label="图片">
            <el-upload
              class="upload-demo"
              :action="upload_url"
              :on-preview="handlePreview"
              :on-remove="handlePictureRemove"
              :on-success="handleSuccess"
              :file-list="picture.pictures"
              :data="picture_upload_type"
              list-type="picture">
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
            <!-- 图片预览弹窗 -->
          <el-dialog v-model="imageDialogVisible" size="tiny">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </el-form-item>
        <el-form-item label="附件类型">
            <el-select v-model="picture.acc_type" placeholder="请选择附件类型">
                <el-option
                  v-for="item in data.picture_types"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="上传附件">
            <el-upload
              class="upload-demo"
              :action="upload_url"
              :on-preview="handleAccPreview"
              :on-remove="handleRemove"
              :on-success="handleAccSuccess"
              :file-list="picture.acc_url"
              :data="acc_upload_type"
              list-type="text">
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
        </el-form-item>
        <!-- <el-form-item>
             <PictureWall></PictureWall>
        </el-form-item> -->
        <el-form-item>
            <el-button type="primary" :disabled="submitVisible" @click="onSubmit('picture')">发布</el-button>
        </el-form-item>
    </el-form>
    <div>
    <el-dialog  :title="dialogTitle" :visible.sync="dialogFormVisible">
      <WordDistribution></WordDistribution>
    </el-dialog>
    </div>
    <div>
    <!-- 弹框 新增、编辑banner图-->
    <el-dialog :visible.sync="dialogTagFormVisible"  :before-close="handleCancle" :title="dialogTagtitle">
        <el-form label-position="left"  ref="picture_tag" :model="picture_tag" label-width="100px" @submit.prevent="handleSubmit" style="margin:30px;width:80%;min-width:600px;" :rules="rules" border>
            <el-form-item label="标签名称" prop="tag_name">
                <el-input v-model="picture_tag.tag_name"></el-input>
            </el-form-item>
            <el-form-item label="描述" prop="tag_desc">
                <el-input v-model="picture_tag.tag_desc"></el-input>
            </el-form-item>
            <el-form-item label="权重" prop="tag_weight">
                <el-input-number v-model="picture_tag.tag_weight" :min="0"></el-input-number>
            </el-form-item>

            <el-form-item>
                <el-button type="primary"  v-on:click="handleSubmit">保存</el-button>
                <el-button type="primary" v-on:click="handleCancle">取 消</el-button>
            </el-form-item>
        </el-form>
    </el-dialog>
    </div>
  </div>
</template>

<script>
    import { mapState, mapActions } from 'vuex'
    import NProgress from 'nprogress'
    import PictureWall from '../../components/album/PictureWall'
    import WordDistribution from '../../components/album/WordDistribution'
    import GlobalConfig from '../../GlobalConfig'
    var Resource = require('../../util/Resource');
    var moment = require('moment');
    export default {
        components : {PictureWall, WordDistribution},
        computed: mapState ({
            wordDistData: state => state.wordDistributions
        }),
        data() {
            var start_time = moment().format("YYYY-MM-DD HH:mm:ss");
            var end_time = moment().add(180, 'days').format("YYYY-MM-DD HH:mm:ss");
            return {
                is_update_date:false,
                id:"",
                imageDialogVisible: false,
                dialogImageUrl: "",
                data: {
                },
                upload_url:GlobalConfig.uploadImageUri,
                picture: {
                    title: '',
                    link_type: '',
                    status: '',
                    weight: 0,
                    desc: '',
                    title_color: '',
                    desc_color: '',
                    desc: '',
                    cp: '',
                    brand: '',
                    tag: [],
                    date: [start_time, end_time],
                    pictures: [],
                    categories: [],
                    acc_url: [],
                    acc_type: '',
                    vendor: 1,
                    txt_size: '',
                    level:true
                },
                form1:{
                    app_type : '',
                    packagename: ''
                },
                gridData: {},
                dialogFormVisible: false,
                dialogPictureListVisible: true,

                dialogTagFormVisible: false,
                dialogTagtitle:'',
                picture_tag: {
                    tag_name: "",
                    tag_desc: "",
                    tag_weight: 0
                },

                formLabelWidth: '120px',
                dialogTitle:'',
                type: '',

                loading: false,
                packShow: false,
                deeplinkShow: true,
                tags: [],
                brands: [],
                cps: [],
                picture_upload_type: {
                    type: "old"
                },
                acc_upload_type:{
                    type: "new"
                },
                submitVisible: false,
                rules: {
                    title: [
                      {
                          required: true,
                          message: '请输入标题',
                          trigger: 'blur'
                      }
                    ],
                    link_type: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请选择链接图标',
                          trigger: 'change'
                      }
                    ],
                    status: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请选择状态',
                          trigger: 'change'
                      }
                    ],
                    weight: [
                      {
                          required: false,
                          type: 'number',
                          message: '请输入权重',
                          trigger: 'blur'
                      },
                      {
                          type: 'number',
                          message: '请输入数字',
                          trigger: 'blur'
                      }
                    ],
                    desc: [
                      {
                          required: true,
                          message: '请输入描述',
                          trigger: 'blur'
                      }
                    ],
                    title_color: [
                      {
                          required: true,
                          message: '请选择标题颜色',
                          trigger: 'change'
                      }
                    ],
                    desc_color: [
                      {
                          required: true,
                          message: '请选择描述颜色',
                          trigger: 'change'
                      }
                    ],
                    categories: [
                      {
                          type: 'array',
                          required: true,
                          message: '请至少选择一个分类',
                          trigger: 'blur'
                      }
                    ],
                    cp: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请选择cp',
                          trigger: 'change'
                      }
                    ],
                    brand: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请选择品牌',
                          trigger: 'change'
                      }
                    ],
                    vendor: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请选择vendor',
                          trigger: 'blur'
                      }
                    ],
                    tag: [
                      {
                          type: 'array',
                          required: true,
                          message: '请至少选择一个标签',
                          trigger: 'blur'
                      }
                    ],
                    tag_name: [
                      {
                          required: true,
                          message: '请输入标签名称',
                      }
                    ]
                },
            }
        },
        methods: {
            ...mapActions({
                delWordDistribution: 'delWordDistribution',
                setWordDistributions: 'setWordDistributions'
            }),
            handleAdd(){
                this.dialogTagFormVisible = true;
                this.dialogTagtitle = "添加标签";
                this.picture_tag = {
                    tag_name: "",
                    tag_desc: "",
                    tag_weight: 0
                }
            },
            handleCancle(){
                this.picture_tag=[];
                this.dialogTagFormVisible = false;
            },
            handleSubmit: function() {
             this.$refs['picture_tag'].validate((valid) => {
                  if (valid) {
                    var _this = this;
                    var data = {
                        name:this.picture_tag.tag_name,
                        desc:this.picture_tag.tag_desc,
                        weight:this.picture_tag.tag_weight
                    };
                    var method = "put";
                    this.listLoading = true;
                    NProgress.start();
                    Resource.options({
                        method: method,
                        resource: "tag.tag",
                        data: data,
                        success: function(data) {
                            _this.listLoading = false;
                            NProgress.done();
                            _this.dialogTagFormVisible=false;
                            _this.picture_tag = {
                                tag_name : "",
                                tag_desc: "",
                                tag_weight: 0
                            }
                            _this.$notify({
                                title: '成功',
                                message: '添加标签成功',
                                type: 'success'
                            });
                        },
                        error: function(data) {
                              _this.listLoading = false;
                              var message = '添加标签失败，请联系相关研发～';
                              if (data.data && data.data.errmsg) {
                                  message = data.data.errmsg
                              }
                              NProgress.done();
                              _this.$notify({
                                  title: '失败',
                                  message: message,
                                  type: 'error'
                              });
                            }
                    });

                  } else {
                    this.$notify({
                          title: '保存失败',
                          message: '请按照红色提示修改区域内容',
                          type: 'error'
                      });
                    return false;
                  }
                });
            },
            handleRemove(file, fileList) {
                this.picture.acc_url = [];
            },
            handlePictureRemove(file, fileList) {
                this.picture.pictures = [];
            },
            handlePreview(file) {
                this.dialogImageUrl = file.url;
                this.imageDialogVisible = true;
            },
            handleAccPreview(file) {
                if (file.url) {
                    window.open(file.url , '_blank');
                }
            },
            handleSuccess(response, file, fileList) {
                if (response.code == 200 && response.data) {
                   this.picture.pictures = [response.data];
                } else {
                    this.$notify({
                        title: '失败',
                        message: '图片上传失败',
                        type: 'error'
                    });
                    this.picture.pictures = [];
                }
            },
            handleAccSuccess(response, file, fileList) {
                if (response.code == 200 && response.data) {
                   this.picture.acc_url = [response.data];
                } else {
                    this.$notify({
                        title: '失败',
                        message: '附件上传失败',
                        type: 'error'
                    });
                    this.picture.acc_url = [];
                }
            },
            showDialogForm(type) {
                this.form1 = {
                    app_type: ""
                }
                this.deeplinkShow = true;
                this.packShow = false;
                this.dialogFormVisible = true;
                this.dialogTitle = '添加文字分发'
                this.type = 'word';
            },
            getTags(value) {
                //search tags
                var _this = this;
                this.loading = true;
                Resource.get({
                    resource: "tag.search_tag",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.loading = false;
                        _this.tags = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            getBrands(value) {
                //search brands
                var _this = this;
                this.loading = true;
                Resource.get({
                    resource: "brand.search_brand",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.loading = false;
                        _this.brands = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            getCPs(value) {
                //search brands
                var _this = this;
                this.loading = true;
                Resource.get({
                    resource: "cp.search_cp",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.loading = false;
                        _this.cps = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            onSubmit(formName) {
              this.$refs[formName].validate((valid) => {
                this.submitVisible = true;
                if (this.picture.pictures.length== 0) {
                    this.$notify({
                        title: '失败',
                        message: '请上传图片',
                        type: 'error'
                    });
                    this.submitVisible = false;
                    return false;
                }
                if ((this.picture.acc_type != 0 && this.picture.acc_type != "") && this.picture.acc_url.length == 0) {
                    this.$notify({
                        title: '提示',
                        message: '请上传附件',
                        type: 'error'
                    });
                    this.submitVisible = false;
                    return false;
                } else if ((this.picture.acc_type == 0 || this.picture.acc_type == "") && this.picture.acc_url.length != 0) {
                    this.$notify({
                        title: '提示',
                        message: '请选择附件类型',
                        type: 'error'
                    });
                    this.submitVisible = false;
                    return false;
                }
                if (this.picture.link_type > 0 && this.wordDistData.length == 0) {
                    this.$notify({
                        title: '提示',
                        message: '添加文字分发',
                        type: 'error'
                    });
                    this.submitVisible = false;
                    return false;
                }

                if (valid) {
                  var _this = this;
                  var data = {
                        picture: this.picture,
                        word_dists: this.wordDistData,
                        id: this.id
                      };
                  if (this.id) {
                      Resource.post({
                          resource: "album.picture",
                          data: data,
                          success: function(data) {
                            _this.$notify({
                                title: '发布成功',
                                message: '发布成功',
                                type: 'success'
                            });
                            window.location.href = '/#/apps/picture';
                            _this.submitVisible = false;

                          },
                          error: function() {
                            _this.$notify({
                                title: '失败',
                                message: '保存失败，请联系相关研发～',
                                type: 'error'
                            });
                            _this.submitVisible = false;
                          }
                      });
                  } else {
                      Resource.put({
                          resource: "album.picture",
                          data: data,
                          success: function(data) {
                              _this.$notify({
                                  title: '发布成功',
                                  message: '发布成功',
                                  type: 'success'
                              });
                              window.location.href = '/#/apps/picture';
                          },
                          error: function() {
                              _this.$notify({
                                  title: '失败',
                                  message: '保存失败，请联系相关研发～',
                                  type: 'error'
                              });
                              _this.submitVisible = false;
                          }
                      });
                  }
                } else {
                  this.$notify({
                        title: '保存失败',
                        message: '请按照红色提示修改区域内容',
                        type: 'error'
                    });
                  this.submitVisible = false;
                  return false;
                }
              });
            },
            formatPicture(picture) {
              this.tags = picture.tags;
              this.brands = picture.brands;
              this.cps = picture.cps;
              if (picture.word_dists) {
                  this.setWordDistributions(picture.word_dists);
              }
              var pictures = []
              if (picture) {
                  picture.images.forEach(function(i){
                      var arr = i.split("/");
                      console.log('----------')
                      pictures.push({
                          name: arr[arr.length-1],
                          url: i
                      });
                  });

              }

              this.picture = {
                    title: picture.title,
                    link_type: picture.link_type,
                    status: picture.status,
                    weight: picture.weight,
                    desc: picture.desc,
                    title_color: picture.title_color,
                    desc_color: picture.desc_color,
                    categories: picture.category_ids,
                    cp: picture.cp_id,
                    brand: picture.brand_id,
                    tag: picture.tag_ids,
                    date: [picture.start_time, picture.end_time],
                    pictures: pictures,
                    acc_url: [],
                    acc_type: picture.acc_type,
                    vendor: picture.vendor_id,
                    id: picture.id,
                    txt_size: picture.txt_size,
                    level: picture.level,
                    legacy_id: picture.legacy_id
            };
            if (picture.acc_cdn) {
                this.picture.acc_url.push({
                    name: picture.acc_md5,
                    url: picture.acc_cdn,
                    md5: picture.acc_md5
                });
            }

          },
          formatDate(date) {
              this.picture.date = date.split(' - ');
          }
        },
        mounted() {
            var picture_id = this.$route.query.id||"";
            this.id = picture_id
            this.listLoading = true;
            NProgress.start();
            var _this = this;
            Resource.get({
                resource: "album.picture",
                data: {
                  id: picture_id
                },
                success: function(data) {
                    if (picture_id) {
                        _this.formatPicture(data.picture);
                    }else{
                        _this.picture = {
                            link_type: 0,
                            title_color: "#ffffffff",
                            desc_color: "#ffffffff",
                            txt_size: "26",
                            cp: '',
                            brand: '',
                            vendor: '',
                            status: '',
                            categories: [],
                            tag:[],
                            title: '',
                            status: '',
                            weight: 0,
                            desc: '',
                            date: _this.picture.date,
                            pictures: [],
                            acc_url: [],
                            acc_type: 0,
                            vendor: 1,
                            level: true
                        }
                    }
                    _this.data = data;
                    _this.listLoading = false;
                    NProgress.done();
                },
                error: function() {
                    _this.listLoading = false;
                    NProgress.done();
                    _this.$notify({
                        title: '失败',
                        message: '请求失败',
                        type: 'error'
                    });
                }
            });
        }
    }

</script>



