<template>
    <section style="left: 185px;">
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item label="全局ID">
                    <el-input v-model="filters.global_id" style="width: 200px;"></el-input>
                </el-form-item>
                <el-form-item label="名称">
                    <el-input v-model="filters.name" style="width: 180px;"></el-input>
                </el-form-item>
                <el-form-item label="状态" style="padding-left: 28px;">
                    <el-select v-model="filters.status" placeholder="请选择状态" style="width: 175px;">
                        <el-option
                          v-for="item in filter_result.status"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                <el-tooltip content="查询" placement="top">
                    <el-button class="el-icon-search"  type="primary" v-on:click="getBrandList"></el-button>
                </el-tooltip>
                </el-form-item>
                <el-form-item>
                <el-tooltip content="新增" placement="top">
                    <el-button class="el-icon-plus" type="primary" @click="handleAdd"></el-button>
                </el-tooltip>
                </el-form-item>
                <!-- <el-form-item >
                    <el-button type="primary" @click="handlePublish">发布</el-button>
                </el-form-item> -->
            </el-form>
        </el-col>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>

        <!--列表-->
        <el-table :data="brands" highlight-current-row v-loading="listLoading" element-loading-text="拼命加载中"  border style="font-size:12px; ">
            <el-table-column prop="name"  label="名称" width="200" align="center"> </el-table-column>

            <el-table-column prop="global_id"  label="全局ID" width="200" align="center"></el-table-column>


            <el-table-column prop="index" label="权重"  width="250" align="center">
                <template scope="scope">
                    <el-input-number  v-model="scope.row.index" v-bind:value="scope.row.index" v-on:change="handleChange('index',$event, scope.row.id)"></el-input-number>
              </template>
            </el-table-column>

            <el-table-column prop="status" label="状态" width="250" align="center">
                <template scope="scope">
                    <el-radio-group  v-model="scope.row.status"  v-on:change="handleChange('status', scope.row.status, scope.row.id)" >
                        <el-radio
                            v-for="item in filter_result.status"
                            :label="item.key"
                            :value="item.value"
                            :key="item.key"
                            >{{item.value}}
                        </el-radio>
                    </el-radio-group>
                </template>
            </el-table-column>

            <el-table-column label="封面" width="235" align="left">
              <template scope="scope">
                <a :href="scope.row.cover" target="_blank">
                    <img :src="scope.row.cover" style="height: 70px">
                </a>
              </template>
            </el-table-column>

             <el-table-column label="海报" width="230" align="left">
              <template scope="scope">
                <a :href="scope.row.s_icon" target="_blank">
                    <img :src="scope.row.s_icon" style="height: 70px">
                </a>
              </template>
            </el-table-column>


            <el-table-column label="操作" width="200" align="center">
                <template scope="scope">
                <el-tooltip content="编辑" placement="top">
                    <el-button class="el-icon-edit" type="primary" size="small" v-on:click="handleUpdate(scope.row.id)"></el-button>
                </el-tooltip>
                </template>
            </el-table-column>
        </el-table>
        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>
        <!-- 弹框 新增、编辑-->
        <el-dialog :visible.sync="dialogFormVisible" :title="title" :before-close="handleCancle" top='5%'>
            <el-form label-position="left"  ref="brand" :model="brand" label-width="100px" @submit.prevent="onSubmit" style="margin:30px;width:80%;min-width:600px;" :rules="rules" border>

                <el-form-item label="名称" prop="name" style="width: 300px;">
                    <el-input v-model="brand.name"></el-input>
                </el-form-item>

                <el-form-item label="全局ID" prop="global_id" style="width: 500px;"v-show="globalIdShow" >
                    <el-input v-model="brand.global_id" readonly></el-input>
                </el-form-item>

                <el-form-item label="权重" prop="index" style="width: 275px;">
                    <el-input-number  v-model="brand.index"></el-input-number>
                </el-form-item>

                <el-form-item label="状态">
                    <el-select v-model="brand.status" placeholder="请选择" style="width: 175px;">
                        <el-option
                          v-for="item in filter_result.status"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="封面" width="100">
                    <el-upload
                      class="upload-demo"
                      :action="upload_url"
                      :on-preview="handlePreview"
                      :on-remove="handleCoverRemove"
                      :on-success="handleCoverSuccess"
                      :file-list="brand.cover"
                      :data="cover_data"
                      list-type="picture">
                      <el-button size="small" type="primary">点击上传</el-button>
                    </el-upload>
                </el-form-item>

                <el-form-item label="海报" width="100">
                    <el-upload
                      class="upload-demo"
                      :action="upload_url"
                      :on-preview="handlePreview"
                      :on-remove="handleSiconRemove"
                      :on-success="handleSiconSuccess"
                      :file-list="brand.s_icon"
                      :data="s_icon_data"
                      list-type="picture">
                      <el-button size="small" type="primary">点击上传</el-button>
                    </el-upload>
                </el-form-item>

                <!-- 图片预览弹窗 -->
                <el-dialog v-model="imageDialogVisible" size="small" :modal="false" :modal-append-to-body="false">
                    <img width="100%" :src="dialogImageUrl" alt="">
                </el-dialog>

                <el-form-item>
                    <el-button type="primary" v-on:click="onSubmit('brand')">保存</el-button>
                    <el-button v-on:click="handleCancle">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </section>
</template>

<script>
    import util from '../../common/js/util'
    import NProgress from 'nprogress'
    import GlobalConfig from '../../GlobalConfig'
    var Resource = require('../../util/Resource');

    export default {
        data() {
            return {
                filters: {
                    global_id: '',
                    status: '',
                    name:''
                },
                brands: [],
                upload_url:GlobalConfig.uploadImageUri,
                total: 0,
                pageSize: 30,
                page: 1,
                listLoading: false,
                imageDialogVisible: false,
                globalIdShow: true,
                cover_data:{
                    type: "new"
                },
                s_icon_data:{
                    type: "old"
                },
                filter_result:{},
                title:"",
                sort: '-id',
                //dialog
                dialogFormVisible: false,
                brand:{
                    id:'',
                    name:'',
                    global_id:'',
                    index:'',
                    status:'',
                    cover: [],
                    s_icon: []
                },
                dialogImageUrl: "",
                rules: {
                    name: [
                      {
                          required: true,
                          message: '请输入名称',
                          trigger: 'blur'
                      },
                    ],
                    index: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请输入顺序，只能为整数！',
                          trigger: 'blur'
                      }
                    ],
                }
            }
        },
        methods: {
            // 页面加载前获取集合值
            getSearch() {
                var _this = this;
                Resource.get({
                    resource: "brand.brand_search",
                    success: function(data) {
                        _this.filter_result = data.filter_result;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                });
            },
            handleCurrentChange(val) {
                this.page = val;
                this.brands = [];
                this.getBrandList();
            },
            handlePublish(){
                var _this = this;
                Resource.post({
                    resource: "brand.brand_publish",
                    success: function(data) {
                        _this.$notify({
                            title: '发布成功',
                            message: '发布成功',
                            type: 'success'
                        });
                    },
                    error: function() {
                        _this.$notify({
                            title: '发布失败',
                            message: '发布失败',
                            type: 'error'
                        });
                    }
                });
            },
            getFilters() {
                var page = this.page || 1;
                var status = this.filters.status;
                var global_id = this.filters.global_id || '';
                if(global_id.length!=0 && global_id.length!=10){
                    this.$notify({
                        title: '全局ID格式错误',
                        message: '请输入完整的全局ID',
                        type: 'error'
                    });
                    return
                }
                var name = this.filters.name || '';
                return {
                    page: page,
                    status: status,
                    global_id: global_id,
                    name: name
                }
            },
            handleAdd(){
                this.globalIdShow = false;
                this.dialogFormVisible = true;
                this.title = "添加媒体";
            },
            handleCancle(){
                this.brand_clean(this.brand)
            },
            handlePreview(file) {
                this.dialogImageUrl = file.url;
                this.imageDialogVisible = true;
            },
            handleCoverRemove(file, fileList) {
                this.brand.cover = [];
            },
            handleSiconRemove(file, fileList) {
                this.brand.s_icon = [];
            },
            handleCoverSuccess(response, file, fileList) {
                if (response.code == 200 && response.data) {
                   this.brand.cover = [response.data];
                } else {
                    this.$notify({
                        title: '失败',
                        message: '图片上传失败',
                        type: 'error'
                    });
                    this.brand.cover = [];
                }
            },
            handleSiconSuccess(response, file, fileList) {
                if (response.code == 200 && response.data) {
                   this.brand.s_icon = [response.data];
                } else {
                    this.$notify({
                        title: '失败',
                        message: '图片上传失败',
                        type: 'error'
                    });
                    this.brand.s_icon = [];
                }
            },
            formatCategory(brand) {
                var cover_array = [];
                var s_icon_array = [];
                if (brand) {
                    // 封面
                    var cover_link = brand.cover
                    var arr = cover_link.split("/");
                    cover_array.push({
                        name: arr[arr.length-1],
                        url: cover_link
                    });
                    // 海报
                    var s_icon_link = brand.s_icon
                    var arr = s_icon_link.split("/");
                    s_icon_array.push({
                        name: arr[arr.length-1],
                        url: s_icon_link
                    });
                }
                this.brand = {
                    name: brand.name,
                    global_id: brand.global_id,
                    index: brand.index,
                    status: brand.status,
                    cover: cover_array,
                    s_icon: s_icon_array,
                    id: brand.id
                };
            },
            handleUpdate(id){
                var data={
                    id: id
                };
                var _this = this;
                Resource.get({
                    resource: "brand.brand",
                    data:data,
                    success: function(data) {
                        _this.dialogFormVisible = true;
                        _this.globalIdShow = true;
                        _this.title = "修改媒体";
                        _this.formatCategory(data.brand);
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                });

            },
            handleChange(type,value,id) {
                this.listLoading = true;
                var _this = this;
                var data = {}
                var resource = ''
                if(type=='index'){
                    data= {
                        type: type,
                        index: value,
                        id: id
                    };
                    resource = "brand.brand_index"
                }else{
                    data= {
                        type: type,
                        status: value,
                        id: id
                    };
                    resource = "brand.brand_status"
                };
                this.listLoading = true;
                NProgress.start();
                Resource.post({
                    resource: resource,
                    data: data,
                    success: function(data) {
                      _this.listLoading = false;
                      NProgress.done();
                      _this.getBrandList();
                      _this.brand_clean(_this.brand);
                      _this.$notify({
                        title: '成功',
                        message: '保存成功',
                        type: 'success'
                      });
                    },
                    error: function() {
                      _this.listLoading = false;
                      NProgress.done();
                      _this.$notify({
                          title: '失败',
                          message: '保存失败，请联系相关研发～',
                          type: 'error'
                      });
                    }
                });
            },
            brand_clean(brand){
                brand.id = '';
                brand.name = '';
                brand.global_id = '';
                brand.index = '';
                brand.cover = [];
                brand.s_icon = [];
                this.dialogFormVisible = false;
            },
            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                  if (valid) {
                    var _this = this;
                    var data = {
                        brand: this.brand,
                    };
                    this.listLoading = true;
                    NProgress.start();
                    var method = "put";
                    if (this.brand.id && this.brand.id != "" ) {
                        method = "post"
                    }
                    Resource.options({
                        method: method,
                        resource: "brand.brand",
                        data: data,
                        success: function(data) {
                          _this.listLoading = false;
                          NProgress.done();
                          _this.brands = [];
                          _this.getBrandList();
                          _this.brand_clean(_this.brand);
                          _this.$notify({
                            title: '成功',
                            message: '保存成功',
                            type: 'success'
                          });
                        },
                        error: function(data) {
                          _this.listLoading = false;
                          var message = '保存失败，请联系相关研发～';
                          if (data.data && data.data.errmsg) {
                              message = data.data.errmsg
                          }
                          NProgress.done();
                          _this.$notify({
                              title: '失败',
                              message: message,
                              type: 'error'
                          });
                        }
                    });
                  } else {
                    this.$notify({
                          title: '保存失败',
                          message: '请按照红色提示修改区域内容',
                          type: 'error'
                      });
                    return false;
                  }
                });
            },
            //获取用分类列表
            getBrandList() {
                let para = this.getFilters();
                this.listLoading = true;
                NProgress.start();
                this.brands = [];
                var _this = this;
                Resource.get({
                    resource: "brand.brand_list",
                    data: para,
                    success: function(data) {
                        _this.total = data.total_count;
                        _this.brands = data.items;
                        _this.pageSize = data.page_size;
                        _this.listLoading = false;
                        NProgress.done();
                    },
                    error: function() {
                        _this.listLoading = false;
                        NProgress.done();
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
        },
        mounted() {
            this.getSearch();
            this.getBrandList();
        }
    }

</script>

<style lang="scss" scoped>
.el-table td>div {
    height:100px!important;
}
</style>



