<template>
    <section style="left: 185px;">
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">

                <el-form-item style="padding-left: 0px;">
                <el-date-picker v-model="filters.date" type="date" placeholder="选择日期" @change="formatDate">
                </el-date-picker>
                </el-form-item>

                <el-form-item>
                    <el-tooltip content="查询" placement="top">
                    <el-button class="el-icon-search"  type="primary" v-on:click="getDailyRecommendList"></el-button>
                    </el-tooltip>
                    <el-tooltip content="新增" placement="top">
                    <el-button class="el-icon-plus" type="primary" @click="handleAdd"></el-button>
                    </el-tooltip>
                </el-form-item>
            </el-form>
        </el-col>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <font class="toolbar">更多操作:</font>
            <el-cascader
            :options="actions"
            @change="handleChange"
            placeholder="更多操作">
            </el-cascader>
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>

        <!--列表-->
        <el-table :data="daily_recommends" highlight-current-row v-loading="listLoading" @selection-change="handleSelectionChange" border style="font-size:12px; ">

            <el-table-column type="selection" width="50">
            </el-table-column>

            <el-table-column prop="date"  label="推荐日期" min-width="10" align="center">
                <template scope="scope">
                    <a :href="'#/apps/edit_daily_recommend?id='+scope.row.id" target="_blank">
                    {{ scope.row.date }}
                    </a>
                </template>
            </el-table-column>

            <el-table-column prop="isready"  label="发布状态" min-width="10" align="center" :formatter="formatBoolean">
            </el-table-column>

            <el-table-column prop="button_desc"  label="按钮文字" min-width="10" align="center">
            </el-table-column>

            <el-table-column prop="banner_id"  label="关联id" min-width="10" align="center">
            </el-table-column>

            <el-table-column prop="banner_title"  label="banner标题" min-width="10" align="center">
            </el-table-column>

            <el-table-column prop="banner_desc"  label="banner描述" min-width="40" align="left">
            </el-table-column>
        </el-table>


        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>
    </section>
</template>

<script>
    import util from '../../common/js/util'
    import NProgress from 'nprogress'
    var Resource = require('../../util/Resource');

    export default {
        data() {
            return {
                filters: {
                    date: ''
                },
                data: {},
                daily_recommends: [],
                daily_recommend: {
                    date: '',
                    button_desc: '',
                    banner_id: '',
                    banner_title: '',
                    banner_desc: '',
                },
                total: 0,
                pageSize: 30,
                page: 1,
                listLoading: false,
                sels: [],//列表选中列
                actions:
                    [{
                      value: 'todailyrecommend',
                      label: '发布到每日常规推荐（推荐日期那天生效）',
                    },
                    {
                      value: 'tosearchsuggest',
                      label: '发布到 APP 搜索推荐页',
                    },
                    ],

                icon_data: {
                    type: "new"
                },
                rules: {
                    name: [
                      {
                          required: true,
                          message: '请输入名称',
                          trigger: 'blur'
                      }
                    ],

                }

            }
        },
        methods: {
            formatBoolean: function(row, column) {
                return row.isready == true ? '是' : row.isready == false ? '否' : '未知';
            },
            handleAdd(){
                window.location.href = '/#/apps/add_daily_recommend';
            },

            handleChange: function(value) {
                if (this.sels.length < 1) {
                    this.$notify({
                        title: '失败',
                        message: '请选择一条每日推荐',
                        type: 'error'
                    });
                    return;
                }
                if (this.sels.length > 1) {
                    this.$notify({
                        title: '失败',
                        message: '只能发布一条每日推荐',
                        type: 'error'
                    });
                    return;
                }
                switch(value[0]) {
                    case "todailyrecommend":
                        this.toDailyRecommend();
                        break;
                    case "tosearchsuggest":
                        this.toSearchSuggest();
                        break;
                }
            },

            toDailyRecommend: function() {
                var _this = this;
                Resource.post({
                    resource: "daily_recommend.to_daily_recommend",
                    data: {
                        id: this.sels,
                    },
                    success: function(data) {
                        _this.$notify({
                            title: '发布成功',
                            message: '发布成功',
                            type: 'success'
                        });
                        _this.getDailyRecommendList();
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },

            toSearchSuggest: function() {
                var _this = this;
                Resource.post({
                    resource: "daily_recommend.to_search_suggest",
                    data: {
                        id: this.sels
                    },
                    success: function(data) {
                        _this.$notify({
                            title: '发布成功',
                            message: '发布成功',
                            type: 'success'
                        });
                        _this.getDailyRecommendList();
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },

            handleCurrentChange(val) {
                this.page = val;
                this.getDailyRecommendList();
            },
            //点击某一行的单选框时触发的事件
            handleSelectionChange(vals) {
                var arr = []
                vals.forEach(function(v){
                    arr.push(v.id);
                });
                this.sels = arr;
            },
            formatDate(date) {
              this.filters.date = date;
            },
            getFilters() {
                var page = this.page || 1;
                var date = this.filters.date || '';
                return {
                    page: page,
                    date: date
                }
            },

            //获取每日推荐列表
            getDailyRecommendList() {
                let para = this.getFilters()
                this.listLoading = true;
                NProgress.start();
                var _this = this;
                Resource.get({
                    resource: "daily_recommend.daily_recommend_list",
                    data: para,
                    success: function(data) {
                        _this.total = data.total_count;
                        _this.daily_recommends = data.items;

                        _this.pageSize = data.page_size;
                        _this.listLoading = false;
                        NProgress.done();
                    },
                    error: function() {
                        _this.listLoading = false;
                        NProgress.done();
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                });
            }
        },
        mounted() {
            this.getDailyRecommendList();
        }
    }

</script>

<style lang="scss" scoped>
.el-table td>div {
    height:100px!important;
}
</style>



