<template>
  <div>
    <el-form label-position="left"  ref="album" :model="album" label-width="100px" @submit.prevent="onSubmit" style="margin:30px;width:80%;min-width:600px;" :rules="rules" border>

        <el-form-item label="标题" prop="title">
            <el-input v-model="album.title"></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="status">
            <el-select v-model="album.status" placeholder="请选择状态">
                <el-option
                  v-for="item in data.status"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
        </el-form-item>
        <!-- <el-form-item label="描述" prop="desc">
            <el-input type="textarea" v-model="album.desc"></el-input>
        </el-form-item> -->
        <el-form-item label="分类" prop="categories">
            <el-checkbox-group v-model="album.categories">
                <el-checkbox v-for="filter in data.categories" :label="filter.id" :key="filter.id">{{filter.name}}</el-checkbox>
            </el-checkbox-group>
        </el-form-item>
        <el-form-item label="CP" prop="cp">
            <el-select v-model="album.cp" placeholder="请选择CP">
               <el-option
                  v-for="item in data.cps"
                  :label="item.name"
                  :value="item.id"
                  :key="item.id">
                </el-option>
            </el-select>
        </el-form-item>


        <el-form-item label="图片" prop="images">
             <PictureWall></PictureWall>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" :disabled="submitVisible" @click="onSubmit('album')">发布</el-button>
        </el-form-item>
    </el-form>
    <div>
    </div>
  </div>
</template>

<script>
    import { mapState, mapActions } from 'vuex'
    import NProgress from 'nprogress'
    import PictureWall from '../../../components/notebook/PictureWall'
    export default {
        components : {
          PictureWall,
        },
        computed: mapState ({
            notebook_sels: state => state.notebook_sels,
            album_id: state => state.album_id,
        }),
        data() {
            return {
                id:"",
                data: {
                },
                album: {
                    title: '',
                    status: '',
                    // desc: '',
                    cp: '',
                    pictures: [],
                    categories: [],
                },
                old_status:0,
                is_update:0,
                //集合
                status: [],
                username: '',
                loading: false,
                submitVisible: false,
                rules: {
                    title: [
                      {
                          required: true,
                          message: '请输入标题',
                          trigger: 'blur'
                      }
                    ],
                    status: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请选择状态',
                          trigger: 'change'
                      }
                    ],
                    // desc: [
                    //   {
                    //       required: true,
                    //       message: '请输入描述',
                    //       trigger: 'blur'
                    //   }
                    // ],
                    categories: [
                      {
                          type: 'array',
                          required: true,
                          message: '请至少选择一个分类',
                          trigger: 'change'
                      }
                    ],
                    cp: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请选择cp',
                          trigger: 'change'
                      }
                    ],
                },
            }
        },
        methods: {
            ...mapActions({
                setNotebookSels: 'setNotebookSels',
                addNotebookSel: 'addNotebookSel',
                setAlbumID:'setAlbumID',
            }),

            onSubmit(formName) {
              this.$refs[formName].validate((valid) => {
                this.submitVisible = true;
                if (this.notebook_sels.length == 0) {
                    this.$notify({
                        title: '失败',
                        message: '请选择图片',
                        type: 'error'
                    });
                    this.submitVisible = false;
                    return false;
                }
                var user = sessionStorage.getItem('user');
                if (user) {
                    user = JSON.parse(user);
                    this.username = user.username
                }
                if (valid) {
                  var _this = this;
                  var legacy_ids = []
                  console.log('=====this.album=========',this.album)
                  var data = {
                        album: this.album,
                        id: this.id,
                        username: this.username,
                        is_update:this.is_update,
                        old_status:this.old_status,
                      };
                      console.log('===this.notebook_sels====',this.notebook_sels)
                  for(var i=0; i<this.notebook_sels.length; i++) {
                      this.notebook_sels[i].index = i
                      if(!this.notebook_sels[i].legacy_id){
                        this.$notify({
                          title: '失败',
                          message: '请编辑上传的图片',
                          type: 'error'
                        });
                        this.submitVisible = false;
                        return false;
                      }
                  }
                  data.album.pictures = this.notebook_sels;

                  if (this.id) {
                    this.API_UTIL.API_NOTEBOOK.postAlbum(data).then(data=>{
                        if (data.code == 200) {
                             _this.$notify({
                                title: '发布成功',
                                message: '发布成功',
                                type: 'success'
                            });
                            window.location.href = '/#/minotebook/album';
                            _this.submitVisible = false;
                        }else {
                            _this.$notify({
                                title: '失败',
                                message: '保存失败，请联系相关研发～',
                                type: 'error'
                            });
                            _this.submitVisible = false;
                        }
                      });
                  } else {
                    this.API_UTIL.API_NOTEBOOK.putAlbum(data).then(data=>{
                        if (data.code == 200) {
                            _this.$notify({
                                  title: '发布成功',
                                  message: '发布成功',
                                  type: 'success'
                              });
                            window.location.href = '/#/minotebook/album';
                        }else {
                            _this.$notify({
                                  title: '失败',
                                  message: '保存失败，请联系相关研发～',
                                  type: 'error'
                              });
                            _this.submitVisible = false;
                        }
                     });
                  }
                } else {
                  this.$notify({
                        title: '保存失败',
                        message: '请按照红色提示修改区域内容',
                        type: 'error'
                    });
                  this.submitVisible = false;
                  return false;
                }
              });
            },
            formatAlbum(album) {
              var _this = this;
              var pictures = []
              var status_lst = []
              if (album) {
                  album.pictures.forEach(function(i){
                      pictures.push(_this.formatePicture(album.id,i));
                      status_lst.push(i.status)
                  });
              }
              // old_status用于保存专辑初始化状态，用于后台判断启用专辑时是否启用全部图片，只有初始状态是停用并且图片都是停用状态时才会全部启用
              if(album.status==20){
                if(status_lst.indexOf(10)==-1){
                  //说明专辑下的图片全是停用的，此时如果专辑编辑时修改专辑为启用状态的话，这时会把所有图片也改成启用状态
                  this.is_update = 1
                  this.old_status = album.status
                }
              }
              this.album = {
                    title: album.title,
                    link_type: album.link_type,
                    status: album.status,
                    // desc: album.desc,
                    categories: album.category_ids,
                    cp: album.cp_id,
                    pictures: pictures,
                    id: album.id,
            };
            if(pictures) {
                this.setNotebookSels(pictures);
            }
          },
          formatePicture(album_id,picture) {
              return {
                  album_id: album_id,
                  title: picture.title,
                  link_type: picture.link_type,
                  status: picture.status,
                  desc: picture.desc,
                  categories: picture.category_ids,
                  cp: picture.cp_id,
                  id: picture.id,
                  icon: picture.picture_url,
                  images: picture.images,
                  legacy_id: picture.legacy_id,
                  link_url: picture.link_url,
                  // word_dists: picture.word_dists,
              }
          },
        },
        mounted() {
            var album_id = this.$route.query.id||"";
            console.log('======album_id======',album_id)
            this.id = album_id
            this.listLoading = true;
            NProgress.start();
            var _this = this;
            var data = {
                id: album_id
            };
            this.API_UTIL.API_NOTEBOOK.getAlbumDetail(data).then(data=>{
                if (data.code == 200) {
                    _this.data = data.data;
                    this.data = data.data;
                    if (album_id) {
                      this.setAlbumID(album_id)
                      _this.formatAlbum(data.data.album);
                    }else{
                      this.setNotebookSels([]);
                    }
                    _this.listLoading = false;
                    NProgress.done();
                }else {
                    _this.listLoading = false;
                    NProgress.done();
                    _this.$notify({
                        title: '失败',
                        message: '请求失败',
                        type: 'error'
                    });
                }
             });
        }
    }

</script>



