<template>
<div>
  <ul class="el-upload-list el-upload-list--picture-card list-group" v-sortable="{ onUpdate: onUpdate }">
    <li class="el-upload-list__item is-success" v-for="item in sels" style="height:228px;width:137px" :key="item.id">
      <img :src="item.icon" alt="" class="el-upload-list__item-thumbnail">
      <a class="el-upload-list__item-name">
        <i class="el-icon-document"></i>{{item.title}}</a>
      <label class="el-upload-list__item-status-label">
        <i class="el-icon-upload-success el-icon-check"></i>
      </label>
      <i class="el-icon-close"></i>
      <!---->
      <span class="el-upload-list__item-actions">
        <span class="el-upload-list__item-preview">
          <i class="el-icon-view" @click="handleAlbumCardPreview(item.id)"></i>
        </span>
        <span class="el-upload-list__item-delete">
          <i class="el-icon-delete2" @click="handleAlbumCardDel(item)"></i>
        </span>
      </span>
    </li>
  </ul>
  <div class="el-upload el-upload--picture-card" style="height:228px;width:137px;line-height: 220px" @click="handleGetAlbumList">
    <i class="el-icon-plus"></i>
  </div>
  <div>
  <!-- 图片列表弹窗 -->
  <el-dialog size="large" v-model="dialogAlbumListVisible">
    <AlbumList></AlbumList>
  </el-dialog>

</div>
</div>
</template>
<script>
    import { mapState, mapActions } from 'vuex';
    import NProgress from 'nprogress'
    import AlbumList from './CommonAlbumList'
    export default {
        components : {AlbumList},
        computed: mapState ({
            sels: state => state.sels,
        }),
        data() {
            return {
                dialogAlbumListVisible:false,
            }
        },
        methods: {
            ...mapActions({
                handleAlbumCardDel: 'clickCommonPic',
                setSels: 'setSels',
            }),
            handleAlbumCardPreview(id) {
              window.open('/#/minotebook/edit_album?id='+id , '_blank');
            },
            onUpdate: function(event) {
              this.sels.splice(event.newIndex, 0, this.sels.splice(event.oldIndex, 1)[0]);
            },
            handleGetAlbumList(){
              this.dialogAlbumListVisible = true;
            }
        },
        mounted() {
            this.setSels([]);
        }
    }
</script>



