<template>
    <el-row>
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form>
                <el-form-item>
                    <el-tooltip content="新增" placement="top">
                    <el-button class="el-icon-plus" type="primary" v-on:click="handleAdd"></el-button>
                    </el-tooltip>
                </el-form-item>
            </el-form>
        </el-col>
        <!--列表-->
        <el-table :data="vendors" highlight-current-row v-loading="listLoading" @selection-change="selsChange" border style="font-size:12px; ">

            <el-table-column prop="name"  label="名称" min-width="10" align="center">
            </el-table-column>

            <el-table-column prop="cms_id" label="Vendor ID" min-width="10" align="center">
            </el-table-column>

            <el-table-column prop="legacy_id" label="Legacy ID" min-width="10" align="center">
            </el-table-column>

            <el-table-column prop="desc" label="描述" min-width="10" align="center">
            </el-table-column>

            <el-table-column label="操作" width="100">
                <template scope="scope">
                    <el-button type="primary" size="small" class="el-icon-edit" @click="handleUpdate(scope.row.id)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!-- 弹框 新增、编辑vendor-->
        <el-dialog :visible.sync="dialogFormVisible" :before-close="handleCancle" >
            <el-form label-position="left"  ref="vendor" :model="vendor" label-width="100px" @submit.prevent="onSubmit" style="margin:30px;width:80%;min-width:600px;"  border>

                <el-form-item label="名称" prop="name">
                    <el-input v-model="vendor.name"></el-input>
                </el-form-item>

                <el-form-item prop="cms_id" label="Vendor ID">
                    <el-input v-model="vendor.cms_id" readonly></el-input>
                </el-form-item>

                <el-form-item prop="legacy_id" label="Legacy ID">
                    <el-input v-model="vendor.legacy_id" readonly></el-input>
                </el-form-item>

                <el-form-item prop="desc" label="描述">
                    <el-input v-model="vendor.desc"></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" v-on:click="onSubmit('vendor')">保存</el-button>
                    <el-button v-on:click="handleCancle">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="30" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
    </el-row>
</template>

<script>
    import util from '../../common/js/util'
    import NProgress from 'nprogress'
    var Resource = require('../../util/Resource');

    export default {
        data() {
            return {
                data: {},
                vendors: [],
                vendor: {
                    id:'',
                    name: '',
                    legacy_id: '',
                    cms_id: '',
                    desc: ''
                },
                total: 0,
                pageSize: 30,
                page: 1,
                listLoading: false,
                dialogFormVisible: false
            }
        },
        methods: {
            formatBoolean: function(row, column) {
                return row.isready == true ? '是' : row.isready == false ? '否' : '未知';
            },
            formatAlbums: function(row, column) {
                return row.albums.join(",");
            },
            handleAdd(){
                this.dialogFormVisible = true
            },
            handleCancle(){
                this.dialogFormVisible = false;
            },
            handleUpdate(id){
                var data={
                    id:id
                }
                var _this = this;
                Resource.get({
                    resource: "vendor.vendor",
                    data:data,
                    success: function(data) {
                        _this.data = data;
                        _this.vendor = {
                            id: data.vendor.id,
                            name: data.vendor.name,
                            legacy_id: data.vendor.legacy_id,
                            cms_id: data.vendor.cms_id,
                            desc: data.vendor.desc
                        };
                        _this.dialogFormVisible = true;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                });

            },

            handleCurrentChange(val) {
                this.page = val;
                this.getVendorList();
            },
            selsChange(val) {
                this.sels = val;
            },
            //获取用Vendor列表
            getVendorList() {
                let para = {
                    page: this.page
                };
                this.listLoading = true;
                NProgress.start();
                var _this = this;
                Resource.get({
                    resource: "vendor.vendor_list",
                    data: para,
                    success: function(data) {
                        _this.total = data.total_count;
                        _this.vendors = data.items;
                        _this.pageSize = data.page_size;
                        _this.listLoading = false;
                        NProgress.done();
                    },
                    error: function() {
                        _this.listLoading = false;
                        NProgress.done();
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                    var _this = this;
                    var data = {
                        vendor: this.vendor
                    };
                    console.log('===============');
                    console.log(data);
                    this.listLoading = true;
                    NProgress.start();
                    var method = "put";
                    if (this.vendor.id){
                        method = "post";
                    }
                    Resource.options({
                        method: method,
                        resource: "vendor.vendor",
                        data: data,
                        success: function(data) {
                          _this.listLoading = false;
                          NProgress.done();
                          _this.dialogFormVisible=false;
                          _this.getVendorList();
                          _this.vendor={};
                          _this.$notify({
                            title: '成功',
                            message: '保存成功',
                            type: 'success'
                          });
                        },
                        error: function() {
                          _this.listLoading = false;
                          NProgress.done();
                          _this.$notify({
                              title: '失败',
                              message: '保存失败，请联系相关研发～',
                              type: 'error'
                          });
                        }
                    });
                });
            },

        },
        mounted() {
            this.getVendorList();
        }
    }

</script>

<style lang="scss" scoped>
.el-table td>div {
    height:100px!important;
}
</style>



