<template>
    <section style="left: 185px;">
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item label="标题">
                    <el-input v-model="filters.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-tooltip content="查询" placement="top">
                    <el-button  class="el-icon-search" type="primary" v-on:click="getTags"></el-button>
                    </el-tooltip>
                    <el-tooltip content="新增" placement="top">
                    <el-button type="primary" @click="handleAdd" class="el-icon-plus"></el-button>
                    </el-tooltip>
                </el-form-item>
            </el-form>
        </el-col>
        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="30" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
        <!--列表-->
        <el-table :data="tags" highlight-current-row v-loading="listLoading" border style="font-size:12px;" align="center">
            <el-table-column  align="center" prop="name"  label="标题"  > </el-table-column>

            <el-table-column  align="center" prop="legacy_id"  label="全局ID" ></el-table-column>

            <el-table-column align="center" prop="id" label="Legacy ID" ></el-table-column>

            <el-table-column align="center" prop="weight" label="权重" ></el-table-column>

            <el-table-column label="操作"  align="center">
                <template scope="scope">
                <el-button type="primary" icon="edit"  @click="handleUpdate(scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="30" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
        <!-- 弹框 新增、编辑banner图-->
        <el-dialog :visible.sync="dialogFormVisible"  :before-close="handleCancle" :title="title">
            <el-form label-position="left"  ref="tag" :model="tag" label-width="100px" @submit.prevent="handleSubmit" style="margin:30px;width:80%;min-width:600px;" :rules="rules" border>
                <el-form-item label="全局ID" v-show="showLegacyId" prop="legacy_id">
                    <el-input :disabled="true" v-model="tag.legacy_id"></el-input>
                </el-form-item>
                <el-form-item label="标签名称" prop="name">
                    <el-input v-model="tag.name"></el-input>
                </el-form-item>
                <el-form-item label="描述" prop="desc">
                    <el-input v-model="tag.desc"></el-input>
                </el-form-item>
                <el-form-item label="权重" prop="weight">
                    <el-input-number v-model="tag.weight"></el-input-number>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" :disabled="submitVisible" v-on:click="handleSubmit">保存</el-button>
                    <el-button type="primary" v-on:click="handleCancle">取 消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </section>
</template>

<script>
    import util from '../../common/js/util'
    import NProgress from 'nprogress'
    var Resource = require('../../util/Resource');

    export default {
        data() {
            return {
                filters: {
                    name: ''
                },
                tags: [],
                total: 0,
                pageSize: 30,
                page: 1,
                listLoading: false,
                sels: [],//列表选中列
                dialogFormVisible: false,
                rules: {
                    name: [
                      {
                          required: true,
                          message: '请输入名称',
                          trigger: 'blur'
                      }
                    ],
                },
                tag: {
                    name: ""
                },
                title:"",
                showLegacyId: false,
                submitVisible: false
            }
        },
        methods: {
            handleAdd: function() {
                this.showLegacyId = false;
                this.tag = {
                    name: ""
                }
                this.title = "添加标签";
                this.dialogFormVisible = true;
                this.submitVisible = false;
            },
            handleCancle(){
                this.tag=[];
                this.dialogFormVisible = false;
                this.getTags();
            },
            handleSubmit: function() {
             this.$refs['tag'].validate((valid) => {
                  this.submitVisible = true;
                  if (valid) {
                    var _this = this;
                    var data = this.tag;
                    var method = "put";
                    if (data.id && data.id !== "") {
                        method = "post";
                    }
                    this.listLoading = true;
                    NProgress.start();
                    Resource.options({
                        method: method,
                        resource: "tag.tag",
                        data: data,
                        success: function(data) {
                            _this.listLoading = false;
                            NProgress.done();
                            _this.dialogFormVisible=false;
                            _this.getTags();
                            _this.tag = {
                                name : ""
                            }
                            _this.$notify({
                                title: '成功',
                                message: '保存成功',
                                type: 'success'
                            });
                            _this.submitVisible = false;
                        },
                        error: function(data) {
                              _this.listLoading = false;
                              var message = '保存失败，请联系相关研发～';
                              if (data.data && data.data.errmsg) {
                                  message = data.data.errmsg
                              }
                              NProgress.done();
                              _this.$notify({
                                  title: '失败',
                                  message: message,
                                  type: 'error'
                              });
                              _this.submitVisible = false;
                            }
                    });

                  } else {
                    this.submitVisible = false;
                    this.$notify({
                          title: '保存失败',
                          message: '请按照红色提示修改区域内容',
                          type: 'error'
                      });
                    return false;
                  }
                });
            },
            handleUpdate: function(row) {
                this.submitVisible = false;
                this.showLegacyId = true;
                this.title = "修改标签";
                this.tag = row;
                this.dialogFormVisible = true;
            },
            handleCurrentChange(val) {
                this.page = val;
                this.getTags();
            },
            //获取用分类列表
            getTags() {
                let para = {
                    page: this.page,
                    name: this.filters.name
                };
                this.listLoading = true;
                NProgress.start();
                var _this = this;
                Resource.get({
                    resource: "tag.tag_list",
                    data: para,
                    success: function(data) {
                        _this.total = data.total_count;
                        _this.tags = data.items;
                        _this.pageSize = data.page_size;
                        _this.listLoading = false;
                        NProgress.done();
                    },
                    error: function() {
                        _this.listLoading = false;
                        NProgress.done();
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
        },
        mounted() {
            this.getTags();
        }
    }

</script>

<style lang="scss" scoped>
.el-table td>div {
    height:100px!important;
}
</style>



