<template>
  <div class="page-navbar">
      <div style='background-color:#000;font-weight:900;text-align: center;height: 30px;'  @click="handleAdd()"><span style="color:#FFF">点击添加</span></div>
      <div class="el-upload-list el-upload-list--picture-card list-group" style="margin:0px!important;background-color:#000" v-sortable="{ onUpdate: onUpdate }">
        
        <div class="el-upload-list__item is-success app-index-custom" v-for="item in modes" style="height:auto;width:100%;margin:0px!important" :key="item.id">

           <div class="ui-content" style="background-color:#000" v-html="item.html">
           </div>
          <!---->
          <span class="el-upload-list__item-actions">
            <span class="el-upload-list__item-preview">
              <i class="el-icon-edit" @click="handleUpdate(item.id,item.type)">{{item.id}}</i>
            </span>
            <span class="el-upload-list__item-delete">
              <i class="el-icon-delete2" @click="handleDel(item.id)"></i>
            </span>
            <span class="el-upload-list__item-delete">
              <i class="el-icon-plus" @click="handleAdd()"></i>
            </span>
          </span>
        </div>
      </div>
  </div>

</template>
<script>
import { mapState, mapActions,mapGetters } from 'vuex'

var Resource = require('../util/Resource');
export default {
  name: 'app_index',
  computed: {
      ...mapState ({
        modes: state => state.modes,
      }),
   },
  data () {
    return {
       content: '',
       currentData: {},
    }
  },
  methods: {
      ...mapActions({
          setDesignCurrentData: 'setDesignCurrentData',
          setDesignCurrentType: 'setDesignCurrentType',
          setModes: 'setModes',
          delMode: 'delMode',
          setDesignDialog: 'setDesignDialog'
      }),
      onUpdate: function(event) {
          this.modes.splice(event.newIndex, 0, this.modes.splice(event.oldIndex, 1)[0]);
          var index = 0
          this.modes.forEach(function(i){
                i.index = index;
                index = index + 1
            });
          console.log(this.modes);
          this.setModes(this.modes);
      },
      handleUpdate: function(id, type) {
          this.setDesignCurrentData(this.modes.getById(id));
          this.setDesignCurrentType(type)
          this.setDesignDialog({
              dialog_name: type,
              status: true
          })
      },
      handleDel: function(id) {
          this.delMode(id)
      },
      handleAdd: function() {
          this.setDesignDialog({
              dialog_name:"selected",
              status:true});
      }
  },
  mounted() {
      
  }
}
</script>
<style lang="scss" src="../assets/main.scss"></style>


