<template>
  <div>
    <el-form label-position="left" :model="shop" label-width="100px" @submit.prevent="onSubmit" style="margin:30px;width:80%;min-width:600px;"  border >
        <el-form-item label="图片">
            <el-upload
              class="upload-demo"
              :action="upload_url"
              :on-preview="handlePreview"
              :on-remove="handlePictureRemove"
              :on-success="handleSuccess"
              :file-list="shop.picture"
              :data="picture_upload_type"
              list-type="picture">
              <el-button size="small" type="primary">点击上传</el-button>
            </el-upload>
            <!-- 图片预览弹窗 -->
          <el-dialog v-model="imageDialogVisible" size="tiny">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </el-form-item>
        <el-form-item label="是否显示" prop="is_show" style="width: 20%">
          <el-select v-model="shop.is_show" placeholder="请选择">
            <el-option
            v-for="item in data.show_types"
            :label="item.value"
            :value="item.key"
            :key="item.key">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="有效时间" prop="effective_time" style="width: 30%">
          <el-input v-model="shop.effective_time">
          <template slot="append">小时</template>
          </el-input>
        </el-form-item>
        <el-form-item label="文字分发">
            <el-button type="primary" size="small" @click="showDialogForm()">添加文字分发</el-button>

            <div class="block">
            <el-table
              :data="wordDistData"
              style="width: 100%" border>
              <el-table-column
                prop="app_type"
                label="类型" align="center" width="100" :formatter="formatType">
              </el-table-column>
              <el-table-column
                prop="deeplink"
                label="deeplink"
                width="350">
              </el-table-column>
              <el-table-column
                prop="strintent"
                label="strintent"
                width="160">
              </el-table-column>

              <el-table-column
                prop="params"
                label="参数" width="350"
                >
              </el-table-column>
              <el-table-column label="操作">
                <template scope="scope">
                    <el-button type="danger" size="small" @click="delShopWordDistribution(scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
            </div>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="onSubmit">发布</el-button>
        </el-form-item>
    </el-form>
    <div>
    <el-dialog  :title="dialogTitle" :visible.sync="dialogFormVisible">
      <WordDistribution></WordDistribution>
    </el-dialog>
    </div>
  </div>
</template>

<script>
    import { mapState, mapActions } from 'vuex'
    import WordDistribution from '../../components/shopping_cart/WordDistribution'
    import NProgress from 'nprogress'
    import GlobalConfig from '../../GlobalConfig'
    var Resource = require('../../util/Resource');
    export default {
        components : {WordDistribution},
        computed: mapState ({
            wordDistData: state => state.shopWordDistributions
        }),
        data() {
            return {
                imageDialogVisible: false,
                dialogImageUrl: "",
                id: "",
                shop: {
                  picture: [],
                  effective_time: 0,
                  is_show:0
                },
                dialogFormVisible: false,
                dialogTitle:'',
                type: '',
                form1:{
                    app_type : 1,
                },
                data:[],
                picture_upload_type: {
                    type: "new"
                },
                upload_url:GlobalConfig.uploadImageUri,
            }
        },
        methods: {
            ...mapActions({
                  delShopWordDistribution: 'delShopWordDistribution',
                  setShopWordDistributions: 'setShopWordDistributions'
              }),
            showDialogForm() {
                this.form1 = {
                    app_type: 1
                }
                this.dialogFormVisible = true;
                this.dialogTitle = '添加文字分发'
            },
            handlePreview(file) {
                this.dialogImageUrl = file.url;
                this.imageDialogVisible = true;
            },
            handleSuccess(response, file, fileList) {
                if (response.code == 200 && response.data) {
                   this.shop.picture = [response.data];
                } else {
                    this.$notify({
                        title: '失败',
                        message: '图片上传失败',
                        type: 'error'
                    });
                    this.shop.picture = [];
                }
            },
            handlePictureRemove(file, fileList) {
                this.shop.picture = [];
            },
            formatType: function(row, column) {
                return row.app_type == 0 ? 'EXT' : row.app_type == 1 ? 'INT' : '未知';
            },
            onSubmit() {
              if (this.shop.picture.length == 0) {
                 this.shop.picture = [];
              }
              if (this.wordDistData.length == 0) {
                    this.$notify({
                        title: '提示',
                        message: '添加文字分发',
                        type: 'error'
                    });
                    return false;
              }
              if(this.shop.effective_time%1 == 0){
                var _this = this;
                var data = {
                      shop: this.shop,
                      word_dists: this.wordDistData,
                      id: this.id
                    };
                Resource.post({
                    resource: "shopping_cart.shopping_cart",
                    data: data,
                    success: function(data) {
                        _this.$notify({
                            title: '发布成功',
                            message: '发布成功',
                            type: 'success'
                        });
                        window.location.href = '/#/apps/shopping_cart';
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '保存失败，请联系相关研发～',
                            type: 'error'
                        });
                    }
                });
              }else{
                this.$notify({
                    title: '错误',
                    message: '有效时间请输入整数',
                    type: 'error'
                });
                focus(this.shop.effective_time);
              }

            },
        },
        mounted() {
            this.listLoading = true;
            NProgress.start();
            var _this = this;
            Resource.get({
                resource: "shopping_cart.shopping_cart",
                success: function(data) {
                    _this.data = data;
                    if ( _this.data['item']['word_dists']) {
                        _this.setShopWordDistributions(_this.data['item']['word_dists']);
                    }
                    _this.shop.effective_time = data['item']['effective_time']
                    _this.shop.is_show = data['item']['is_show']
                    if(data['item']['url']){
                      var url = data['item']['url']
                      url.forEach(function(i){
                          var arr = i.split("/");
                          _this.shop.picture.push({
                              name: arr[arr.length-1],
                              url: i
                          });
                      });
                    }

                    _this.listLoading = false;
                    NProgress.done();
                },
                error: function() {
                    _this.listLoading = false;
                    NProgress.done();
                    _this.$notify({
                        title: '失败',
                        message: '请求失败',
                        type: 'error'
                    });
                }
            });
        }
    }

</script>



