var isProduction = process.env.NODE_ENV === 'production';
let base = 'http://localhost:8008';
let uploadType = "new"
if (isProduction) {
    base = "";
    uploadType = "old";
}
var uploadImageUri = base + "/rest/account/api/upload";

var fonts = [{
        "key": "fz-pangtyjt", value: "方正胖头鱼简体" 
    },
    {
        "key": "fzyh-bz", value: "方正悠黑标准" 
    },
    {
        "key": "fzltkh-gbk", value: "方正兰亭刊黑_GBK" 
    },
    {
        "key": "fzyh-zc", value: "方正兰亭中黑" 
    },
    // {
    //     "key": "fzyh-zc", value: "方正悠黑中粗" 
    // }
    ]

var mifgUrl = "mifg://fashiongallery/player_preview?typeofid=image&"
var golbal_config = {
    base: base,
    uploadImageUri : uploadImageUri,
    isProduction: isProduction,
    uploadType: uploadType,
    fonts: fonts,
    mifgUrl: mifgUrl
}


export default golbal_config


