<template>
<div>
  <ul class="el-upload-list el-upload-list--picture-card list-group" v-sortable="{ onUpdate: onUpdate }">
    <li class="el-upload-list__item is-success" v-for="item in sels" style="height:228px;width:137px" :key="item.id">
      <img :src="item.icon" alt="" class="el-upload-list__item-thumbnail">
      <a class="el-upload-list__item-name">
        <i class="el-icon-document"></i>{{item.title}}</a>
      <label class="el-upload-list__item-status-label">
        <i class="el-icon-upload-success el-icon-check"></i>
      </label>
      <i class="el-icon-close"></i>
      <!---->
      <span class="el-upload-list__item-actions">
        <span class="el-upload-list__item-preview">
          <i class="el-icon-view" @click="handlePictureCardPreview(item.id,item.type)"></i>
        </span>
        <span class="el-upload-list__item-delete">
          <i class="el-icon-delete2" @click="handlePictureCardDel(item)"></i>
        </span>
      </span>
    </li>
  </ul>
  <div class="el-upload el-upload--picture-card" style="height:228px;width:137px;line-height: 220px" @click="handleGetPictureList">
    <i class="el-icon-plus"></i>
  </div>
  <div>
  <!-- 图片列表弹窗 -->
  <el-dialog size="large" v-model="dialogPictureListVisible">
    <PictureList></PictureList>
  </el-dialog>

  <!-- 图片预览弹窗 -->
  <el-dialog v-model="dialogVisible" size="tiny">
    <img width="100%" :src="dialogImageUrl" alt="">
  </el-dialog>
</div>
</div>
</template>
<script>
    import { mapState, mapActions } from 'vuex';
    import NProgress from 'nprogress'
    import PictureList from './CommonPictureList'
    var Resource = require('../../util/Resource');
    export default {
        components : {PictureList},
        computed: mapState ({
            sels: state => state.sels,
            type: state => state.type
        }),
        data() {
            return {
                dialogPictureListVisible:false,
                dialogVisible: false,
                dialogImageUrl: ""
            }
        },
        methods: {
            ...mapActions({
                handlePictureCardDel: 'clickCommonPic',
                setType: 'setType',
                setSels: 'setSels',
            }),
            handlePictureCardPreview(id, type) {
              if(type==0){
                window.open('/#/apps/edit_picture?id='+id , '_blank');
              }else{
                window.open('/#/apps/edit_album?id='+id , '_blank');
              }
            },
            onUpdate: function(event) {
              this.sels.splice(event.newIndex, 0, this.sels.splice(event.oldIndex, 1)[0]);
            },
            handleGetPictureList(){
              this.dialogPictureListVisible = true;
              this.setType(this.type);
            }
        },
        mounted() {
            this.setSels([]);
        }
    }
</script>



