<template>
    <section style="left: 185px;">
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <!-- <el-form-item label="下架时间">
                    <el-input v-model="filters.date" style="width: 100px;"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getForceOfflineList">查询</el-button>
                </el-form-item> -->
                <el-form-item>
                    <el-button type="primary" @click="handleAdd" class="el-icon-plus"></el-button>
                </el-form-item>
            </el-form>
        </el-col>

        <!--列表-->
        <el-table :data="force_offlines" highlight-current-row v-loading="listLoading" border style="font-size:12px;" align="center">
            <el-table-column prop="date"  label="推荐日期" min-width="10" align="center">
                <template scope="scope">
                    <a :href="'#/apps/edit_force_offline?id='+scope.row.id" target="_blank">
                       {{scope.row.date}}
                    </a>
                </template>
            </el-table-column>

            <el-table-column prop="status"  label="发布状态" min-width="10" align="center" :formatter="formatBoolean"> </el-table-column>
            <el-table-column prop="publish_date"  label="发布日期" min-width="10" align="center" > </el-table-column>
        </el-table>
    </section>
</template>

<script>
    import util from '../../common/js/util'
    import NProgress from 'nprogress'
    var Resource = require('../../util/Resource');

    export default {
        data() {
            return {
                filters: {
                    date: ''
                },
                data: {},
                force_offlines: [],
                force_offline: {
                    date: '',
                },
                total: 0,
                pageSize: 30,
                page: 1,
                listLoading: false,
                sels: [],//列表选中列
                actions:
                    [{
                      value: 'toforceoffline',
                      label: '实时发布',
                    },
                    ],
                icon_data: {
                    type: "new"
                },
                rules: {
                    name: [
                      {
                          required: true,
                          message: '请输入名称',
                          trigger: 'blur'
                      }
                    ],

                }

            }
        },
        methods: {
            formatBoolean: function(row, column) {
                return row.status == true ? '是' : row.status == false ? '否' : '未知';
            },
            handleAdd(){
                window.location.href = '/#/apps/edit_force_offline';
            },
            handleSuccess(response, file, fileList) {
                if (response.code == 200 && response.data) {
                   console.log(response.data);
                   this.cp.infos = [response.data];
                } else {
                    this.$notify({
                        title: '失败',
                        message: '图片上传失败',
                        type: 'error'
                    });
                }
            },
            toForceOffline: function() {
                var _this = this;
                Resource.post({
                    resource: "force_offline.force_offline",
                    data: {
                        rows: this.sels,
                        action: "to_force_offline"
                    },
                    success: function(data) {
                        _this.getForceOfflineList();
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            //获取每日推荐列表
            getForceOfflineList() {
                let para = {
                    page: this.page,
                    // date: this.filters.date,
                    // id: this.filters.id
                };
                this.listLoading = true;
                NProgress.start();
                var _this = this;
                Resource.get({
                    resource: "force_offline.force_offline_list",
                    data: para,
                    success: function(data) {
                        _this.total = data.total_count;
                        _this.force_offlines = data.items;
                        _this.pageSize = data.page_size;
                        _this.listLoading = false;
                        NProgress.done();
                    },
                    error: function() {
                        _this.listLoading = false;
                        NProgress.done();
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                });
            },

            removeUser: function(para){
                Resource.delete({
                    resource: "cp.cp",
                    data: para,
                    success: function(data) {
                        return true;
                    },
                    error: function() {
                        return false;
                    }
                })
            },

            // onSubmit(formName) {
            //     this.$refs[formName].validate((valid) => {
            //       if (valid) {
            //         var _this = this;
            //         var data = {
            //             force_offline: this.force_offline,
            //             id: this.force_offline.id
            //         };
            //         this.listLoading = true;
            //         NProgress.start();
            //         var method = "put";
            //         if (this.force_offline.id){
            //             method = "post";
            //         }
            //         Resource.options({
            //             method: method,
            //             resource: "force_offline.force_offline",
            //             data: data,
            //             success: function(data) {
            //               _this.listLoading = false;
            //               NProgress.done();
            //               _this.getForceOfflineList();
            //               _this.force_offline.date='';
            //               _this.force_offline.id='';
            //               _this.$notify({
            //                 title: '成功',
            //                 message: '保存成功',
            //                 type: 'success'
            //               });
            //               this.banner='';
            //             },
            //             error: function() {
            //               _this.listLoading = false;
            //               NProgress.done();
            //               _this.$notify({
            //                   title: '失败',
            //                   message: '保存失败，请联系相关研发～',
            //                   type: 'error'
            //               });
            //             }
            //         });
            //       } else {
            //         this.$notify({
            //               title: '保存失败',
            //               message: '请按照红色提示修改区域内容',
            //               type: 'error'
            //           });
            //         return false;
            //       }
            //     });
            // },

        },
        mounted() {
            this.getForceOfflineList();
        }
    }

</script>





