 <template>
 <div>
  <el-form ref="form1" label-width="80px" :model="form1"  label-position="left" :rules="form1Rules">
    <el-form-item label="类型" prop="app_type">
      <el-select v-model="form1.app_type" placeholder="请选择应用类型">
        <el-option
        v-for="item in data.app_types"
        :label="item.value"
        :value="item.key"
        :key="item.key">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="deeplink" prop="deeplink">
      <el-input v-model="form1.deeplink"></el-input>
    </el-form-item>
    <el-form-item label="strintent">
      <el-input v-model="form1.strintent"></el-input>
    </el-form-item>
    <el-form-item label="参数" >
      <el-input type="textarea" v-model="form1.params" placeholder="请输入json格式的参数"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="addDist()">确 定</el-button>
    </el-form-item>
  </el-form>

</div>
</template>

<script>
    import { mapState, mapActions } from 'vuex';
    import NProgress from 'nprogress'
    var Resource = require('../../util/Resource');
    export default {
        computed: mapState ({
            wordDistData: state => state.shopWordDistributions
        }),
        data() {
            return {
                data: {},
                form1:{
                    app_type : 1,
                },
                form1Rules: {
                    app_type: [
                      {
                          required: true,
                          message: '请选择应用类型',
                      }
                    ]
                }
            }
        },
        methods: {
            ...mapActions({
                addShopWordDistrion: 'addShopWordDistribution',
            }),
            resetForm(){
                this.form1 = {
                    app_type : 1,
                    deeplink: '',
                    strintent: '',
                    params: '',
                }
            },
            addDist(){
                this.$refs['form1'].validate((valid) => {
                if (valid) {
                    NProgress.start();
                    var wordDistId = -1;
                    for (var i = 0; i < this.wordDistData.length; i++) {
                        if (this.wordDistData[i].id >= wordDistId) {
                            wordDistId = this.wordDistData[i].id;
                        }
                    }
                    wordDistId = wordDistId + 1;
                    this.addShopWordDistrion({
                        id: wordDistId,
                        strintent: this.form1.strintent,
                        deeplink: this.form1.deeplink,
                        app_type: this.form1.app_type,
                        params: this.form1.params,
                    });
                    this.$notify({
                        title: '添加成功',
                        message: '添加成功',
                        type: 'success'
                    });
                    this.resetForm();
                    this.$parent.$parent.dialogFormVisible=false;
                  } else {
                    this.$notify({
                        title: '添加失败',
                        message: '请按照红色提示修改区域内容',
                        type: 'error'
                    });
                    return false;
                  }
                });
            },
        },
        mounted() {
            var _this = this;
            Resource.get({
                resource: "metax.app_type_list",
                data: {
                },
                success: function(data) {
                    _this.data = data;
                },
                error: function() {
                    _this.$notify({
                        title: '失败',
                        message: '请求失败',
                        type: 'error'
                    });
                }
            });
        }
    }
</script>



