<template>
    <section style="left: 185px;">
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item label="ID">
                    <el-input v-model="filters.id" style="width: 110px;"></el-input>
                </el-form-item>

                <el-form-item label="名称">
                    <el-input v-model="filters.name" style="width: 100px;"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-tooltip content="查询" placement="top">
                    <el-button type="primary" v-on:click="getCPList" class="el-icon-search"></el-button>
                    </el-tooltip>
                    <el-tooltip content="新增" placement="top">
                    <el-button type="primary" @click="handleAdd" class="el-icon-plus"></el-button>
                    </el-tooltip>
                </el-form-item>
            </el-form>
        </el-col>
        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="30" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
        <!--列表-->
        <el-table :data="cps" highlight-current-row v-loading="listLoading" @selection-change="selsChange" border style="font-size:12px; ">
            <el-table-column label="封面" width="150" align="center">
              <template scope="scope">
                <a :href="scope.row.icon_link" target="_blank">
                    <img :src="scope.row.icon_link" style="height: 150px;">
                </a>
              </template>
            </el-table-column>
            <el-table-column prop="global_id" label="全局ID" min-width="10" align="center">
            </el-table-column>

            <el-table-column prop="name"  label="名称" min-width="10" align="center">
            </el-table-column>

            <el-table-column label="操作" width="80" align="center">
                <template scope="scope">
                <el-tooltip content="编辑" placement="top">
                    <el-button class="el-icon-edit" type="primary" size="small" v-on:click="handleUpdate(scope.row.id)"></el-button>
                </el-tooltip>
                </template>
            </el-table-column>
        </el-table>

        <!-- 弹框 新增、编辑cp图-->
        <el-dialog :visible.sync="dialogFormVisible" :before-close="handleCancle" >
            <el-form label-position="left"  ref="cp" :model="cp" label-width="100px" @submit.prevent="onSubmit" style="margin:30px;width:80%;min-width:600px;" :rules="rules" border>

                <el-form-item label="名称" prop="name">
                    <el-input v-model="cp.name"></el-input>
                </el-form-item>

                <el-form-item label="图片" width="100">
                    <el-upload
                      class="upload-demo"
                      :action="upload_url"
                      :on-preview="handlePreview"
                      :on-remove="handleRemove"
                      :on-success="handleSuccess"
                      :file-list="cp.infos"
                      :data="icon_data"
                      list-type="picture">
                      <el-button size="small" type="primary">点击上传</el-button>
                    </el-upload>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" v-on:click="onSubmit('cp')">保存</el-button>
                    <el-button v-on:click="handleCancle">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="30" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
    </section>
</template>

<script>
    import util from '../../common/js/util'
    import NProgress from 'nprogress'
    import GlobalConfig from '../../GlobalConfig'
    var Resource = require('../../util/Resource');

    export default {
        data() {
            return {
                upload_url:GlobalConfig.uploadImageUri,
                filters: {
                    id: '',
                    name: ''
                },
                data: {},
                cps: [],
                cp: {
                    name: '',
                    desc: '',
                    infos: []  // {'name':x, 'url':y} 固定格式
                },
                total: 0,
                pageSize: 30,
                page: 1,
                listLoading: false,
                dialogFormVisible: false,
                sels: [],//列表选中列
                icon_data: {
                    type: "new"
                },
                rules: {
                    name: [
                      {
                          required: true,
                          message: '请输入名称',
                          trigger: 'blur'
                      }
                    ],

                }

            }
        },
        methods: {
            formatBoolean: function(row, column) {
                return row.isready == true ? '是' : row.isready == false ? '否' : '未知';
            },
            formatAlbums: function(row, column) {
                return row.albums.join(",");
            },
            handleAdd(){
                this.dialogFormVisible = true
            },
            handleRemove(file, fileList) {
            },
            handlePreview(file) {
            },
            handleSuccess(response, file, fileList) {
                if (response.code == 200 && response.data) {
                   this.cp.infos = [response.data];
                } else {
                    this.$notify({
                        title: '失败',
                        message: '图片上传失败',
                        type: 'error'
                    });
                }
            },
            handleCancle(){
                this.dialogFormVisible = false;
            },


            handleUpdate(id){
                var data={
                    id:id
                }
                var _this = this;
                Resource.get({
                    resource: "cp.cp",
                    data:data,
                    success: function(data) {
                        _this.data = data;
                        var infos = [];
                        infos.push({
                            name: data.cp.name,
                            url: data.cp.icon_link
                        })
                        _this.cp = {
                            name: data.cp.name,
                            infos: infos,
                            id: id
                        };
                        _this.dialogFormVisible = true;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                });

            },

            handleCurrentChange(val) {
                this.page = val;
                this.getCPList();
            },
            selsChange(val) {
                this.sels = val;
            },

            //获取用分类列表
            getCPList() {
                let para = {
                    page: this.page,
                    name: this.filters.name,
                    id: this.filters.id
                };
                this.listLoading = true;
                NProgress.start();
                var _this = this;
                Resource.get({
                    resource: "cp.cp_list",
                    data: para,
                    success: function(data) {
                        _this.total = data.total_count;
                        _this.cps = data.items;
                        _this.pageSize = data.page_size;
                        _this.listLoading = false;
                        NProgress.done();
                    },
                    error: function() {
                        _this.listLoading = false;
                        NProgress.done();
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },

            removeUser: function(para){
                Resource.delete({
                    resource: "cp.cp",
                    data: para,
                    success: function(data) {
                        return true;
                    },
                    error: function() {
                        return false;
                    }
                })
            },

            //删除
            handleDel: function (index, row) {
                this.$confirm('确认删除该记录吗?', '提示', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    NProgress.start();
                    let para = { id: row.id };
                    removeUser(para).then((res) => {
                        this.listLoading = false;
                        NProgress.done();
                        this.$notify({
                            title: '成功',
                            message: '删除成功',
                            type: 'success'
                        });
                        this.getCPList();
                    });
                }).catch(() => {

                });
            },

            //批量删除
            batchRemove: function () {
                var ids = this.sels.map(item => item.id).toString();
                this.$confirm('确认删除选中记录吗？', '提示', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    NProgress.start();
                    let para = { ids: ids };
                    batchRemoveUser(para).then((res) => {
                        this.listLoading = false;
                        NProgress.done();
                        this.$notify({
                            title: '成功',
                            message: '删除成功',
                            type: 'success'
                        });
                        this.getCPList();
                    });
                }).catch(() => {

                });
            },

            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                  if (valid) {
                    var _this = this;
                    var data = {
                        cp: this.cp,
                        id: this.cp.id
                    };
                    this.listLoading = true;
                    NProgress.start();
                    var method = "put";
                    if (this.cp.id){
                        method = "post";
                    }
                    Resource.options({
                        method: method,
                        resource: "cp.cp",
                        data: data,
                        success: function(data) {
                          _this.listLoading = false;
                          NProgress.done();
                          _this.dialogFormVisible=false;
                          _this.getCPList();
                          _this.cp.name='';
                          _this.cp.infos=[];
                          _this.cp.id='';
                          _this.$notify({
                            title: '成功',
                            message: '保存成功',
                            type: 'success'
                          });
                          this.banner='';
                        },
                        error: function() {
                          _this.listLoading = false;
                          NProgress.done();
                          _this.$notify({
                              title: '失败',
                              message: '保存失败，请联系相关研发～',
                              type: 'error'
                          });
                        }
                    });
                  } else {
                    this.$notify({
                          title: '保存失败',
                          message: '请按照红色提示修改区域内容',
                          type: 'error'
                      });
                    return false;
                  }
                });
            },

        },
        mounted() {
            this.getCPList();
        }
    }

</script>

<style lang="scss" scoped>
.el-table td>div {
    height:100px!important;
}
</style>



