<template>
  <div>
    <el-form label-position="left"  ref="picture" :model="picture" label-width="100px" @submit.prevent="onSubmit" style="margin:30px;width:80%;min-width:600px;" :rules="rules" border>

        <el-form-item label="标题" prop="title">
            <el-input v-model="picture.title"></el-input>
        </el-form-item>

        <el-form-item label="描述" prop="desc">
            <el-input type="textarea" v-model="picture.desc"></el-input>
        </el-form-item>
        <el-form-item label="分类" prop="categories" style="width:100%;">
            <el-checkbox-group v-model="picture.categories">
                <el-checkbox v-for="filter in data.items" :label="filter.id" :key="filter.id">{{filter.name}}</el-checkbox>
            </el-checkbox-group>
        </el-form-item>
        <el-form-item label="品牌" prop="brand" style="margin-bottom: 10px;">
          <el-select
              v-model="picture.brand"
              filterable
              remote
              placeholder="请选择品牌"
              :remote-method="getBrands"
              >
              <el-option
                v-for="item in brands"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="图片" prop="images">
             <PictureWall></PictureWall>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" :disabled="submitVisible" @click="onSubmit('picture')" v-if="picture.status === 1">审核通过</el-button>
        </el-form-item>
    </el-form>
    <div>
    </div>
  </div>
</template>

<script>
    import { mapState, mapActions } from 'vuex'
    import NProgress from 'nprogress'
    import PictureWall from '../../components/cp/PictureWall'
    var Resource = require('../../util/Resource');
    export default {
        components : {PictureWall},
        computed: mapState ({
            sels: state => state.sels,
        }),
        data() {
            return {
                id:"",
                data: {
                },
                status: -1,
                picture: {
                    title: '',
                    desc: '',
                    categories: [],
                    pictures:[],
                    brand: '',
                },
                brands: '',
                loading: false,
                submitVisible: false,
                rules: {
                    title: [
                      {
                          required: true,
                          message: '请输入标题',
                          trigger: 'blur'
                      }
                    ],

                    desc: [
                      {
                          required: true,
                          message: '请输入描述',
                          trigger: 'blur'
                      }
                    ],
                    categories: [
                      {
                          type: 'array',
                          required: true,
                          message: '请至少选择一个分类',
                          trigger: 'blur'
                      }
                    ],
                    brand: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请选择品牌',
                          trigger: 'blur'
                      }
                    ],
                },
            }
        },
        methods: {
            ...mapActions({
                setSels: 'setSels',
            }),
            onSubmit(formName) {
              this.$refs[formName].validate((valid) => {
                  // this.submitVisible = true;
                  var n=0
                  this.sels.forEach(function(i){
                        if(i.status!=3){
                          n=n+1
                        }
                    });
                  if (n < 3) {
                      this.$notify({
                          title: '失败',
                          message: '图片数量少于3张',
                          type: 'error'
                      });
                      this.submitVisible = false;
                      return false;
                  }
                  if (valid) {

                      if (this.sels) {
                        for(var i=0; i<this.sels.length; i++) {
                            if(this.sels[i].status!=3 && !this.sels[i].picture) {
                                var n = i +1
                                this.$message.error('第'+n+'张图片尚未编辑，不能审核通过');
                                this.submitVisible = false;
                                return false;
                            }
                        }

                      }
                      this.picture.id = this.id;
                      this.picture.type = 'pass';
                      this.picture.status = '2';
                      this.picture.pictures = this.sels;
                      console.log('===picture====',this.picture)
                      this.API_UTIL.API_PHONE.setAlbum(this.picture).then(data => {
                             if (data.code == 200) {
                                this.listLoading = false;
                                this.$notify({
                                    title: '成功',
                                    message: '执行成功',
                                    type: 'success'
                                });
                                // this.submitVisible = true;
                                window.location.href = '/#/apps/cp_check';
                            } else {
                                if(data.data.msg){
                                  this.$notify({
                                    title: '失败',
                                    message: data.data.msg,
                                    type: 'error'
                                  });
                              }else{
                                  this.$notify({
                                    title: '失败',
                                    message: '后台错误',
                                    type: 'error'
                                  });
                              }
                            }
                      })
                  }


              })
            },
            formatAlbum(picture) {
                var _this = this;
                var pictures = [];
                var pasing_pictures = [];
                if (picture) {
                    picture.pictures.forEach(function(i){
                        pictures.push(_this.formatePicture(i));
                        if(i.status==1||i.status==2){
                          pasing_pictures.push(_this.formatePicture(i))
                        }
                    });
                }
                if(picture.brand_id){
                    this.getBrand(picture.brand_id)
                }
                this.picture = {
                      title: picture.title,
                      desc: picture.desc,
                      categories: picture.category_ids,
                      pictures: pictures,
                      status: picture.status,
                      origin_type: picture.origin_type,
                      cp_id: picture.cp_id,
                      id: picture.id,
                      brand: picture.brand_id
                };
                if(pictures) {
                  this.setSels(pictures);
                }
                if(pasing_pictures.length<3){
                  this.$message.error('待审核图片数量少于3张');
                }
            },
            formatePicture(picture) {
                var images = []
                if (picture) {
                    var arr = picture.url.split("/");
                    images.push({
                            name: arr[arr.length-1],
                            url: picture.url,
                            md5: picture.md5,
                    });
                  }

                return {
                    short_title: picture.short_title,
                    short_desc: picture.short_desc,
                    title: picture.title,
                    desc: picture.desc,
                    id: picture.id,
                    md5: picture.md5,
                    url: picture.url,
                    deeplink: picture.deeplink,
                    tag: picture.tag,
                    image: images[0],
                    status: picture.status
                }
            },
            getBrands(value) {
                //search brands
                var _this = this;
                Resource.get({
                    resource: "brand.search_brand",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.brands = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            getBrand(id) {
                //search brands
                var _this = this;
                Resource.get({
                    resource: "brand.find_brand",
                    data: {
                      id: id
                    },
                    success: function(data) {
                        _this.brands = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
        },
        mounted() {
            var picture_id = this.$route.query.id||"";
            this.id = picture_id
            this.listLoading = true;
            // 查询启用状态的分类
            var params = {status:1}
            this.API_UTIL.API_PHONE.getCategories(params).then(data => {
                this.data = data.data;
              }
            );
            if(this.id){
                var params = {album_id:this.id}
                this.API_UTIL.API_PHONE.getAlbum(params).then(data => {
                  this.formatAlbum(data.data.picture);
                });
            }else{
                this.picture = {
                    title: '',
                    brand: '',
                    desc: '',
                    categories: [],
                    pictures:[]
                };
                this.setSels([])
            }
        }
    }

</script>



