import axios from 'axios';
import GlobalConfig from '../GlobalConfig'
var querystring = require('querystring');
// let base = GlobalConfig.base+'/notebook';
let base = GlobalConfig.base;

export const getNotebookCategorys = params => {
    return axios.get(`${base}/rest/notebook/api/category_list`,  {params: params}).then(res => res.data);
};

export const setNotebookCategory = params => {
    var method = params.method
    if (method=='post') {
        return axios.post(`${base}/rest/notebook/api/category`, JSON.stringify(params)).then(res => res.data);
    } else {
        return axios.put(`${base}/rest/notebook/api/category`, JSON.stringify(params)).then(res => res.data);
    }
};

// 更新分类的顺序和状态
export const updateCategoryIndexStatus = params => {
    var resource = params.resource
    if (resource=='notebook.category_index') {
        return axios.post(`${base}/rest/notebook/api/category_index`, JSON.stringify(params)).then(res => res.data);
    } else {
        return axios.post(`${base}/rest/notebook/api/category_status`, JSON.stringify(params)).then(res => res.data);
    }
};

export const getCategory = params => {
    return axios.get(`${base}/rest/notebook/api/category`,  {params: params}).then(res => res.data);
};

export const getCategorySearch = params => {
    return axios.get(`${base}/rest/notebook/api/category_search`).then(res => res.data);
};

export const getCreators = params => {
    return axios.get(`${base}/rest/notebook/api/creators`,  {params: params}).then(res => res.data);
};

export const getCPs = params => {
    return axios.get(`${base}/rest/cp/api/search_cp`,  {params: params}).then(res => res.data);
};

export const changeStatus = params => {
    return axios.post(`${base}/rest/notebook/api/status`, JSON.stringify(params)).then(res => res.data);
};

export const toDailyRecommend = params => {
    return axios.put(`${base}/rest/notebook/api/daily_recommend`, JSON.stringify(params)).then(res => res.data);
};

export const getAlbumSearch = params => {
    return axios.get(`${base}/rest/notebook/api/album_search`).then(res => res.data);
};

export const getAlbumList = params => {
    return axios.get(`${base}/rest/notebook/api/album_list`,  {params: params}).then(res => res.data);
};

export const getAlbumDetail = params => {
    return axios.get(`${base}/rest/notebook/api/album`,  {params: params}).then(res => res.data);
};

export const postAlbum = params => {
    return axios.post(`${base}/rest/notebook/api/album`, JSON.stringify(params)).then(res => res.data);
};

export const putAlbum = params => {
    return axios.put(`${base}/rest/notebook/api/album`, JSON.stringify(params)).then(res => res.data);
};

export const getPictureSet = params => {
    return axios.get(`${base}/rest/notebook/api/picture_set`).then(res => res.data);
};
