 <template>
 <div>
  <el-form ref="form1" label-width="80px" :model="form1"  label-position="left" :rules="form1Rules">
    <el-form-item label="跳转应用" prop="app_type">
      <el-select v-model="form1.app_type" placeholder="请选择应用类型" @change="onSelectedAppType">
        <el-option
        v-for="item in data.app_types"
        :label="item.name"
        :value="item.id"
        :key="item.id">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="deeplink" v-show="deeplinkShow" prop="deeplink">
      <el-input v-model="form1.deeplink"></el-input>
    </el-form-item>
    <el-form-item label="包名" v-show="packShow" prop="packagename">
      <el-input v-model="form1.packagename"></el-input>
    </el-form-item>
    <el-form-item label="strintent">
      <el-input v-model="form1.strintent"></el-input>
    </el-form-item>
    <el-form-item label="参数">
      <el-input type="textarea" v-model="form1.params"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="addDist()">确 定</el-button>
    </el-form-item>
  </el-form>
    
</div>
</template>

<script>
    import { mapState, mapActions } from 'vuex';
    import NProgress from 'nprogress'
    var Resource = require('../../util/Resource');
    export default {
        computed: mapState ({
            wordDistData: state => state.wordDistributions
        }),
        data() {
            return {
                data: {
                  
                },
                form1:{
                    app_type : '',
                    packagename: ''
                },
                packShow: false,
                deeplinkShow: true,
                form1Rules: {
                    app_type: [
                      {   
                          type: 'integer', 
                          required: true, 
                          message: '请选择应用类型', 
                          trigger: 'change' 
                      }
                    ]
                }
            }
        },
        methods: {
            ...mapActions({
                addWordDistrion: 'addWordDistribution',
                closeDialog: 'closeWordDistributionDialog'
            }),
            resetForm(){
                this.form1 = {
                    app_type : '',
                    packagename: '' ,
                    deeplink: '',
                    strintent: '',
                    params: '',
                    is_automatic: 0
                }
            },
            onSelectedAppType(value) {
                console.log(this.data.app_id2app, value);
                if (value === "") {
                  return;
                }
                var is_automatic = this.data.app_id2app[parseInt(value)].is_automatic;
                console.log(is_automatic);
                this.form1.app_type = value;
                if (is_automatic == 1) {
                    this.deeplinkShow = false;
                    this.packShow = true;
                    this.form1.deeplink = "";
                    this.form1.is_automatic = is_automatic;
                } else {
                    this.deeplinkShow = true;
                    this.packShow = false;
                    this.form1.packagename = "";
                    this.form1.is_automatic = is_automatic;
                }
            },

            addDist(){
                this.$refs['form1'].validate((valid) => {
                if (valid) {
                    //如果是需要后台生成下载地址的类型发起向后台请求
                    NProgress.start();
                    var packagename_deeplink = "";
                    var is_automatic = this.data.app_id2app[this.form1.app_type].is_automatic;
                    var _this = this;
                    if (is_automatic == 1) {
                        Resource.post({
                            resource: "account.app_store_link",
                            data: {
                              packagename: this.form1.packagename
                            },
                            async: false,
                            success: function(data) {
                                _this.form1.deeplink = data.url;
                                NProgress.done();
                            },  
                            error: function() {
                                NProgress.done();
                                _this.$notify({
                                    title: '失败',
                                    message: '请求失败',
                                    type: 'error'
                                });
                            }
                        });
                    }
                    if (is_automatic == 1 && _this.form1.deeplink == "")  {
                        this.$notify({
                              title: '失败',
                              message: '请求失败',
                              type: 'error'
                        });
                        return;
                    }
                    var wordDistId = -1;
                    for (var i = 0; i < this.wordDistData.length; i++) {
                        if (this.wordDistData[i].id >= wordDistId) {
                            wordDistId = this.wordDistData[i].id;
                        }
                    }
                    wordDistId = wordDistId + 1;
                    this.addWordDistrion({
                        id: wordDistId,
                        strintent: this.form1.strintent,
                        deeplink: this.form1.deeplink,
                        app_id: this.form1.app_type,
                        app_name: this.data.app_id2app[this.form1.app_type].name,
                        params: this.form1.params,
                        packagename: this.form1.packagename,
                        is_automatic: is_automatic
                    });
                    this.$notify({
                        title: '添加成功',
                        message: '添加成功',
                        type: 'success'
                    });
                    this.resetForm();
                  } else {
                    this.$notify({
                        title: '添加失败',
                        message: '请按照红色提示修改区域内容',
                        type: 'error'
                    });
                    return false;
                  }
                });
            },
        },
        mounted() {
            var picture_id = this.$route.query.id||"";
            this.id = picture_id
            this.listLoading = true;
            NProgress.start();
            var _this = this;
            Resource.get({
                resource: "metax.app_jump_list",
                data: {
                },
                success: function(data) {
                    _this.data = data;
                    _this.listLoading = false;
                    NProgress.done();
                },  
                error: function() {
                    _this.listLoading = false;
                    NProgress.done();
                    _this.$notify({
                        title: '失败',
                        message: '请求失败',
                        type: 'error'
                    });
                }
            });
        }
    }
</script>


