<template>
    <section style="center: 185px;">
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item label="推荐时间" prop="date">
                    <el-date-picker v-model="force_recommend.date" type="date" format="yyyy-MM-dd" placeholder="选择时间" style="width: 300px;" @change="formatDate">
                    </el-date-picker>
                </el-form-item>
                <el-form-item>
                    <el-tooltip content="查询" placement="top">
                    <el-button class="el-icon-search"  type="primary" v-on:click="getForceRecommendList"></el-button>
                    </el-tooltip>
                    <el-tooltip content="新增" placement="top">
                    <el-button type="primary" @click="handleAdd" class="el-icon-plus"></el-button>
                    </el-tooltip>
                </el-form-item>
            </el-form>
        </el-col>


        <!--列表-->
        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="30" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
        <el-table :data="force_recommends" highlight-current-row v-loading="listLoading" border style="font-size:12px; ">
            <el-table-column prop="date"  label="推荐日期" min-width="10" align="center">
                <template scope="scope">
                    <a :href="'#/apps/edit_force_recommend?id='+scope.row.id" target="_blank">
                       {{scope.row.date}}
                    </a>
                </template>
            </el-table-column>

            <el-table-column prop="recommend_counts" label="推荐个数" min-width="10" align="center"></el-table-column>
            <el-table-column prop="status"  label="发布状态" min-width="10" align="center" :formatter="formatBoolean"> </el-table-column>
            <el-table-column prop="publish_date"  label="发布日期" min-width="10" align="center" > </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="30" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
    </section>
</template>

<script>
    import util from '../../common/js/util'
    import NProgress from 'nprogress'
    var Resource = require('../../util/Resource');

    export default {
        data() {
            return {
                filters: {
                    date: ''
                },
                data: {},
                force_recommends: [],
                force_recommend: {
                    date: '',
                },
                total: 0,
                pageSize: 30,
                page: 1,
                listLoading: false,
                dialogFormVisible: false,
                sels: [],//列表选中列
                actions:
                    [{
                      value: 'toforcerecommend',
                      label: '实时发布',
                    },
                    ],
                icon_data: {
                    type: "new"
                },
                rules: {
                    name: [
                      {
                          required: true,
                          message: '请输入名称',
                          trigger: 'blur'
                      }
                    ],

                }

            }
        },
        methods: {
            formatBoolean: function(row, column) {
                return row.status == true ? '是' : row.status == false ? '否' : '未知';
            },
            handleAdd(){
                window.location.href = '/#/apps/edit_force_recommend';
            },
            formatDate(date) {
              this.filters.date = date;
            },
            handleUpdate(id){
                var data={
                    id:id
                }
                var _this = this;
                Resource.get({
                    resource: "force_recommend.force_recommend",
                    data:data,
                    success: function(data) {
                        _this.data = data;
                        _this.force_recommend = {
                            id: data.id,
                            date: data.date,
                        };
                        _this.dialogFormVisible = true;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                });

            },
            toForceRecommend: function() {
                var _this = this;
                Resource.post({
                    resource: "force_recommend.force_recommend",
                    data: {
                        rows: this.sels,
                        action: "to_force_recommend"
                    },
                    success: function(data) {
                        _this.getForceRecommendList();
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            handleCurrentChange(val) {
                this.page = val;
                this.getForceRecommendList();
            },
            selsChange(val) {
                this.sels = val;
            },
            //获取每日推荐列表
            getForceRecommendList() {
                let para = {
                    page: this.page,
                    date: this.filters.date,
                    id: this.filters.id
                };
                this.listLoading = true;
                NProgress.start();
                var _this = this;
                Resource.get({
                    resource: "force_recommend.force_recommend_list",
                    data: para,
                    success: function(data) {
                        _this.total = data.total_count;
                        _this.force_recommends = data.items;
                        _this.pageSize = data.page_size;
                        _this.listLoading = false;
                        NProgress.done();
                    },
                    error: function() {
                        _this.listLoading = false;
                        NProgress.done();
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                });
            },
            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                  if (valid) {
                    var _this = this;
                    var data = {
                        force_recommend: this.force_recommend,
                        id: this.force_recommend.id
                    };
                    this.listLoading = true;
                    NProgress.start();
                    var method = "put";
                    if (this.force_recommend.id){
                        method = "post";
                    }
                    Resource.options({
                        method: method,
                        resource: "force_recommend.force_recommend",
                        data: data,
                        success: function(data) {
                          _this.listLoading = false;
                          NProgress.done();
                          _this.dialogFormVisible=false;
                          _this.getForceRecommendList();
                          _this.force_recommend.date='';
                          _this.force_recommend.id='';
                          _this.$notify({
                            title: '成功',
                            message: '保存成功',
                            type: 'success'
                          });
                        },
                        error: function() {
                          _this.listLoading = false;
                          NProgress.done();
                          _this.$notify({
                              title: '失败',
                              message: '保存失败，请联系相关研发～',
                              type: 'error'
                          });
                        }
                    });
                  } else {
                    this.$notify({
                          title: '保存失败',
                          message: '请按照红色提示修改区域内容',
                          type: 'error'
                      });
                    return false;
                  }
                });
            },

        },
        mounted() {
            this.getForceRecommendList();
        }
    }

</script>

<style lang="scss" scoped>
.el-table td>div {
    height:100px!important;
}
</style>



