<template>
<div>
  <div style="height : auto; width:40%; display:inline-block;float:left;">
      <Phone msg="story"></Phone>
  </div>
  <div  style="height : auto;width:60%;display:inline-block;float:right;">
     <el-form label-position="left"  ref="story" :model="story" label-width="100px"  style="margin:30px;width:80%;min-width:600px;" border :rules="rules">
        <el-form-item label="标题" prop="title">
            <el-input v-model="story.title"></el-input>
        </el-form-item>
        <el-form-item label="头图" prop="head_img">
            <el-upload
              :action="upload_url"
              list-type="picture-card"
              :show-file-list="false"
              :on-preview="handlePictureCardPreview"
              :on-success="handleSuccess"
              :data="upload_type"
              :on-remove="handleRemove">
              <img v-if="story.head_img" :src="story.head_img" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
            <el-dialog v-model="dialogVisible" size="tiny">
               <img width="100%" :src="dialogImageUrl" alt="">
            </el-dialog>
        </el-form-item>
        
        <el-form-item label="状态" prop="status">
            <el-select v-model="story.status" placeholder="请选择状态">
                <el-option
                  v-for="item in status"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
        </el-form-item>

        <el-form-item label="权重" prop="weight">
            <el-input-number v-model="story.weight"></el-input-number>
        </el-form-item>
        <el-form-item label="标签" prop="tags">
            <el-select
              v-model="story.tags"
              multiple
              filterable
              remote
              placeholder="请输入关键词"
              :remote-method="getTags"
              :loading="loading">
              <el-option
                v-for="item in tags"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="分类" prop="categories">
            <el-checkbox-group v-model="story.categories">
                <el-checkbox v-for="filter in categories" :label="filter.id" :key="filter.id">{{filter.name}}</el-checkbox>
            </el-checkbox-group>
        </el-form-item>
        <el-form-item label="专辑" prop="images" style="padding-top: 10px;">
            <PictureWall></PictureWall>
        </el-form-item>
       <!--  <el-form-item label="内容" prop="content" >
            <VueUEditor @ready="editorReady" :ueditorConfig="editorConfig"></VueUEditor>  
        </el-form-item> -->
        <el-form-item>
            <el-button type="primary" :disabled="submitVisible" @click="onSubmit('story')">发布</el-button>
        </el-form-item>
    </el-form>
    </div>  
</div>
</template>

<script>
import VueUEditor from 'vue-ueditor'
import GlobalConfig from '../../GlobalConfig'
import { mapState, mapActions,mapGetters } from 'vuex'
import Phone from "../phone"
import NProgress from 'nprogress'
import PictureWall from '../../components/album/PictureWall'
var Resource = require('../../util/Resource');
var album_url = GlobalConfig.base + "/rest/album/api/pictures/";
export default {
  name: 'edit_story',
  components : {
      VueUEditor,
      Phone,
      PictureWall
  },
  computed: {
      ...mapState ({
        sels: state => state.sels,
        type: state => state.type
      }),
      ...mapGetters({
        getSels: 'getSels'
      })
    },
  data () {
    return {
      upload_url:GlobalConfig.uploadImageUri,
      dialogImageUrl: '',
      dialogVisible: false,
      submitVisible: false,
      upload_type:{
          type: "new"
      },
      id:'',
      loading: false,
      first_loading: true,
      status:[],
      categories:[],
      tags:[],
      story: {
          title: '',
          status: '',
          categories: [],
          content: '',
          album_ids:[],
          head_img: '',
          weight:0,
          tags:[]
      },
      show_editor: false,
      rules: {
            categories: [
                {
                    type: 'array',
                    required: true,
                    message: '请至少选择一个分类',
                    trigger: 'blur'
                }
            ],
            title: [
              {
                  required: true,
                  message: '请输入标题',
                  trigger: 'blur'
              }
            ],
            status: [
              {
                  type: 'integer',
                  required: true,
                  message: '请选择状态',
                  trigger: 'change'
              }
            ],
        },
    }
  },
  watch: {
      getSels: function (albums) {
        var _this = this;
        _this.story.album_ids = [];
        var albumLegacyIds = [];
        albums.forEach(function(i){
            _this.story.album_ids.push(i.id);
            albumLegacyIds.push(i.legacy_id);
        })
        // this.editorConfig.serverUrl = album_url + "?ids="+ JSON.stringify(this.story.album_ids);
        // this.editorConfig.albumIds = JSON.stringify(albumLegacyIds)
        // if (this.story.album_ids.length != 0 && this.editorInstance) {
        //     this.show_editor = true;
        //     this.editorInstance.options.serverUrl = this.editorConfig.serverUrl;
        //     this.editorInstance.options.albumIds = this.editorConfig.albumIds;
        // }
      }
  },
  methods: {
    ...mapActions({
        setPhoneContent: 'setPhoneContent',
        setSels: 'setSels',
        setType: 'setType'
    }),
    ...mapGetters({
        getDesignCurrentType: 'getDesignCurrentType',
        getType: 'getDesignCurrentType',
        getModes: 'getModes'
    }),
   
    onSubmit(name) {
      this.$refs[name].validate((valid) => {
         if (valid) {
            var method = "put";
            if (this.id) {  
              method = "post";
            }
            var data = this.story;
            data.albums = []
            this.submitVisible=true;
            data.components = this.getModes();
            console.log(data.components)
            var _this = this;
            Resource.options({
              method: method,
              resource: "album.story",
              data: data,
              success: function(data) {
                  _this.listLoading = false;
                  
                  _this.$notify({
                      title: '成功',
                      message: '保存成功',
                      type: 'success'
                  });
                  if (data.id) {
                      window.location.href = '/#/apps/story';
                  } else {
                      _this.submitVisible = false;
                  }
              },
              error: function(data) {
                    _this.listLoading = false;
                    var message = '添加失败，请联系相关研发～';
                    if (data.data && data.data.errmsg) {
                        message = data.data.errmsg
                    }
                    this.submitVisible=false;
                    NProgress.done();
                    _this.$notify({
                        title: '失败',
                        message: message,
                        type: 'error'
                    });
                  }
          });
          } else {
              this.$notify({
                  title: '保存失败',
                  message: '请按照红色提示修改区域内容',
                  type: 'error'
              });
              this.submitVisible = false;
            return false;
          }

          })
    },
    handleRemove(file, fileList) {
        console.log(file, fileList);
      },
    handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
    },
    handleSuccess(response, file, fileList) {
        if (response.code == 200 && response.data) {
            this.story.head_img = response.data.url;
            var cacheParams = {
              updated: true,
              key: "img"
            }
            sessionStorage.setItem('story_updated', JSON.stringify(cacheParams));
            sessionStorage.setItem('story_img', response.data.url);

        } else {
            this.$notify({
                title: '失败',
                message: '图片上传失败',
                type: 'error'
            });
            this.story.head_img = "";
        }
    },
    ...mapActions({
          setModes: 'setModes'
    }),
    getStory(id) {
        var _this = this;
        Resource.options({
          method: "get",
          resource: "album.story",
          data: {id:id},
          success: function(data) {
              
              if (data.story){
                  _this.story = data.story;
                  _this.setSels( _this.story.albums);  
                  _this.setModes(data.story.components);
              }
              
              _this.categories = data.categories;
              _this.status = data.status;
              
          },
          error: function(data) {
                _this.listLoading = false;
                var message = '失败，请联系相关研发～';
                if (data.data && data.data.errmsg) {
                    message = data.data.errmsg
                }
                NProgress.done();
                _this.$notify({
                    title: '失败',
                    message: message,
                    type: 'error'
                });
              }
      });
    },
     getTags(value) {
        //search tags
        var _this = this;
        this.loading = true;
        Resource.get({
            resource: "tag.search_tag",
            data: {
              name: value
            },
            success: function(data) {
                _this.loading = false;
                _this.tags = data.items;
            },
            error: function() {
                _this.$notify({
                    title: '失败',
                    message: '请求失败',
                    type: 'error'
                });
            }
        })
    },
  },
  mounted() {
    this.id = this.$route.query.id||"";
    this.setType(1);
    this.getStory(this.id);
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #20a0ff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



