<template>
    <section style="left: 185px;">
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item label="标题">
                    <el-input v-model="filters.title"></el-input>
                </el-form-item>
                <el-form-item label="类型" v-show="isShowType()">
                    <el-select v-model="filters.custom_type" placeholder="请选择">
                      <el-option
                        v-for="item in type_options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getPictureList">查询</el-button>
                    <!--  <el-button type="primary" @click="handleAdd">新增图片</el-button> -->
                </el-form-item>
            </el-form>
        </el-col>
        <!--列表-->
        <el-col :span="24" class="toolbar">
        <div v-masonry origin-left="true" transition-duration="1s" item-selector=".item"  v-loading.body="listLoading">
          <div v-masonry-tile class="item" v-for="(item, index) in pictures">
            <div  v-on:click="imgClick(item)" style= "position:relative ">
            <div  class="blocks" style= "position:absolute; left:0; top:0; color:#fff; font-weight:bold "> {{ item.title }} </div>
            <div  class="blocks" style= "position:absolute; left:0px; top:185px; color:#fff; font-weight:bold; height:43px;width:128px;"> {{ item.type_name }} </div>
            <img :src="item.icon" style= "height:228px;width:128px;"> </img>
            <div  class="cover" style= "position:absolute; left:0; top:0;" v-show="isShow(item)"></div>
            </div>
          </div>
        </div>
        </el-col>
        <el-col :span="24" class="toolbar">
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>
    </section>
</template>

<script>
    import { mapMutations } from 'vuex'
    import { mapActions } from 'vuex'
    import { mapState } from 'vuex';
    import util from '../../common/js/util'
    import NProgress from 'nprogress'
    var Resource = require('../../util/Resource');

    export default {
        directives: {
        },
        computed: mapState ({
            sels: state => state.sels,
            type: state => state.type
        }),
        data() {
            return {
                filters: {
                    title: '',
                    custom_type: this.type
                },
                pictures: [],
                total: 0,
                page: 1,
                pageSize: 32,
                listLoading: false,
                //sels: [],//列表选中列
                isActive: false,
                type_options: [{
                      value: "-1",
                      label: '全部'
                    }, {
                      value: "0",
                      label: '图片'
                    }, {
                      value: "1",
                      label: '专辑'
                    }],
            }
        },
        methods: {
            isShow(item) {

                if (!this.sels.existId(item.id)){
                    return "";
                } else {
                    return "ok";
                }
            },
            isShowType() {
                return this.type === -1;
            },
            ...mapActions({
                imgClick: 'clickCommonPic'
            }),
            handleCurrentChange(val) {
                this.page = val;
                this.getPictureList();
            },
            //获取用户列表
            getPictureList() {
                let para = {
                    page: this.page,
                    page_size: this.pageSize,
                    title: this.filters.title,
                    status: "10",
                    type: this.filters.custom_type || this.type
                };
                this.listLoading = true;
                NProgress.start();
                var _this = this;
                Resource.get({
                    resource: "album.picture_list",
                    data: para,
                    async: false,
                    success: function(data) {
                        _this.total = data.total_count;
                        _this.pictures = data.items;
                        _this.pageSize =data.page_size;
                        _this.listLoading = false;
                        NProgress.done();
                    },
                    error: function() {
                        _this.listLoading = false;
                        NProgress.done();
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
        },
        mounted() {
            this.getPictureList();
        }
    }

</script>

<style scoped>
.el-table .cell, .el-table th>div {
    height: 201px!important;
}
.blocks{
  background-color: rgba(0, 0, 0, 0.2);
  color: #fff;
  padding: 15px;
  font-size: 12px;
  text-align: left;
  box-sizing: border-box;
  /*padding-right: 60px;*/
}
.divGrid{
  background-color: rgba(0, 0, 0, 0.7);
  padding: 10px;
  border-radius: 10px;
  box-sizing: border-box;
  width: 189px;
  height: 300px
}
.item{
  padding-left: 4px;
  padding-top: 1px;
}
.cover{
  background-color: rgba(0, 0, 0, 0.7);
  /*color: #fff;*/
  padding: 15px;
  font-size: 12px;
  text-align: left;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background-image:url('../../assets/cover.png');
  background-repeat:no-repeat;
  background-position:center;
}
</style>



