<template>
    <section style="left: 185px;">
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-tooltip  v-show="show_add" content="新增" placement="top">
                    <el-button type="primary" @click="handleAdd" class="el-icon-plus"></el-button>
                    </el-tooltip>
                </el-form-item>
            </el-form>
        </el-col>
        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="30" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
        <!--列表-->
        <el-table :data="mi_ecommerces" highlight-current-row v-loading="listLoading" border style="font-size:12px;" align="center">
            <el-table-column  align="center" prop="topic_id"  label="专辑ID"  > </el-table-column>
            <el-table-column  align="center" prop="index"  label="位置"  > </el-table-column>
            <el-table-column  align="center" prop="story_ids"  label="合辑IDS"  > </el-table-column>
            <el-table-column align="center" prop="created_at" label="创建时间" ></el-table-column>


            <el-table-column label="操作"  align="center">
                <template scope="scope">
                    <el-button type="primary" icon="edit"  @click="handleUpdate(scope.row.id)"></el-button>
                    <el-button type="primary" icon="delete"  @click="handleDelete(scope.row.id)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!-- 弹框 新增、编辑-->
        <el-dialog :visible.sync="dialogFormVisible" :before-close="handleCancle" >
            <el-form label-position="left"  ref="mi_ecommerce" :model="mi_ecommerce" label-width="100px" @submit.prevent="onSubmit" style="margin:30px;width:80%;min-width:600px;" :rules="form_rules"  border>

                <el-form-item label="专辑ID" prop="topic_id">
                    <el-input v-model="mi_ecommerce.topic_id"></el-input>
                </el-form-item>

                <el-form-item prop="story_ids" label="合集IDS">
                    <el-input v-model="mi_ecommerce.story_ids"></el-input>
                </el-form-item>

                <el-form-item prop="index" label="位置">
                   <el-input-number :min="0" v-model="mi_ecommerce.index"></el-input-number>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="onSubmit('mi_ecommerce')">保存</el-button>
                    <el-button v-on:click="handleCancle">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="30" :total="total" style="float:right;">
            </el-pagination>
        </el-col>
    </section>
</template>

<script>
    import util from '../../common/js/util'
    import NProgress from 'nprogress'
    var Resource = require('../../util/Resource');
    export default {
        data() {
            return {
                filters: {
                    name: ''
                },
                dialogVisible: false,
                mi_ecommerces: [],
                total: 0,
                pageSize: 30,
                page: 1,
                listLoading: false,
                sels: [],//列表选中列
                dialogFormVisible: false,
                show_add:false,
                rules: {
                    name: [
                      {
                          required: true,
                          message: '请输入搜索条件',
                          trigger: 'blur'
                      }
                    ],
                },
                form_rules: {
                    topic_id: [
                      {
                          required: true,
                          message: '请输入专辑ID',
                          trigger: 'blur'
                      }
                    ],
                    story_ids: [
                      {
                          required: true,
                          message: '请输入合辑IDS',
                          trigger: 'blur'
                      }
                    ],
                },
                mi_ecommerce: {
                    topic_id: "",
                    story_ids: "",
                    index : 0,
                },
                tag: {
                    name: ""
                },
                title:"",
                qr_text: "",
                previewUrl:""
            }
        },
        methods: {
            handleAdd: function() {
                this.dialogFormVisible = true
            },
            
            handleCurrentChange(val) {
                this.page = val;
            },
            handleUpdate(id) {
                var _this = this;
                
                Resource.get({
                    resource: "commerce.mi_ecommerce",
                    data: {
                        id: id
                    },
                    success: function(data) {
                        _this.mi_ecommerce = data.mi_ecommerce
                        _this.dialogFormVisible = true;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            handleDelete(id) {
                var _this = this;
                Resource.delete({
                    resource: "commerce.mi_ecommerce",
                    data: {
                        id: id
                    },
                    success: function(data) {
                        _this.getMiEcommerce();
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            handleCancle(){
                this.dialogFormVisible = false;
            },
            onSubmit(form_name) {
                 this.$refs['mi_ecommerce'].validate((valid) => {
                    if (valid) {
                        var _this = this;
                        Resource.put({
                            resource: "commerce.mi_ecommerce",
                            data: _this.mi_ecommerce,
                            success: function(data) {
                                _this.dialogFormVisible = false;
                                _this.getMiEcommerce();
                            },
                            error: function() {
                                _this.$notify({
                                    title: '失败',
                                    message: '请求失败',
                                    type: 'error'
                                });
                            }
                        })
                    } else {
                        this.$notify({
                          title: '保存失败',
                          message: '请按照红色提示修改区域内容',
                          type: 'error'
                        });
                        return false;
                    }

                 });
            },
            //获取用分类列表
            getMiEcommerce() {
                let para = {
                    page: this.page,
                    story_id: this.filters.name
                };
                this.listLoading = true;
                NProgress.start();
                var _this = this;
                Resource.get({
                    resource: "commerce.mi_ecommerces",
                    data: para,
                    success: function(data) {
                        _this.total = data.total_count;
                        _this.mi_ecommerces = data.items;
                        _this.pageSize = data.page_size;
                        _this.listLoading = false;
                        console.log(_this.total)
                        if (_this.total == 0) {
                            _this.show_add = true;
                        } else {
                            _this.show_add = false;
                        }
                        NProgress.done();
                    },
                    error: function() {
                        _this.listLoading = false;
                        NProgress.done();
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
        },
        mounted() {
            this.getMiEcommerce();
        }
    }

</script>

<style lang="scss" scoped>
.el-table td>div {
    height:100px!important;
}
</style>



