<template>
<div>
  <section style="left: 185px;">
    <!--工具条-->
    <el-form ref="cp_picture" label-width="80px" :model="cp_picture" label-position="left" style="margin:30px;width:80%;min-width:600px;":rules="rules" border>
        <el-form-item label="标题" prop="title" style="margin-bottom: 10px;">
          <el-input v-model="cp_picture.title"></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="desc" style="margin-bottom: 10px;" >
          <el-input type="textarea" v-model="cp_picture.desc"></el-input>
        </el-form-item>
        <el-form-item label="标题颜色" prop="title_color" style="margin-bottom: 10px;">
            <el-select v-model="cp_picture.title_color" placeholder="请选择标题颜色">
                <el-option
                  v-for="item in data.color_types"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="描述颜色" prop="desc_color" style="margin-bottom: 10px;">
            <el-select v-model="cp_picture.desc_color" placeholder="请选择描述颜色">
                <el-option
                  v-for="item in data.color_types"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
            </el-select>
        </el-form-item>
        <el-form-item label="标题字体大小" prop="txt_size" style="margin-bottom: 10px;">
            <el-select v-model="cp_picture.txt_size" placeholder="请选择标题字体大小">
                <el-option
                  v-for="item in data.font_size_types"
                  :label="item.value"
                  :value="item.key"
                  :key="item.key">
                </el-option>
            </el-select>
            </el-select>
        </el-form-item>
        <el-form-item label="数据等级" prop="level" style="margin-bottom: 10px;">
           <el-checkbox v-model="cp_picture.level">锁屏库</el-checkbox>
        </el-form-item>
        <!-- <el-form-item label="CP" prop="cp" style="margin-bottom: 10px;">
            <el-select
              v-model="cp_picture.cp"
              filterable
              remote
              placeholder="请选择CP"
              :remote-method="getCPs"
              >
              <el-option
                v-for="item in cps"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
        </el-form-item> -->

        <el-form-item label="Vendor" prop="vendor" style="margin-bottom: 10px;">
            <el-select v-model="cp_picture.vendor" placeholder="请选择Vendor">
                <el-option
                  v-for="item in data.vendors"
                  :label="item.name"
                  :value="item.id"
                  :key="item.id">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="标签" prop="tag" style="margin-bottom: 10px;">
          <el-select
              v-model="cp_picture.tag"
              multiple
              filterable
              remote
              placeholder="请输入关键词"
              :remote-method="getTags"
              >
              <el-option
                v-for="item in tags"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
            <el-tooltip content="新增" placement="top">
                <el-button type="primary" @click="handleAdd" class="el-icon-plus"></el-button>
            </el-tooltip>
        </el-form-item>
        <el-form-item label="h5链接" prop="deeplink" style="margin-bottom: 10px;" v-if="cp_picture.deeplink" >
          <el-input v-model="cp_picture.deeplink"></el-input>
        </el-form-item>

        <el-form-item label="图片" style="margin-bottom: 10px;">
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
        </el-form-item>
        <el-form-item style="margin-bottom: 10px;">
            <!-- <el-button @click="resetPicture('cp_picture')">取 消</el-button> -->
            <el-button type="primary" @click="addPicture">确 定</el-button>
        </el-form-item>
    </el-form>
  </section>

  <el-dialog v-model="dialogImagePreview" size="tiny">
    <img width="100%" :src="dialogImageUrl" alt="">
  </el-dialog>


    <!-- 弹框 新增、编辑banner图-->
    <el-dialog :visible.sync="dialogTagFormVisible"  :before-close="handleCancle" :title="dialogTagtitle" :modal="false" :modal-append-to-body="false">
        <el-form label-position="left"  ref="picture_tag" :model="picture_tag" label-width="100px" @submit.prevent="handleSubmit" style="margin:30px;width:80%;min-width:600px;" :rules="rules" border>
            <el-form-item label="标签名称" prop="tag_name">
                <el-input v-model="picture_tag.tag_name"></el-input>
            </el-form-item>
            <el-form-item label="描述" prop="tag_desc">
                <el-input v-model="picture_tag.tag_desc"></el-input>
            </el-form-item>
            <el-form-item label="权重" prop="tag_weight">
                <el-input-number v-model="picture_tag.tag_weight" :min="0"></el-input-number>
            </el-form-item>

            <el-form-item>
                <el-button type="primary"  v-on:click="handleSubmit">保存</el-button>
                <el-button type="primary" v-on:click="handleCancle">取 消</el-button>
            </el-form-item>
        </el-form>
    </el-dialog>


</div>
</template>
<script>
    import { mapState, mapActions } from 'vuex';
    var Resource = require('../../util/Resource');
    var moment = require('moment');

    export default {
        directives: {
        },
        computed: mapState ({
            sels: state => state.sels,
            picture_id: state => state.picture_id,
        }),

        data() {
            return {
                data: {
                },
                dialogImagePreview: false,
                dialogImageUrl: "",
                cp_picture:{
                    id:0,
                    title: "",
                    desc: "",
                    image:{},
                    url:"",
                    deeplink:"",
                    title_color: '',
                    desc_color: '',
                    // cp: '',
                    brand: '',
                    tag: [],
                    // date: [],
                    vendor: '',
                    txt_size: '',
                    level:true

                },

                tags: [],
                cps: [],
                font_size_types: [],

                dialogTagFormVisible: false,
                dialogTagtitle:'',
                picture_tag: {
                    tag_name: "",
                    tag_desc: "",
                    tag_weight: 0
                },

                imageUrl: '',
                // picture_upload_type: {
                //     type: "new"
                // },

                rules: {
                    title: [
                      {
                          required: true,
                          message: '请输入标题',
                          trigger: 'blur'
                      }
                    ],
                    desc: [
                      {
                          required: true,
                          message: '请输入描述',
                          trigger: 'blur'
                      }
                    ],
                    title_color: [
                      {
                          required: true,
                          message: '请选择标题颜色',
                          trigger: 'change'
                      }
                    ],
                    desc_color: [
                      {
                          required: true,
                          message: '请选择描述颜色',
                          trigger: 'change'
                      }
                    ],
                    categories: [
                      {
                          type: 'array',
                          required: true,
                          message: '请至少选择一个分类',
                          trigger: 'blur'
                      }
                    ],
                    // cp: [
                    //   {
                    //       type: 'integer',
                    //       required: true,
                    //       message: '请选择cp',
                    //       trigger: 'change'
                    //   }
                    // ],

                    vendor: [
                      {
                          type: 'integer',
                          required: true,
                          message: '请选择vendor',
                          trigger: 'blur'
                      }
                    ],
                    tag: [
                      {
                          type: 'array',
                          required: true,
                          message: '请至少选择一个标签',
                          trigger: 'blur'
                      }
                    ],
                    tag_name: [
                      {
                          required: true,
                          message: '请输入标签名称',
                      }
                    ]
                },
            }
        },
        methods: {
            ...mapActions({
                handlePictureCardDel: 'clickCommonPic',
                addSel: 'addSel'
            }),
            handleAdd(){
                this.dialogTagFormVisible = true;
                this.dialogTagtitle = "添加标签";
                this.picture_tag = {
                    tag_name: "",
                    tag_desc: "",
                    tag_weight: 0
                }
            },
            handleCancle(){
                this.picture_tag=[];
                this.dialogTagFormVisible = false;
            },
            // handleFormCancle(){
            //   this.addPicture();
            // },
            handleSubmit: function() {
             this.$refs['picture_tag'].validate((valid) => {
                  if (valid) {
                    var _this = this;
                    var data = {
                        name:this.picture_tag.tag_name,
                        desc:this.picture_tag.tag_desc,
                        weight:this.picture_tag.tag_weight
                    };
                    var method = "put";
                    Resource.options({
                        method: method,
                        resource: "tag.tag",
                        data: data,
                        success: function(data) {
                            _this.dialogTagFormVisible=false;
                            _this.picture_tag = {
                                tag_name : "",
                                tag_desc: "",
                                tag_weight: 0
                            }
                            _this.$notify({
                                title: '成功',
                                message: '添加标签成功',
                                type: 'success'
                            });
                        },
                        error: function(data) {
                              var message = '添加标签失败，请联系相关研发～';
                              if (data.data && data.data.errmsg) {
                                  message = data.data.errmsg
                              }
                              _this.$notify({
                                  title: '失败',
                                  message: message,
                                  type: 'error'
                              });
                            }
                    });
                  } else {
                    this.$notify({
                          title: '保存失败',
                          message: '请按照红色提示修改区域内容',
                          type: 'error'
                      });
                    return false;
                  }
                });
            },

            addPicture(){
                this.$refs['cp_picture'].validate((valid) => {
                    if (valid) {
                        var item = this.sels.getSel(this.picture_id)

                        // this.cp_picture.url = this.imageUrl;
                        item["picture"] = this.cp_picture
                        if (item.picture.desc.length > 5){
                            item.short_desc = item.picture.desc.substring(0,5)+"...";
                        }else{
                            item.short_desc = item.picture.desc
                        }
                        if(item.picture.title.length > 5){
                            item.short_title = item.picture.title.substring(0,5)+"...";
                        }else{
                            item.short_title = item.picture.title
                        }
                        var index = this.sels.indexOfId(this.picture_id)
                        this.sels[index] = item
                        this.$parent.$parent.dialogVisible=false;
                    }else {
                      this.$notify({
                        title: '添加失败',
                        message: '请按照红色提示修改区域内容',
                        type: 'error'
                    });
                    return false;
                  }
                });

            },
            resetPicture(ref){
                this.$refs[ref].resetFields();
                this.imageUrl = ""
            },

            getTags(value,id) {
                //search tags
                var _this = this;
                Resource.get({
                    resource: "tag.search_tag",
                    data: {
                      name: value,
                      id: id
                    },
                    async: true,
                    success: function(data) {
                        _this.tags = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            getTag(id) {
                //search tags
                var _this = this;
                Resource.get({
                    resource: "tag.find_tag",
                    data: {
                      id: id
                    },
                    success: function(data) {
                        _this.tags = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            getCPs(value) {
                //search brands
                var _this = this;
                Resource.get({
                    resource: "cp.search_cp",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.cps = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            // getCP(id) {
            //     //search brands
            //     var _this = this;
            //     Resource.get({
            //         resource: "cp.find_cp",
            //         data: {
            //           id: id
            //         },
            //         success: function(data) {
            //             _this.cps = data.items;
            //         },
            //         error: function() {
            //             _this.$notify({
            //                 title: '失败',
            //                 message: '请求失败',
            //                 type: 'error'
            //             });
            //         }
            //     })
            // },
        },
        mounted() {
            // var _this = this;
            var item = this.sels.getSel(this.picture_id)
            var start_time = moment().format("YYYY-MM-DD HH:mm:ss");
            var end_time = moment().add(180, 'days').format("YYYY-MM-DD HH:mm:ss");
            this.API_UTIL.API_PHONE.getCpPicture().then(data => {
                    this.data = data.data
                    this.imageUrl = item.image.url;
                });

            if(item.picture){
                // if(item.picture.cp){
                //     this.getCP(item.picture.cp)
                // }
                if(item.picture.tag){
                    this.getTag(item.picture.tag)
                }
                var tmp = item.picture
                this.cp_picture = item.picture

            }else{
                if(item.tag){
                    this.getTag(item.tag)
                }
                this.cp_picture = {
                    title: item.title,
                    desc: item.desc,
                    title_color: "#ffffffff",
                    desc_color: "#ffffffff",
                    txt_size: "26",
                    vendor: 1,
                    level: true,
                    tag: item.tag,
                    deeplink: item.deeplink,
                    brand: '',
                };
            }
        },
    }
</script>
<style>

 .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #20a0ff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px!important;
    text-align: center;
  }
  .avatar {
    width: 116px;
    height: 178px;
    display: block;
  }
</style>



