<template>
    <section style="left: 185px;">
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item label="专辑标题" >
                    <el-input v-model="filters.title" style="width: 200px;" ></el-input>
                </el-form-item>
                <el-form-item label="审核状态" >
                    <el-select v-model="filters.status" placeholder="请选择" style="width: 180px;">
                        <el-option
                          v-for="item in filter_result.status"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="来源" prop="origin_type" >
                     <el-select
                       v-model="filters.origin_type"
                       filterable
                       remote
                       placeholder="请选择来源"
                       :remote-method="getCPs"style="width: 180px;padding-left: 10px;">
                        <el-option
                            v-for="item in cps"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                        </el-option>
                     </el-select>
                </el-form-item>
                <el-form-item>
                    <el-tooltip content="查询" placement="top">
                    <el-button type="primary" v-on:click="getAlbumList" class="el-icon-search"></el-button>
                    </el-tooltip>
                    <el-tooltip content="重置" placement="top">
                    <el-button type="primary" v-on:click="handleReset" class="el-icon-circle-cross" ></el-button>
                    </el-tooltip>
                </el-form-item>
            </el-form>
        </el-col>

        <!--列表-->
        <el-table :data="albums" :highlight-current-row="true"element-loading-text="拼命加载中" border
        @selection-change="handleSelectionChange"
        @sort-change="handlerSortChange" v-loading="listLoading"  :default-expand-all="true">
            <el-table-column type="expand">
              <template scope="props">
               <el-row>
                <el-col :span="4" v-for="( picture, index) in props.row.pictures" :key="index" style="padding: 10px;">

                  <el-card :body-style="{ padding: '0px'}">
                    <div class="div-content">
                       <div class="close-btn">
                        <div class="im_css1">
                        <img class="im_delete" v-if="handleDisabledRefuse(picture.status,props.row.status)"  @click="handleSingleRefuse(picture.id,props.row.id)" src="../../assets/delete.png" style="margin-top:10px;">
                        <img class="im_recover" v-if="handleDisabledRefresh(picture.status,props.row.status)"   @click="handleSingleRefresh(picture.id,props.row.id)" src="../../assets/recover.png" style="margin-top:10px;">
                        </div>
                        <div class="im_css2">
                        <img class="im_pic"   @click="handlePictureCardPreview(picture.url)" src="../../assets/pic.png" style="margin-top:10px;">
                        </div>
                        </div>

                        <el-tooltip v-if="picture.status===3" :content="picture.refuse_reason" placement="top" style="position: absolute" >

                        <img class="image" v-if="picture.status===3" src="../../assets/wrong.png" style="position: absolute">
                        </el-tooltip>
                        <img :src="picture.url"  class="img" >
                    </div>
                    <div style="padding: 14px;">
                      <span>{{ picture.short_title }}</span>
                      <div class="bottom clearfix">
                          {{ picture.short_desc }}
                      </div>
                    </div>
                  </el-card>


                </el-col>
              </el-row>
              </template>
            </el-table-column>
            <el-table-column align="left" prop="title"  label="标题" width="200" >
                <template scope="scope">
                    <a :href="'#/apps/cp_edit_album?id='+scope.row.id" >
                       {{scope.row.title}}
                    </a>
                </template>
            </el-table-column>

            <el-table-column align="left" prop="desc"  width="600" label="描述">
            </el-table-column>

            <el-table-column prop="origin_type" label="来源" width="100">
            </el-table-column>

            <el-table-column align="center" prop="status" width="100" label="状态"  >
                <template scope="scope">
                    <el-button  style="background-color:#C0CCDA;" v-if="scope.row.status === 0">草稿
                    </el-button>
                    <el-button  type="warning" v-else-if="scope.row.status === 1">待审核
                    </el-button>
                    <el-button  type="success" v-else-if="scope.row.status === 2">审核通过
                    </el-button>
                   <!--  <el-button  type="warning" v-else-if="scope.row.status === 9">驳回
                    </el-button> -->
                    <el-tooltip v-else :content="scope.row.refuse_reason" placement="top" effect="light">
                    <el-button  type="danger">审核未通过
                    </el-button>
                    </el-tooltip>
                </template>


            </el-table-column>

            <el-table-column align="center" prop="created_at" label="入库时间"  width="300"
            ></el-table-column>

            <el-table-column label="操作" width="220" align="center" >
                <template scope="scope">

                    <!-- <el-button id='btn_succ' type="success" size="small" v-on:click="handlePass(scope.row)" v-if="handlePassDisabled(scope.row, scope.row.no_pass_picture_ids)">通过</el-button> -->
                    <!-- <el-button type="warning" size="small" v-on:click="handleReturn(scope.row.id)" v-if="scope.row.status in [0,1]">驳回</el-button> -->

                    <el-button type="info" size="small" v-on:click="handleUpdate(scope.row.id)" v-if="scope.row.status in [0,1]">编辑</el-button>

                    <el-button type="info" size="small" v-on:click="handleUpdate(scope.row.id)" v-if="scope.row.status === 2">查看</el-button>
                    <!-- <el-button type="warning" size="small"  v-on:click="handleReopen(scope.row)" v-if="handleAbled(scope.row, scope.row.no_pass_picture_ids)" >重置待审</el-button> -->
                    <el-button type="danger" size="small" v-on:click="handleRefuse(scope.row)" v-if="handleDisabled(scope.row, scope.row.no_pass_picture_ids)">拒绝</el-button>
                </template>
            </el-table-column>

        </el-table>
        <!-- 图片审核不通过时弹出，填写原因 -->
          <el-dialog :before-close="handleCancle" title="拒绝原因" v-model="dialogVisible" size="small" >

            <el-form ref="reason" label-width="80px" :model="reason" @submit.prevent="onSubmit"  label-position="left" style="margin:30px;width:80%;min-width:600px;" border >

              <el-form-item label="拒绝原因" prop="refuse_reason" >

                <el-checkbox-group v-model="reason.refuse_reason" v-on:change="handleChange(reason.refuse_reason, reason.id)">
                    <el-checkbox v-for="item in reasons"
                    :label="item.value" :key="item.key">
                    </el-checkbox>
                </el-checkbox-group>

              </el-form-item>
              <el-form-item label="其他原因" prop="other_reason" style="">
                <el-input type="textarea" v-model="reason.other_reason" placeholder="请输入原因"></el-input>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="onSubmit">确 定</el-button>
            </div>
          </el-dialog>

          <el-dialog v-model="dialogImagePreview" size="tiny">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>
    </section>
</template>

<script>
    import NProgress from 'nprogress'
    import { mapState, mapActions } from 'vuex'
    var Resource = require('../../util/Resource');
    export default {
        data() {
            return {
                filters: {
                    title: '',
                    status: '',
                    origin_type: '',
                    sort: '-create_time',
                },
                cps: [],
                creators: [],
                brands: [],
                filter_result:{},
                albums: [],
                total: 0,
                type: 1,
                page: 1,
                pageSize: 30,
                listLoading: true,
                sels: [],//列表选中列
                dialogVisible: false,
                reasons:{},
                //预览
                dialogImagePreview: false,
                dialogImageUrl: "",
                reason: {
                    id: "",
                    album_id: "",
                    refuse_reason: [],
                    other_reason: "",
                    type: "",
                },

            }
        },
        methods: {
            handlePictureCardPreview(url) {
              this.dialogImagePreview = true;
              this.dialogImageUrl = url;
            },
            getCPs(value) {
                var _this = this;
                Resource.get({
                    resource: "cp.search_cp",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.cps = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            handleCancle(){
                this.reason = {
                        id: "",
                        album_id: "",
                        refuse_reason: [],
                        other_reason: "",
                        type: "",
                    };
                this.dialogVisible = false;
            },

            handlerSortChange: function(obj) {
                if (obj.column) {
                    if (obj.order == "descending") {
                        this.filters.sort = "-" + obj.prop;
                    } else {
                        this.filters.sort = obj.prop;
                    }
                }
            },

            handleDisabled(row,arr) {
                if(row.status==3||row.status==2){
                    return false;
                }
                var album_id = row.id
                var pictures = row.pictures
                var ids = []
                if (pictures) {
                    pictures.forEach(function(i){
                        ids.push(i.id);
                    });
                }
                let picture_ids = ids.concat(arr).filter(v => !ids.includes(v) || !arr.includes(v))
                if(picture_ids.length==0){
                    return false;
                }
                return true;
            },
            handlePassDisabled(row,arr) {
                if(row.status==2){
                    return false;
                }
                var album_id = row.id
                var pictures = row.pictures
                var ids = []
                if (pictures) {
                    pictures.forEach(function(i){
                        ids.push(i.id);
                    });
                }
                let picture_ids = ids.concat(arr).filter(v => !ids.includes(v) || !arr.includes(v))
                if(picture_ids.length==0){
                    return false;
                }
                return true;
            },
            handleDisabledRefuse(status, album_status) {
                if(status==1 &&album_status==1){
                    return true;
                }
                return false;
            },
            handleDisabledRefresh(status, album_status) {
                if(status==3 &&album_status==1){
                    return true;
                }
                return false;
            },
            handleAbled(row,arr) {
                if(row.cp_user_third_name){
                    return false;
                }
                if(row.status==3){
                    return true;
                }
                var album_id = row.id
                var pictures = row.pictures
                var ids = []
                if (pictures) {
                    pictures.forEach(function(i){
                        ids.push(i.id);
                    });
                }
                let picture_ids = ids.concat(arr).filter(v => !ids.includes(v) || !arr.includes(v))
                if(picture_ids.length==0){
                    return true;
                }
                return false;
            },
            // 整组图片通过审核
            // handlePass(row) {
            //     var pictures = row.pictures
            //     var ids = []
            //     if (pictures) {
            //         pictures.forEach(function(i){
            //             if(i.status==1||i.status==2){
            //                 ids.push(i.id);
            //             }
            //         });
            //     }
            //     if(ids.length<3){
            //         this.$notify({
            //           title: '请求失败',
            //           message: '通过审核图片数量少于三张',
            //           type: 'error'
            //         });
            //         return
            //     }
            //     var localUser = JSON.parse(sessionStorage.getItem("user"));
            //     var username = localUser.username;
            //     var params = {
            //         username: username,
            //         album_id: row.id,
            //         refuse_reason: [],
            //         other_reason: '',
            //         type: "pass",
            //         status: 2
            //     }
            //     this.updateAlbumStatus(params);
            // },
            // 整组图片拒绝审核
            handleRefuse(row){
                this.reason.album_id = row.id;
                this.reason.type = "refuse";
                this.reason.status = 3;
                this.dialogVisible = true;
            },
            // 整组图片重置审核
            handleReopen(row) {
                var params = {
                    album_id: row.id,
                    refuse_reason: [],
                    other_reason: '',
                    type: "reopen",
                    status: 0
                }
                this.updateAlbumStatus(params);
            },
            // handleReturn(id) {
            //     var params = {
            //         album_id: id,
            //         type: "return",
            //         status: 9
            //     }
            //     this.updateAlbumStatus(params);
            // },
            handleCurrentChange(val) {
                this.page = val;
                this.getAlbumList();
            },
            handleSelectionChange(vals) {
                this.sels = [];
                var _this = this;
                vals.forEach(function(v){
                    _this.sels.push(v.id);
                })
            },
            handleUpdate(id) {
                window.location.href = '/#/apps/cp_edit_album?id='+id;
            },
            handleSingleRefuse(picture_id, album_id){
                this.reason.id = picture_id;
                this.reason.album_id = album_id;
                this.dialogVisible = true;
                this.reason.status = 3;
                this.reason.type = 'single_refuse';
            },
            handleSingleRefresh(picture_id, album_id){
                var params = {
                    id:picture_id,
                    album_id: album_id,
                    refuse_reason: '',
                    other_reason: '',
                    type: 'refresh',
                    //重新待审核
                    status: 1
                }
                this.updateStatus(params);
            },
            handleChange(value,picture_id) {
                this.reason.refuse_reason = value;
            },
            onSubmit(){
                if (this.reason.refuse_reason.length== 0 && !this.reason.other_reason) {
                    this.$message.error('  请填写原因!');
                    return
                }
                var params = {
                    //单图拒绝时reason.id有值，整组拒绝时为空
                    id:this.reason.id || '',
                    album_id: this.reason.album_id,
                    refuse_reason: this.reason.refuse_reason,
                    other_reason: this.reason.other_reason,
                    type: this.reason.type,
                    //审核未通过
                    status: this.reason.status
                }
                this.listLoading = true;

                if(this.reason.type=="refuse"){
                    this.updateAlbumStatus(params);
                }
                if(this.reason.type=="single_refuse"){
                    this.updateStatus(params);
                }
            },
            //获取列表
            getAlbumList() {
                var params = {
                    page: this.page || 1,
                    title: this.filters.title || '',
                    status: this.filters.status,
                    origin_type: this.filters.origin_type,
                }
                this.listLoading = true;
                this.API_UTIL.API_PHONE.getAlbumList(params).then(data=>{
                    if (data.code == 200) {
                        this.albums = data.data.items;
                        this.reasons = data.data.reasons
                        this.listLoading = false;
                    }else {
                        this.$notify({
                          title: '失败',
                          message: '请求失败',
                          type: 'error'
                        });
                    }
                });
            },
            // 更新单张图片的状态
            updateStatus(params){
                this.API_UTIL.API_PHONE.updateStatus(params).then(data=>{
                        if (data.code == 200) {
                            this.$notify({
                              title: '操作成功',
                              message: '操作成功',
                              type: 'success'
                            });
                            this.reason = {
                                id: "",
                                album_id: "",
                                refuse_reason: [],
                                other_reason: "",
                                type: "",
                            };
                            this.dialogVisible = false;
                            this.getAlbumList();
                            this.listLoading = false;

                        }else{
                            this.$notify({
                              title: '操作失败',
                              message: '操作失败',
                              type: 'error'
                            });
                        }
                    }
                );
            },
            // 更新整组图片的状态
            updateAlbumStatus(params){
                this.API_UTIL.API_PHONE.updateAlbumStatus(params).then(data=>{
                        if (data.code == 200) {
                            this.$notify({
                              title: '操作成功',
                              message: '操作成功',
                              type: 'success'
                            });
                            this.reason = {
                                id: "",
                                album_id: "",
                                refuse_reason: [],
                                other_reason: "",
                                type: "",
                            };
                            this.dialogVisible = false;
                            this.getAlbumList();
                            this.listLoading = false;
                        }else{
                            if(data.data.msg){
                                this.$notify({
                                  title: '操作失败',
                                  message: data.data.msg,
                                  type: 'error'
                                });
                            }else{
                                this.$notify({
                                  title: '操作失败',
                                  message: '操作失败',
                                  type: 'error'
                                });
                            }
                        }
                    }
                );
            },
            // 页面加载前获取集合值
            getSearch() {
                var _this = this;
                this.API_UTIL.API_PHONE.getFilterResult().then(data=>{
                        if (data.code == 200) {
                            _this.filter_result = data.data.filter_result;
                        }else{
                            this.$notify({
                              title: '失败',
                              message: '加载审核状态失败',
                              type: 'error'
                            });
                        }
                    }
                );
            },
            handleReset() {
                this.filters={
                    title: '',
                    status: '',
                    origin_type: ''
                },
                this.getAlbumList();
            },
        },
        mounted() {
            this.getSearch();
            this.getAlbumList();
        }
    }

</script>

<style>
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.avatar-uploader .el-upload:hover {
    der-color: #20a0ff;
}

.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px!important;
    text-align: center;
}

.avatar {
    width: 116px;
    height: 178px;
    display: block;
}

.image {
  width: 224px;
  height: 312px;
  margin-top: 42px;
  display: block;
  float:bottom;
}
.img {
    width: 225px;
    height: 250px;
    display: block;
}
/*.image {
    width: 228px;
    height: 250px;
    display: block;
}*/
.close-btn {
    top: 10px;
    right: 10px;
    display: block;
}
/*.div-content{
    width: 228px;
    height: 300px;
    display: block;
}*/
.div-content:hover .close-btn{
    display: block;
}
.im_delete {
    margin-left: 95px;
    width: 30px;
    height: 30px;
}
.im_recover {
    margin-left: 95px;
    width: 30px;
    height: 30px;
}
.im_pic {
    margin-left: 10px;
    width: 30px;
    height: 30px;
}
.im_css1 {
    width:60%;
    float:right;
    display: inline;
}
.im_css2 {
    width:30%;
    float:left;
    display: inline;
}
</style>



