import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
import Login from '@/views/Login.vue'
import PictureList from '@/views/album/PictureList'
import AlbumList from '@/views/album/AlbumList'
import CategoryList from '@/views/category/CategoryList'
import CPList from '@/views/cp/CPList'
import VendorList from '@/views/vendor/VendorList'
import DailyRecommendList from '@/views/daily_recommend/DailyRecommendList'
import EditDailyRecommend from '@/views/daily_recommend/EditDailyRecommend'
import ForceRecommendList from '@/views/force_recommend/ForceRecommendList'
import EditForceRecommend from '@/views/force_recommend/EditForceRecommend'
import ForceOfflineList from '@/views/force_offline/ForceOfflineList'
import EditForceOffline from '@/views/force_offline/EditForceOffline'
import BrandList from '@/views/brand/BrandList'
import BannerList from '@/views/banner/BannerList'
import EditPicture from '@/views/album/EditPicture'
import LatestTopic from '@/views/latest_topic/LatestTopic'
import ShoppingCart from '@/views/shopping_cart/ShoppingCart'
import EditAlbum from '@/views/album/EditAlbum'
import Index from '@/views/index'
import AppIndex from '@/views/AppIndex'
import Tag from '@/views/tag/TagList'
import CPUserList from '@/views/cp/CPUserList'
import AlbumCheck from '@/views/cp_album/AlbumCheck'
import CPEditAlbum from '@/views/cp_album/EditAlbum'
import Hello from '@/views/Hello.vue'
import Phone from '@/views/phone.vue'
import H5 from '@/views/h5.vue'
import StoryList from '@/views/story/StoryList.vue'
import EditStory from '@/views/story/EditStory.vue'
import OldEditStory from '@/views/story/OldEditStory.vue'
import Test from '@/views/Test.vue'
import MiECommerce from '@/views/mi_ecommerce/MiECommerce.vue'

import NotebookCategoryList from '@/views/notebook/category/NotebookCategoryList.vue'
import NotebookAlbumList from '@/views/notebook/album/AlbumList.vue'
import NotebookEditAlbum from '@/views/notebook/album/EditAlbum.vue'
import NotebookDailyRecommendList from '@/views/notebook/daily_recommend/DailyRecommendList.vue'
import NotebookEditDailyRecommend from '@/views/notebook/daily_recommend/EditDailyRecommend.vue'

Vue.use(Router)

const Router1 = new Router({
  routes:
  [
     {
      path: '/test',
      name: 'test',
      component: Test,
      hidden: true
    },
    {
      path: '/',
      name: 'Login',
      component: Login,
      hidden: true
    },
    {
      path: '/phone',
      name: 'phone',
      component: Phone,
      hidden: true
    },
    {
      path: '/story',
      name: 'story',
      component: H5,
      hidden: true
    },
    {
      path: '/app_index',
      name: 'AppIndex',
      component: AppIndex,
      hidden: true
    },
    {
      path: '/apps',
      name: '基础数据',
      iconCls: 'fa fa-database',
      component: Home,
      children:
        [
          {
            hidden: true,
            path: '',
            redirect: to => {
                return 'picture'
            }
          },
          { path: 'picture', component: PictureList, name: '图片' },
          { path: 'album', component: AlbumList, name: '专辑' },
          { path: 'vendor', component: VendorList, name: 'Vendor'},
          // { path: 'index', component: Index, name: '首页' },
          { path: 'add_picture', component: EditPicture, name: '新增图片', hidden: true },
          { path: 'edit_picture', component: EditPicture, name: '编辑图片', hidden: true },
          { path: 'add_album', component: EditAlbum, name: '新增专辑', hidden: true },
          { path: 'edit_album', component: EditAlbum, name: '编辑专辑', hidden: true },
          { path: 'tag', component: Tag, name: '标签' },
          { path: 'category', component: CategoryList, name: '分类' },
          { path: 'banner', component: BannerList, name: 'Banner图' },
          { path: 'cp', component: CPList, name: 'CP' },
          { path: 'brand', component: BrandList, name: '媒体' },
          { path: 'cp_user', component: CPUserList, name: 'CP用户' },

          { path: 'old_edit_story', component: OldEditStory, name: '旧编辑合集', hidden: false },
          //  {
          //   path: '/test',
          //   name: '合集',
          //   component: Hello,
          //   hidden: false
          // },
        ],
    },
    {
      path: '/apps',
      name: '运营',
      iconCls: 'fa fa-database',
      component: Home,
      children:
        [
          { path: 'daily_recommend', component: DailyRecommendList, name: '每日推荐' },
          { path: 'force_recommend', component: ForceRecommendList, name: '强制推荐' },
          { path: 'force_offline', component: ForceOfflineList, name: '强制下架' },
          { path: 'edit_daily_recommend', component: EditDailyRecommend, name: '编辑每日推荐', hidden: true },
          { path: 'add_daily_recommend', component: EditDailyRecommend, name: '新增每日推荐', hidden: true },
          { path: 'edit_force_recommend', component: EditForceRecommend, name: '编辑强制推荐', hidden: true },
          { path: 'edit_force_offline', component: EditForceOffline, name: '编辑强制下架', hidden: true },
          { path: 'latest_topic', component: LatestTopic, name: '最新话题' },
          { path: 'story', component: StoryList, name: '合集' },
          { path: 'edit_story', component: EditStory, name: '编辑合集', hidden: true },
        ],
    },
    {
      path: '/apps',
      name: '商务广告',
      iconCls: 'fa fa-database',
      component: Home,
      children:
        [

          { path: 'mi_ecommerce', component: MiECommerce, name: '米电商' },

        ],
    },
    {
      path: '/apps',
      name: 'CP管理',
      iconCls: 'fa fa-database',
      component: Home,
      children:
        [

          { path: 'cp_check', component: AlbumCheck, name: 'CP审核' },
          { path: 'cp_edit_album', component: CPEditAlbum, name: 'CP编辑' , hidden: true },
          { path: 'cp_user', component: CPUserList, name: 'CP用户' },
        ],
    },
    {
      path: '/apps',
      name: '系统配置',
      iconCls: 'fa fa-database',
      component: Home,
      children:
        [
          { path: 'shopping_cart', component: ShoppingCart, name: '购物车配置' },
        ],
    },
    {
      path: '/mitv',
      name: '电视数据',
      iconCls: 'fa fa-database',
      component: Home,
      children:
        [
          {
            hidden: true,
            path: '',
            redirect: to => {
                return 'album'
            }
          },
         { path: 'album', component: AlbumList, name: '专辑' },
        ],
    },
    {
      path: '/minotebook',
      name: '笔记本数据',
      iconCls: 'fa fa-database',
      component: Home,
      children:
        [
          {
            hidden: true,
            path: '',
            redirect: to => {
                return 'album'
            }
          },
         { path: 'album', component: NotebookAlbumList, name: '专辑' },
         { path: 'add_album', component: NotebookEditAlbum, name: '新增专辑', hidden: true },
          { path: 'edit_album', component: NotebookEditAlbum, name: '编辑专辑', hidden: true },
         { path: 'category', component: NotebookCategoryList, name: '分类' },
         { path: 'daily_recommend', component: NotebookDailyRecommendList, name: '每日推荐' },
         { path: 'add_daily_recommend', component: NotebookEditDailyRecommend, name: '新增每日推荐', hidden: true },
         { path: 'edit_daily_recommend', component: NotebookEditDailyRecommend, name: '编辑每日推荐', hidden: true },
        ],
    },
  ]
});
export default Router1;






