<template>
<div>
  <ul class="el-upload-list el-upload-list--picture-card list-group" v-sortable="{ onUpdate: onUpdate }">

    <li class="el-upload-list__item is-success" v-for="item in sels" style="height:300px;width:190px" :key="item.id">

      <img class="image" v-if="item.status===3" src="../../assets/wrong.png" style="position: absolute">
      <img :src="item.url" alt="" style="height:70%" class="el-upload-list__item-thumbnail">

      <label class="el-upload-list__item-status-label">
        <i class="el-icon-upload-success el-icon-check"></i>
      </label>

      <!---->
      <span class="el-upload-list__item-actions">
        <span class="el-upload-list__item-preview">
          <i class="el-icon-view" @click="handlePictureCardPreview(item.url)"></i>
        </span>
        <!-- <span class="el-upload-list__item-delete">
          <i class="el-icon-delete2" @click="handlePictureCardDel(item)" v-if="item.status in [0,1]"></i>
        </span> -->
        <span class="el-upload-list__item-update">
          <i class="el-icon-edit" v-if="item.status in [0,1]" @click="handlePictureUpdate(item)"></i>
        </span>
      </span>

      <div style="padding: 14px;">
         <span><strong>{{ item.short_title }}</strong></span>
         <div class="bottom clearfix">
            {{ item.short_desc }}
        </div>
      </div>
    </li>
  </ul>

  <!-- 图片列表弹窗 -->
  <el-dialog title="编辑图片" v-model="dialogVisible" size="small" :before-close="handleClose" :key="this.picture_id" :modal="true" :modal-append-to-body="false">
    <EditPicture></EditPicture>
  </el-dialog>

   <el-dialog v-model="dialogImagePreview" size="tiny">
    <img width="100%" :src="dialogImageUrl" alt="">
  </el-dialog>

  </div>
</template>
<script>
    import { mapState, mapActions } from 'vuex';
    import EditPicture from '../../views/cp_album/EditPicture'

    export default {
        components : {EditPicture},
        computed: mapState ({
            sels: state => state.sels,
            picture_id: state => state.picture_id,
        }),
        data() {
            return {
                dialogImagePreview: false,
                dialogVisible: false,
                dialogImageUrl: "",
                imageUrl: '',
            }
        },
        methods: {
            ...mapActions({
                handlePictureCardDel: 'clickCommonPic',
                setPictureID:'setPictureID'
            }),
            handleClose(done) {
                this.$confirm('确认关闭？')
                  .then(_ => {
                    done();
                  })
                  .catch(_ => {});
            },
            handlePictureCardPreview(url) {
              this.dialogImagePreview = true;
              this.dialogImageUrl = url;
            },
            onUpdate: function(event) {
                this.sels.splice(event.newIndex, 0, this.sels.splice(event.oldIndex, 1)[0]);
            },
            handlePictureUpdate(item) {
                this.setPictureID(item.id)
                this.dialogVisible = true;
            },
        },
    }
</script>
<style>

 .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #20a0ff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px!important;
    text-align: center;
  }
  .avatar {
    width: 116px;
    height: 178px;
    display: block;
  }
  .image {
  width: 190px;
  height: 300px;
  display: block;
}
</style>



