<template>
    <el-row>
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
               <el-form-item label="标签" prop="tag">
                <el-select
                    v-model="filters.tag"
                    filterable
                    remote
                    placeholder="请输入关键词"
                    :remote-method="getTags"
                     style="width: 180px;padding-left: 14px;">
                    <el-option
                      v-for="item in tags"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="图片标题" >
                    <el-input v-model="filters.title" style="width: 180px;"></el-input>
                </el-form-item>
                <el-form-item label="图片描述" >
                    <el-input v-model="filters.desc" style="width: 180px;"></el-input>
                </el-form-item>
                <el-form-item label="图片ID">
                    <el-input v-model="filters.id" style="width: 180px;padding-left: 14px;"></el-input>
                </el-form-item>
                <el-form-item label="所属专辑ID">
                    <el-input v-model="filters.album_id" style="width: 180px;"></el-input>
                </el-form-item>
                <el-form-item label="分类" style="padding-left: 25px;">
                    <el-select v-model="filters.category" placeholder="请选择分类" style="width: 180px;">
                        <el-option
                          v-for="item in filter_result.categories"
                          :label="item.name"
                          :value="item.id"
                          :key="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="是否过期" style="padding-left: 10px;">
                    <el-select v-model="filters.is_out" placeholder="请选择" style="width: 100px;">
                        <el-option
                          v-for="item in filter_result.is_out"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>
                </br>
                <el-form-item label="创建人" prop="creator" >
                     <el-select
                       v-model="filters.creator"
                       filterable
                       remote
                       placeholder="请选择创建人"
                       :remote-method="getCreators"style="width: 180px;">
                        <el-option
                            v-for="item in creators"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                        </el-option>
                     </el-select>
                </el-form-item>
                <el-form-item label="所属CP" prop="cp" >
                     <el-select
                       v-model="filters.cp"
                       filterable
                       remote
                       placeholder="请选择CP"
                       :remote-method="getCPs"style="width: 180px;padding-left: 10px;">
                        <el-option
                            v-for="item in cps"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                        </el-option>
                     </el-select>
                </el-form-item>
                <el-form-item label="所属品牌" prop="brand">
                   <el-select
                       v-model="filters.brand"
                       filterable
                       remote
                       placeholder="请选择品牌"
                       :remote-method="getBrands"
                       style="width: 180px;">
                       <el-option
                         v-for="item in brands"
                         :key="item.id"
                         :label="item.name"
                         :value="item.id">
                       </el-option>
                     </el-select>
                 </el-form-item>
               <!--  <el-form-item label="发布"  style="padding-left: 27px;">
                    <el-select v-model="filters.isready" placeholder="请选择" style="width: 180px;">
                        <el-option
                          v-for="item in filter_result.isready"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item> -->
                <el-form-item label="状态" style="padding-left: 40px;">
                    <el-select v-model="filters.status" placeholder="请选择状态" style="width: 180px;">
                        <el-option
                          v-for="item in filter_result.status"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="图片类型" style="padding-left: 0px;">
                    <el-select v-model="filters.acc_type" placeholder="请选择状态" style="width: 180px;">
                        <el-option
                          v-for="item in filter_result.acc_type"
                          :label="item.value"
                          :value="item.key"
                          :key="item.key">
                        </el-option>
                    </el-select>
                </el-form-item>

                <!-- <el-form-item label="发布时间" style="padding-left: 14px;">
                    <div class="block">
                        <el-date-picker v-model="filters.date" type="datetimerange" format="yyyy-MM-dd HH:mm:ss" placeholder="选择时间范围">
                        </el-date-picker>
                    </div>
                </el-form-item> -->
                <el-form-item>
                    <el-tooltip content="查询" placement="top">
                    <el-button type="primary" v-on:click="getPictureList" class="el-icon-search" ></el-button>
                    </el-tooltip>
                    <el-tooltip content="重置" placement="top">
                    <el-button type="primary" v-on:click="handleReset" class="el-icon-circle-cross" ></el-button>
                    </el-tooltip>
                    <el-tooltip content="新增" placement="top" >
                     <el-button type="primary" v-on:click="toAddPicture" class="el-icon-plus"></el-button>
                     </el-tooltip>
                </el-form-item>
            </el-form>
        </el-col>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <font class="toolbar">更多操作:</font>
            <el-cascader
            :options="actions"
            @change="handleChange"
            placeholder="请选择" >
            </el-cascader>
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>

        <!--列表-->
        <el-table :data="pictures" ref="table" highlight-current-row v-loading="listLoading"
        element-loading-text="拼命加载中"
        @selection-change="handleSelectionChange"
        @sort-change="handlerSortChange"
        border style="font-size:12px;" class="picture_list" >
            <el-table-column type="selection" width="50">
            </el-table-column>
            <el-table-column label="图片" width="113"  align="left">
              <template scope="scope">
                <a :href="scope.row.icon" target="_blank">
                    <img :src="scope.row.icon" style="height: 200px">
                </a>
              </template>
            </el-table-column>
            <el-table-column prop="title"  label="标题"  >
                <template scope="scope">
                    <a :href="'#/apps/edit_picture?id='+scope.row.id" target="_blank">
                       {{scope.row.title}}
                    </a>
                </template>
            </el-table-column>

            <el-table-column prop="desc"  label="描述" width="300">
            </el-table-column>

            <el-table-column align="center" prop="legacy_id" sortable="custom" label="ID" >
                <!-- <template scope="scope">
                    <a :href="'#/apps/edit_picture?id='+scope.row.id" target="_blank">
                       {{scope.row.legacy_id}}
                    </a>
                </template> -->
            </el-table-column>


            <el-table-column prop="isready" label="发布" width="30" :formatter="formatBoolean" >
            </el-table-column>

             <el-table-column prop="status" label="状态" width="30" >
             </el-table-column>
             <el-table-column prop="level" label="数据等级" width="50">
             </el-table-column>

            <el-table-column  label="所属专辑" :formatter="formatAlbums">
            </el-table-column>

            <el-table-column prop="cp" label="CP">
            </el-table-column>

            <el-table-column prop="brand" label="品牌" >
            </el-table-column>
            <el-table-column prop="creator_name" label="创建者">
            </el-table-column>

            <el-table-column prop="operator_name" label="最近操作者">
            </el-table-column>

            <el-table-column prop="create_time" sortable="custom" label="入库时间" width="120">
            </el-table-column>

            <el-table-column prop="update_time"  sortable="custom" label="更新时间" width="120">
            </el-table-column>

            <el-table-column prop="last_readytime"  sortable="custom" label="最后发布时间" width="120">
            </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="page"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>

    </el-row>
</template>

<script>
    // import { downloadPic } from '../../api/api';
    import util from '../../common/js/util'
    import NProgress from 'nprogress'
    var Resource = require('../../util/Resource');

    export default {
        data() {
            return {
                filters: {
                    tag: '',
                    title: '',
                    status: '',
                    date:'',
                    id: '',
                    album_id:'',
                    creator:'',
                    isready:'',
                    cp:'',
                    brand:'',
                    sort: '-create_time',
                    category: "",
                    acc_type: "",
                    is_out: "",
                    desc:""
                },
                tags: [],
                cps: [],
                creators: [],
                brands: [],
                filter_result:{},
                pictures: [],
                total: 0,
                type: 0,
                page: 1,
                pageSize: 30,
                listLoading: true,
                sels: [],//列表选中列
                actions:
                    [{
                      value: 'level',
                      label: '加入锁屏库',
                    },{
                      value: 'tooffline',
                      label: '停用图片',
                    },{
                      value: 'toavailable',
                      label: '启用图片',
                    },{
                      value: 'toforceoffline',
                      label: '加入强制下架',
                    },{
                      value: 'todailyrecommend',
                      label: '加入每日推荐',
                    },{
                      value: 'mergetopic',
                      label: '合并成专辑',
                    },{
                      value: 'download',
                      label: '下载预览图',
                    }
                    // ,{
                    //   value: 'torecommend',
                    //   label: '加入强制推荐',
                    // }
                    ],
            }
        },
        methods: {
            getCreators(value) {
                var _this = this;
                Resource.get({
                    resource: "album.creators",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.creators = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            getBrands(value) {
                var _this = this;
                Resource.get({
                    resource: "brand.search_brand",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.brands = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            getCPs(value) {
                var _this = this;
                Resource.get({
                    resource: "cp.search_cp",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.cps = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            getTags(value) {
                var _this = this;
                Resource.get({
                    resource: "tag.search_tag",
                    data: {
                      name: value
                    },
                    success: function(data) {
                        _this.tags = data.items;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            toAddPicture: function() {
                window.location.href = '/#/apps/add_picture';
            },
            handleChange: function(value) {
                if (this.sels.length < 1) {
                    this.$notify({
                        title: '失败',
                        message: '请选择一条或一条以上图片',
                        type: 'error'
                    });
                    return;
                }
                switch(value[0]) {
                    case "tooffline":
                        this.changeStatus("off");
                        break;
                    case "toavailable":
                        this.changeStatus("on");
                        break;
                    case "toforceoffline":
                        this.toForceOffline();
                        break;
                    case "todailyrecommend":
                        this.toDailyRecommend();
                        break;
                    case "torecommend":
                        this.toRecommend();
                        break;
                    case "mergetopic":
                        this.mergeTopic();
                        break;
                    case "download":
                        this.downloadPic();
                        break;
                    case "level":
                        this.toSetLevel();
                        break;
                }
            },
            changeStatus: function(status) {
                var _this = this;
                Resource.post({
                    resource: "album.picture_status",
                    data: {
                        ids: this.sels,
                        status: status
                    },
                    success: function(data) {
                        _this.getPictureList();
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            mergeTopic: function() {
                if (this.sels.length < 2) {
                    this.$notify({
                        title: '失败',
                        message: '请选择两条或两条以上图片',
                        type: 'error'
                    });
                    return;
                }

                var _this = this;
                Resource.put({
                    resource: "album.picture_merge",
                    data: {
                        ids: this.sels
                    },
                    success: function(data) {
                        window.location.href = '/#/apps/edit_album?id='+data.id;
                    },
                    error: function(data) {
                        var message = '请求失败';
                        if (data.data && data.data.errmsg) {
                            message = data.data.errmsg
                        }
                        NProgress.done();
                        _this.$notify({
                            title: '失败',
                            message: message,
                            type: 'error'
                        });
                        _this.submitVisible = false;
                    }
                });
            },
            downloadPic: function() {
                var params = {
                    ids: this.sels.join(",")
                };
                this.API_UTIL.API_PHONE.downloadPic(params);
            },
            toForceOffline: function() {
                var _this = this;
                Resource.options({
                      method: "put",
                      resource: "force_offline.force_offline",
                      data: {
                          album_ids: this.sels.join(","),
                      },
                      success: function(data) {
                          _this.$notify({
                              title: '发布成功',
                              message: '发布成功',
                              type: 'success'
                          });
                          //window.location.href = "/#/apps/edit_force_offline?id="+ data.id;
                      },
                      error: function(data) {
                          var message = '保存失败，请联系相关研发～';
                          var id = 0;
                          if (data.data && data.data.errmsg) {
                              message = data.data.errmsg;
                              id = data.data.id;
                          }
                          _this.$notify({
                              title: '失败',
                              message: message,
                              type: 'error'
                          });
                          if (id > 0) {
                              window.location.href = "/#/apps/edit_force_offline?id="+ id;
                          }
                      }
                  });
            },
            toRecommend: function() {
                var _this = this;
                Resource.options({
                      method: "put",
                      resource: "force_recommend.force_recommend",
                      data: {
                          album_ids: this.sels.join(","),
                      },
                      success: function(data) {
                          _this.$notify({
                              title: '发布成功',
                              message: '发布成功',
                              type: 'success'
                          });
                          //window.location.href = "/#/apps/edit_force_recommend?id="+ data.id;
                      },
                      error: function(data) {
                          var message = '操作失败，请联系相关研发～';
                          var id = 0;
                          if (data.data && data.data.errmsg) {
                              message = data.data.errmsg;
                              id = data.data.id;
                          }
                          _this.$notify({
                              title: '失败',
                              message: message,
                              type: 'error'
                          });
                          if (id > 0) {
                              window.location.href = "/#/apps/edit_force_recommend?id="+ id;
                          }
                      }
                  });
            },
            toSetLevel: function() {
                var _this = this;
                Resource.post({
                    resource: "album.picture_level",
                    data: {
                        ids: this.sels
                    },
                    success: function(data) {
                        _this.getPictureList();
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            toDailyRecommend: function() {
              var _this = this;
                Resource.options({
                      method: "put",
                      resource: "daily_recommend.daily_recommend",
                      data: {
                          album_ids: this.sels,
                      },
                      success: function(data) {
                          _this.$notify({
                              title: '保存成功',
                              message: '保存成功',
                              type: 'success'
                          });
                          window.location.href = "/#/apps/edit_daily_recommend?id="+ data.id;
                      },
                      error: function(data) {
                          var message = '操作失败，请联系相关研发～';
                          var id = 0;
                          if (data.data && data.data.errmsg) {
                              message = data.data.errmsg;
                              id = data.data.id;
                          }
                          _this.$notify({
                              title: '失败',
                              message: message,
                              type: 'error'
                          });
                          if (id > 0) {
                              window.location.href = "/#/apps/edit_daily_recommend?id="+ id;
                          }
                      }
                  });
            },
            handlerSortChange: function(obj) {
                if (obj.column) {
                    if (obj.order == "descending") {
                        this.filters.sort = "-" + obj.prop;
                    } else {
                        this.filters.sort = obj.prop;
                    }
                }
                this.getPictureList();

            },
            formatBoolean: function(row, column) {
                return row.isready == true ? '是' : row.isready == false ? '否' : '未知';
            },
            formatAlbums: function(row, column) {
                return row.albums.join(",");
            },
            handleCurrentChange(val) {
                this.page = val;
                this.getPictureList();
            },
            //点击某一行的单选框时触发的事件
            handleSelectionChange(vals) {
                this.sels = [];
                var _this = this;
                vals.forEach(function(v){
                    _this.sels.push(v.id);
                })
            },
            // 页面加载前获取集合值
            getSearch() {
                var _this = this;
                Resource.get({
                    resource: "album.picture_search",
                    success: function(data) {
                        _this.filter_result = data.filter_result;
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                });
            },
            getFilters() {
                if(this.filters.date){
                    var date_start = util.formatDate.format(this.filters.date[0],'y-M-d h:m:s');
                    var date_end = util.formatDate.format(this.filters.date[1],'y-M-d h:m:s');
                }else{
                    var date_start = '';
                    var date_end = '';

                }
                var page = this.page || 1;
                var title = encodeURIComponent(this.filters.title || '');
                var date_start = date_start || '';
                var date_end = date_end || '';
                var status = this.filters.status;
                var id = this.filters.id || '';
                var album_id = this.filters.album_id || '';
                var isready = this.filters.isready;
                var cp_id = this.filters.cp || '';
                var brand_id = this.filters.brand || '';
                var tag = this.filters.tag || '';
                var creator = this.filters.creator || '';
                var category = this.filters.category || '';
                var acc_type = this.filters.acc_type || '';
                var is_out = this.filters.is_out || '';
                var sort = this.filters.sort || '-create_time';
                var desc = this.filters.desc || '';
                return {
                    page: page,
                    title: title,
                    date_start: date_start,
                    date_end: date_end,
                    status: status,
                    id: id,
                    album_id: album_id,
                    isready: isready,
                    cp_id: cp_id,
                    brand_id: brand_id,
                    type: this.type,
                    tag: tag,
                    creator: creator,
                    category: category,
                    acc_type: acc_type,
                    is_out: is_out,
                    sort: sort,
                    desc:desc
                }
            },
            handleReset() {
                this.filters={
                    tag: '',
                    creator: '',
                    title: '',
                    status: '',
                    date:'',
                    id: '',
                    album_id:'',
                    isready:'',
                    cp:'',
                    brand:'',
                    sort: '-create_time',
                    acc_type: '',
                    is_out: '',
                    desc: ''
                },
                this.getPictureList();
            },
            //获取用户列表
            getPictureList() {
                this.pictures = [];
                this.listLoading = true;
                let para = this.getFilters();
                var _this = this;
                NProgress.start();
                Resource.get({
                    resource: "album.picture_list",
                    data: para,
                    success: function(data) {
                        _this.total = data.total_count;
                        _this.pictures = data.items;
                        _this.listLoading = false;
                        NProgress.done();
                    },
                    error: function() {
                        _this.listLoading = false;
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            removeUser: function(para){
                Resource.delete({
                    resource: "album.picture",
                    data: para,
                    success: function(data) {
                        return true;
                    },
                    error: function() {
                        return false;
                    }
                })
            },
        },
        mounted() {
            this.getSearch();
            this.getPictureList();

        }
    }

</script>

<style scoped>
.el-table .cell, .el-table th>div {
    height: 201px!important;
}
</style>



