<template>
    <el-row>
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <!-- <el-form-item label="ID">
                    <el-input v-model="filters.global_id" style="width: 100px;" type="number"></el-input>
                </el-form-item> -->

                <el-form-item label="名称">
                    <el-input v-model="filters.name" style="width: 100px;"></el-input>
                </el-form-item>

                <!-- <el-form-item label="入库时间">
                    <div class="block">
                        <el-date-picker v-model="filters.date" type="datetimerange" format="yyyy-MM-dd HH:mm:ss" placeholder="选择时间范围">
                        </el-date-picker>
                    </div>
                </el-form-item> -->

                <el-form-item>
                    <el-tooltip content="查询" placement="top">
                    <el-button class="el-icon-search" type="primary" v-on:click="getBannerList"></el-button>
                    </el-tooltip>
                    <el-tooltip content="新增" placement="top">
                     <el-button class="el-icon-plus" type="primary" v-on:click="handleAdd"></el-button>
                     </el-tooltip>
                </el-form-item>
            </el-form>
        </el-col>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>

        <!--列表-->
        <el-table :data="banners" highlight-current-row v-loading="listLoading" border style="font-size:12px; ">

            <el-table-column label="ID" prop="global_id" min-width="10" align="center">
            </el-table-column>

            <el-table-column prop="name" min-width="10"  label="名称" align="center" >
            </el-table-column>

             <el-table-column label="Banner Img" min-width="40" align="left">
                <template scope="scope">
                    <a :href="scope.row.icon" target="_blank">
                        <img :src="scope.row.icon" style="height: 50px">
                    </a>
                </template>
            </el-table-column>
            <el-table-column prop="create_time" min-width="20" align="center"  label="入库时间">
            </el-table-column>
            <el-table-column label="操作" width="80">
                <template scope="scope">
                    <el-button class="el-icon-edit" type="primary" size="small" v-on:click="handleUpdate(scope.row.id)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page="1"
                  :page-size="pageSize"
                  layout="prev, pager, next, jumper"
                  :total="total"
                   style="float:right;">
            </el-pagination>
        </el-col>
        <!-- 弹框 新增、编辑banner图-->
        <el-dialog :visible.sync="dialogFormVisible" :title="title" :before-close="handleCancle" >
            <el-form label-position="left"  ref="banner" :model="banner" label-width="100px" @submit.prevent="onSubmit" style="margin:80px;width:80%;min-width:600px;" :rules="rules" border>

                <el-form-item label="Banner图名称" prop="name" label-width="120px" >
                    <el-input v-model="banner.name" ></el-input>
                </el-form-item>

                <el-form-item label="图片" label-width="110px" style="padding-left: 13px;">
                    <el-upload
                      class="upload-demo"
                      :action="upload_url"
                      :on-preview="handlePreview"
                      :on-remove="handleRemove"
                      :on-success="handleSuccess"
                      :file-list="banner.banners"
                      :data="icon_data"
                      list-type="picture">
                      <el-button size="small" type="primary">点击上传</el-button>
                    </el-upload>
                </el-form-item>

                <!-- 图片预览弹窗 -->
                <el-dialog v-model="imageDialogVisible" size="tiny" :modal="false" :modal-append-to-body="false">
                    <img width="100%" :src="dialogImageUrl" alt="">
                </el-dialog>

                <el-form-item style="padding-left: 25px;">
                    <el-button type="primary" v-on:click="onSubmit('banner')">保存</el-button>
                    <el-button type="primary" v-on:click="handleCancle">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </el-row>
</template>

<script>
    import util from '../../common/js/util';
    import NProgress from 'nprogress';
    import GlobalConfig from '../../GlobalConfig';
    var Resource = require('../../util/Resource');

    export default {
        data() {
            return {
                filters: {
                    global_id: '',
                    name: '',
                    date:''
                },
                banners: [],
                total: 0,
                pageSize: 30,
                page: 1,
                title:"",
                listLoading: false,
                imageDialogVisible: false,
                dialogImageUrl: "",
                upload_url:GlobalConfig.uploadImageUri,
                //dialog
                id:"",
                dialogFormVisible: false,
                icon_data: {
                    type: "new"
                },
                banner: {
                    id:"",
                    name: '',
                    banners: []
                },
                rules: {
                    name: [
                      {
                          required: true,
                          message: '请输入名称',
                          trigger: 'blur'
                      }
                    ],

                }
            }
        },
        // components: { EditBanner },
        methods: {
            handleCurrentChange(val) {
                this.page = val;
                this.getBannerList();
            },
            handleAdd(){
                this.dialogFormVisible = true;
                this.title = "添加Banner图";
            },
            handleCancle(){
                this.banner.name='';
                this.banner.banners=[];
                this.banner.id='';
                this.dialogFormVisible = false;
            },
            handleUpdate(id){
                var data={
                    id:id
                }
                var _this = this;
                Resource.get({
                    resource: "banner.banner",
                    data:data,
                    success: function(data) {
                        _this.formatBanner(data.banner);
                        _this.dialogFormVisible = true;
                        _this.title = "修改Banner图";
                    },
                    error: function() {
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                });

            },
            getFilters() {
                if(this.filters.date){
                    var date_start = util.formatDate.format(this.filters.date[0],'y-M-d h:m:s');
                    var date_end = util.formatDate.format(this.filters.date[1],'y-M-d h:m:s');
                }else{
                    var date_start = '';
                    var date_end = '';

                };
                var date_start = date_start || '';
                var date_end = date_end || '';
                var page = this.page || 1;
                var name = this.filters.name || '';
                var global_id = this.filters.global_id || '';
                return {
                    page: page,
                    name: name,
                    global_id: global_id,
                    date_start: date_start,
                    date_end: date_end,
                }
            },
            //获取用banner图列表
            getBannerList() {
                let para = this.getFilters();
                this.listLoading = true;
                NProgress.start();
                var _this = this;
                Resource.get({
                    resource: "banner.banner_list",
                    data: para,
                    success: function(data) {
                        _this.total = data.total_count;
                        _this.banners = data.items;
                        _this.pageSize = data.page_size;
                        _this.listLoading = false;
                        NProgress.done();
                    },
                    error: function() {
                        _this.listLoading = false;
                        NProgress.done();
                        _this.$notify({
                            title: '失败',
                            message: '请求失败',
                            type: 'error'
                        });
                    }
                })
            },
            handleRemove(file, fileList) {
                this.banner.banners = [];
            },
            handlePreview(file) {
                this.dialogImageUrl = file.url;
                this.imageDialogVisible = true;
            },
            handleSuccess(response, file, fileList) {
                if (response.code == 200 && response.data) {
                   this.banner.banners = [response.data];
                } else {
                    this.$notify({
                        title: '失败',
                        message: '图片上传失败',
                        type: 'error'
                    });
                }
            },
            formatBanner(banner) {
                var banners = []
                if (banner) {
                    var banner_link = banner.banner_link
                    var arr = banner_link.split("/");
                    banners.push({
                        name: arr[arr.length-1],
                        url: banner_link
                    })
                }

                this.banner = {
                      name: banner.name,
                      banners: banners,
                      id: banner.id
                };
            },
            onSubmit(formName) {
                this.$refs[formName].validate((valid) => {
                  if (valid) {
                    var _this = this;
                    var data = {
                        banner: this.banner,
                    };
                    this.listLoading = true;
                    NProgress.start();
                    var method = "put";
                    if (this.banner.id && this.banner.id != "" ) {
                        method = "post"
                    }
                    Resource.options({
                        method: method,
                        resource: "banner.banner",
                        data: data,
                        success: function(data) {
                          _this.listLoading = false;
                          NProgress.done();
                          _this.dialogFormVisible=false;
                          _this.getBannerList();
                          _this.banner.name='';
                          _this.banner.banners=[];
                          _this.banner.id='';
                          _this.$notify({
                            title: '成功',
                            message: '保存成功',
                            type: 'success'
                          });
                        },
                        error: function(data) {
                          _this.listLoading = false;
                          var message = '保存失败，请联系相关研发～';
                          if (data.data && data.data.errmsg) {
                              message = data.data.errmsg
                          }
                          NProgress.done();
                          _this.$notify({
                              title: '失败',
                              message: message,
                              type: 'error'
                          });
                        }
                    });

                  } else {
                    this.$notify({
                          title: '保存失败',
                          message: '请按照红色提示修改区域内容',
                          type: 'error'
                      });
                    return false;
                  }
                });
            },
        },
        mounted() {
            this.getBannerList();
        }
    }

</script>

<style lang="scss" scoped>
.el-table td>div {
    height:100px!important;
}
</style>



